// StickyBlock (style overrides for EPUB mode)
// -------------------------------------------
// When viewing in Bookshelf (EPUB reader), "position: sticky" doesn't work in the context that StickyBlock is in.
// A workaround using inview events and "position: fixed" needs to be used instead.
//
// Note: these elements only need styles when stuck (fixed). 


// Inview helper
// ------------------------------------------------------------------------------------
// This element simplifies the script needed for the EPUB-specific jQuery 'onscreen' event. 
.stickyblock__epub-inview-helper {
    position: absolute;
    top: 100vh;
    right: 0;
    width: 5px;
    bottom: calc(100vh ~"+" (@block-padding-ver/2));

    pointer-events: none;

    // Become visible in debug mode.
    .mode-debug & {
        pointer-events: all;
        background-color: @debug-color;
    }
}

.stickyblock.is-epub-stickyblock {
    
    // Improve rendering inside of Desktop Bookshelf.
    .stickyblock__epub-block {
        transform: translateZ(0px);
        
        // Add a smooth transition between stuck/unstuck.
        .transition(transform @duration ease);
    }
    .stickyblock__epub-block-inner {
        width: 100%;
    }
    .stickyblock__col.is-sticky {
        position: relative;
    }

    // Set the max-height of the sticky col's items.
    .stickyblock__epub-component-container {
        height: calc(100vh ~"-" @block-padding-ver);
        margin-top: -@block-padding-ver/2;
    }

    .stickyblock__col.is-sticky {

        .stickyblock__item {
            height: 100%;
            padding-top: 0;
            padding-bottom: 0;
        }
    }
    
    // When the stickyblock isn't stuck
    // --------------------------------
    &.is-epub-free .stickyblock__col.is-sticky {
        // Override the .col-* class applied to this element (which is used to set its size when it's stuck).
        max-width: none !important;
        height: 100%;
        .stickyblock__item {
            
            // Have the sticky components overlap each other. When the user scrolls,
            // JavaScript will cross-fade them.
            &:not(:first-child) {
                margin-top: calc(-100vh ~"+" @block-padding-ver);
            }
        }
    }

    &.is-epub-free.is-top {

    }
    // Align the no-longer position: fixed element to the bottom if the user just scrolled the stickyBlock in that direction.
    &.is-epub-free.is-bottom .stickyblock__epub-block {
        display: flex;
        align-items: flex-end;
    }
}

.stickyblock.is-epub-stickyblock.is-epub-stuck {

    // The following elements will mimick the same layout styles as their non 'stickyblock__epub-' prefixed counterparts.
    .stickyblock__epub-block {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;

        // Override the .col-* class applied to this element (which is used to set its size when it isn't stuck).
        max-width: none !important;

        .stickyblock__epub-block-inner {
            //&:extend(.block__inner all); // LESSCSS 'extend all' is a very costly function. Use sparingly.
            .l-container-padding(@block-padding-ver, @block-padding-hoz);
            max-width: @stickyblock-maxwidth;
            margin: auto;
            height: 100%;
        }
    }

    .stickyblock__col.is-sticky {
        position: relative;
        height: 100%;
        display: block;
        margin-left: auto;

        .stickyblock__item {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            padding-top: 0;
            padding-bottom: 0;

            margin-top: 0;
        }
    }

    // Swap layout if right alignment is used.
    &.layout-right .stickyblock__col.is-sticky {
        margin-left: 0;
        margin-right: auto;
    }

    // Allow clicking through the positioning elements.
    .stickyblock__epub-block {
        pointer-events: none;
    }
    .stickyblock__item {
        pointer-events: all;
    }
}