// DD Img
// --------------------------------

@rimg-line-height: 1.2em;

// FIXME: These shouldn't be global?
img.--no-max-width {
    max-width: none;
    width: auto;
}
img.--width-auto {
    width: auto;
    margin: auto;
    min-width: 0;
}


.ddimg {
    // The container's size should be determined by the image it contains / its parent adapt-dd-figure item element.
    position: relative;
    display: block;

    // Center the &__inner element (text-align needs to be used as it is an inline-block).
    text-align: center;

    &__inner {
        position: relative;
        //display: inline-block;  // Always be as wide as the inner image.
    }
    // Inline block used to have the <div> fully wrap the image, even if it scrolls.
    // Will only ever be applied when containing a non-SVG image (as PNGs and JPGs have preset widths when auto width).
    /*&.--width-fix*/
    &.is-auto-width {
        line-height: 0; // Done to remove the whitespace caused by the inline-block.
    }
    &.is-auto-width &__inner {
        display: inline-block;
        line-height: 1.5;
    }

    // If this .ddimg is inside of another .ddimg, have its positioning be absolute (treat it like an overlay).
    & .ddimg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    // If the sizing property is set, allow for any image (including a non-svg) to have its width explicitly set.  
    &__img.col {
        width: 100%;
    } 

    // Legacy: if inside a parent .figure__item, and it has .col sizing; inherit that sizing.
    .figure__item.col:not(.col-auto) > & &__img {
        width: 100%;
    }

    // Lightbox
    // ----------------------------------------------
    &.has-lightbox &__inner {
        cursor: zoom-in;
    }

    // Show a magnifying glass to indicate that this img has a lightbox.
    &__lightbox-icon {
        position: absolute;
        top: @item-margin;
        right: @item-margin;

        &::before {
            font-size: 3rem;
        }
    }

    // Fade in the lightbox icon on mouseover.
    .no-touch &:hover &__lightbox-icon {
        filter: drop-shadow(1px 1px 0px lighten(@black, 20%));
        color: lighten(@black, 10%);
    }
}


/**
 * All measurements inside of .rimg MUST use em. This will make the content scale with the width of the SVG
 */
.rimg {

    overflow: visible; // WARNING: If the text exceeds the size of the container, it will be clipped.
    position: relative;

    &__inner {
        position: relative;
    }

    // Textboxes
    // ----------------------------
    &__item {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;

        padding: 8em; // Half of item padding.

        // Hide items until the image has loaded.
        visibility: hidden;

        // Left alignment
        &.align-left {
            justify-content: flex-start;
        }
    }

    &__item-inner {
        text-align: center;

        // Sometimes, there are no <tspan> elements wrapping the inner text.
        // Hence, the line-height is set here and on tspan.
        line-height: @rimg-line-height;
    }

    // Show items once the image has loaded.
    &.is-ready &__item{
        visibility: inherit; // Use inherit; incase the parent is still invisible.
    }

    // FIXME: There's no easy way to get the line-height of a textbox from Illustrator's SVG at this time!
    // All line-heights set to a default 1.2em until further notice.
    tspan {
        line-height: @rimg-line-height;
        display: inline;

        // Sometimes the SVG will have trailing spaces in its <tspan>s.
        // They NEED to be preserved (as otherwise, some words won't have gaps between them).
        white-space: pre-wrap; 

        &[x="0"] {
            display: inline-block;
        }
    }
}