// DD Figure Popup (lightbox)
// --------------------------------

// Modify the notify elements to give as much room for the lightbox figure as possible.
// FIXME: Copied from ddImgPopup.less. Should be made global somehow.
.notify__popup.figure-lightbox {

    // Allow scrolling in both directions.
    overflow-x: auto;
    overflow-y: auto;
    width: 100vw;
    height: 100vh;

    .notify__popup-inner {
        max-width: none;
        padding-left: 0;
        padding-right: 0;
    }
    // Allow the user to click through everything except the <img>.
    .notify__content {pointer-events: none;}
    
    .notify__content-inner {
        text-align: center;
    }
}

.figure-popup {
  
    &__content {
        pointer-events: all;

        position: relative;
        display: inline-block; // Fully wrap the figure.
        
        padding-left: @notify-padding-hoz;
        padding-right: @notify-padding-hoz;

        &.has-bg::before {
            content: "";
            position: absolute;

            top: -@notify-padding-hoz;
            bottom: -@notify-padding-hoz;
            
            left: 0;
            right: 0;
            background: @white;
        }
    }

    // Allow for custom lightbox widths.
    // --------------------------------------------------
    .size-loop(@i) when (@i <= 24) {

        .figure-popup__size-@{i} .figure-popup__content-inner {
            width: (@i/12*@device-width-medium);
        }
        .size-loop(@i + 1);
    }
    .size-loop(1);
}