// PDF mixins.
// ---------------------------------

/**
 * page-break-after fix:
 * (Hacky solution) Add a slight buffer under the element avoid breaking it.
 * This will have a similar effect to "page-break-after: avoid;" (a property which doesn't work in Chrome at the moment).
 */
.avoid-break-after(@bufferSize: 6rem) {
  page-break-inside: avoid;
		
  &::after {
    content: "";
    display: block;
    height: @bufferSize;
    margin-bottom: -@bufferSize;
  }
}