.hotgraphicflip {
  
  &__item {
    .drop-shadow-btn(); // DO NOT APPLY THIS TO &__item-inner! WILL BREAK BACKFACE!
  }

  // Front
  // -------------------------------------
  &__item-front {
    background-color: @item-btn-color;
  }
  
  .no-touch &__item:not(.is-active):hover &__item-front {
    background-color: @item-color-hover;
    .transition(background-color @duration ease-in, color @duration ease-in;);
  }
  
  &__item.is-visited &__item-front {
    background-color: @visited;
  }
  
  // Increase padding around image if using col sizing of 6 (temp.).
  .col-sm-6 &__item-front {
    padding: @hotgraphicflip-padding * 2;
  }
  
  // Back button
  // -------------------------------------
  &__item-btn {
    color: @font-color;

    .no-touch &:hover {
      color: lighten(@font-color, 15%);
    }
  }

  // Ensure non-svg images are also centered.
  &__item-front .ddimg__inner {
    display: flex;
    align-items: center;
    justify-content: center;

    img.--width-auto {
      // Ensure that auto sized ddImgs still center themselves in the items vertically too.
      object-fit: none;
      height: 100%;
    }
  }
}