// DD Helper - Error comment
// ----------------------------------

@dderror-min-width: 20rem;

.dderror {
	display: none;
}

// The element should only be visible in debug mode.
.mode-debug .dderror {

	font-size: @body-size;
	pointer-events: none;
    
    display: block;
    p &,
    span & {
        display: inline;
    }

    &__icon {
		pointer-events: all;
		display: inline-block;
		
		background-color: #ffddff;
		border: 1px solid #ff00ff;
		color: red;

		cursor: pointer;
	}

	&__comment {
		pointer-events: all;
		position: absolute;
		
		right: 0;
		width: calc(~"(" 100vw ~"-" @device-width-large ~")" / 2);
		min-width: @dderror-min-width;
		transform: translate(100%, -50%);

		padding: 0 @item-padding/2;

		z-index: 50;
		.transition(right @duration ease, transform @duration ease;);
	}

	&__comment-inner {
		display: block;
		padding: @item-padding*0.7 @item-padding;
		.drop-shadow-double();
		background-color: #ffddff;
		border: 1px solid #ff00ff;
		color: red;

		.transition(all @duration ease);
	}

	// Hover
	// ------------------------------
	&:hover {

		.dderror__icon {
			background-color: mix(#ffddff, red, 50%);
		}
		
		.dderror__comment {
			z-index: 51;
		}

		.dderror__comment-inner {
			background-color: #fff8ff;
			color: rgb(44, 19, 19);
			border-color: mix(#ffddff, red, 50%);
		}
	}

	@d: (@device-width-large + @dderror-min-width*2);
	@media (max-width: @d) {

		&:hover {

			.dderror__comment {
				right: calc(~"(" 100vw ~"-" @device-width-large ~")" / -2);
				transform: translate(0%, -50%);
			}
		}
	}
}

.mode-debug .stickyblock .dderror__comment {
	right: auto;
	transform: none;
}