// FIXME: Temporary implementation of Didasko logo in the top-right corner of the page
// ----------------------------------------------------------------------------

.page::after {
    content: "";
    position: fixed;

    top: 0.75rem;
    right: 2.25rem;
    pointer-events: none;

    background-image: url("assets/didasko-logo-bw.png");
    width: 25rem;
    height: 12rem;

    background-position: top right;
    background-repeat: no-repeat;

    opacity: 0.35;

    @media (max-width: (@device-width-large + 25rem + (@block-padding-hoz*2))) {
        background-image: url("assets/didasko-icon-bw.png");
    }

    @media (max-width: @device-width-medium) {
        background-image: url("assets/didasko-logo-small.png");
        opacity: 1;
        right: 1rem;
    }

    z-index: 98;
}