// Transitions
// --------------------------------------------------
// Transitions are used by various scripts (mainly stickyElement at the moment) to create dynamic content movement.

// --------------------------------------------------
// Variables
// --------------------------------------------------

@t-duration: @duration*2;

// --------------------------------------------------

.t-fade-in {
  opacity: 0;
  .transition(opacity @t-duration ease);

  &.t-active {
    opacity: 1;
  }
}