@btn-circle-size:           @btn-padding * 2.5;
@btn-circle-size-small:     1rem;
@btn-circle-border-width:   1px;
@btn-circle-padding:        0.1875em; // 3px.

// Use only with btn-circle() mixins. 
.btn-circle-hover(@hoverColorEnabled: true;) {

    // Don't hover if locked.
    &:not(.is-disabled):not(.is-locked) {
        & when (@hoverColorEnabled = true) {
            color: @btn-color-hover;
        }
        &::after {
            transform: scale(1.2);
        }
    }
}

.btn-circle(@hoverColorEnabled: true; @small: false;) {

    position: relative;
    margin: auto;

    // Reduce size if specified.
    & when (@small = false) {
        width: @btn-circle-size;
        height: @btn-circle-size;
    }
    & when (@small = true) {
        width: @btn-circle-size-small;
        height: @btn-circle-size-small;
    }

    padding: @btn-circle-padding;
    background-clip: content-box;

    color: @btn-color; // Override this to change colour easily.
    background-color: currentColor;
    border-radius: @btn-circle-size;

    .transition(color (@duration/2) ease;);

    // Border
    // ---------------------------------------------------------------
    &::after {
        content: "";
        position: absolute;

        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        border-radius: inherit;
        border: @btn-circle-border-width solid currentColor;

        .transition(transform (@duration/2) ease;);
    }

    .no-touch &:not(.is-disabled):not(.is-locked) {
        cursor: pointer;
    }

    .no-touch &:not(.is-disabled):not(.is-locked):hover {
        .btn-circle-hover(@hoverColorEnabled);
    }

    // Disabled
    // ----------------------------------------------------------------
    &.is-disabled {
        color: @disabled;
    }
}
