// .col-*-* classes
// ---------------------------
// Based on Bootstrap implementation.
// Use for customizing element widths.
// FIXME: (Is currently being used by HotgraphicFlip) may be removed in future if deemed too niche/contrasting with Adapt theme.

.col() {
  position: relative;
  width: 100%;
  min-height: 1px;
  display: block;
}

.col-def(@mid, @min-device-size) {

  @_mid: e(@mid);

  // 1 - 12 loop.
  .col-loop(@i, @min-device-size) when (@i <= 12) {

    .col-@{_mid}-@{i} {
      .col();
      @media (min-width: @min-device-size) {
        flex: 0 0 (@i/12*100%);
        max-width: (@i/12*100%);
      }
    }

    .col-loop((@i + 1), @min-device-size);
  }
  .col-loop(1, @min-device-size);

  // .col-**-auto
  .col-@{_mid}-auto {
    .col();
    @media (min-width: @min-device-size) {
      flex: 0 0 auto;
      width: auto;
      max-width: none;
    }
  }
}

.col-def("sm", @device-width-small);
.col-def("md", @device-width-medium);
.col-def("lg", @device-width-large);