// FlipCard
// ------------------------------

.flipcard {
  
  // Have a slightly reduced margin for the button.
  // (The same margin as body text).
  &__widget {
    margin-top: -(@component-body-margin - @body-text-margin);
  }

  &__content {
    .drop-shadow-half();
  }

  &__card-inner {
    transition-duration: @duration*4;
  }

  // Button
  // -------------------------------------
  &__btn-container {
      margin-bottom: @componentrow-margin;
  }
}