// FreeThinking
// ------------------------------

.freethinking {

    // Don't have the content take up the full desktop width (stylistic choice).
    &__content {
        max-width: @device-width-medium;
        margin: auto;
    }

    // Temp? Increase padding inside of the page.
    &__content-inner {

        @media (min-width: @device-width-medium) {
            padding: 0 @item-padding;
        }
    }
    
    // Content header
    // ------------------------------------------------------------------------
    &__content-header {
        //margin-bottom: @component-body-margin;
        margin-bottom: @btn-margin;
    }
    
    &__content-instruction {
        margin-top: @component-instruction-margin;
        .instruction-text();
    }
    
    // Content activity
    // ------------------------------------------------------------------------
    &__content-textarea {
        width: 100%;
    }

    // Content correct
    // ------------------------------------------------------------------------

    // Start off hidden
    &__content-correct {
        display: none;
    }

    &__content-correct-instruction {
        margin-top: @component-instruction-margin;
        margin-bottom: @component-instruction-margin;
        .instruction-text();
    }

    &__content-correct-body {
        background: fade(@validation-success, 15%);
        padding: @item-padding;

        border: @border-width @validation-success solid;
    }

    // Buttons
    // ------------------------------------------------------------------------
    .btn__container {
        display: flex;
        justify-content: center;
    }

    &__btn {
        cursor: pointer;
    }
}