// --------------------------------------------------
// Smooths the font on the pixel level rather than the subpixel.
// Switching from subpixel rendering to antialiasing for
// light text on dark backgrounds makes it look lighter.
// --------------------------------------------------
.font-smoothing() {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
// --------------------------------------------------

// --------------------------------------------------
// Globals
// --------------------------------------------------
body {
  .font-smoothing;
  font-family: @body-family;
  font-size: @body-size;
  font-weight: @body-weight;
  line-height: @body-line-height;
  color: @font-color;

  // Set up a language specific font override. 
  // This will need to be uncommented out, duplicated and updated if there are 
  // more languages that require overriding. All overrides must be duplicated 
  // and added to each of the font mixins below.
  // html[lang="de"] & {
  //   font-family: Impact;
  // }
}

._removeInlineCodeStyles() {
  code {
    background-color: transparent;
		padding: 0;
		margin: 0;
		font-weight: inherit;

		// Inherit colors.
		color: inherit;
    mix-blend-mode: normal;
    border: none;
  }
}

.instruction-text() {
  font-family: @instruction-family;
  font-size: @instruction-size;
  font-weight: @instruction-weight;
  line-height: @instruction-line-height;
  color: @instruction-color;
  //font-style: italic;

  ._removeInlineCodeStyles();
}
.instruction {
  .instruction-text();
}

.button-text() {
  font-family: @btn-family;
  font-size: @btn-size;
  font-weight: @btn-weight;
  line-height: @btn-line-height;

  ._removeInlineCodeStyles();
}

.link-text() {
  font-family: @link-family;
  font-size: @link-size;
  font-weight: @link-weight;
  line-height: @link-line-height;
  color: @link;
  word-break: break-word; // Hyperlink text can be quite long. Don't let it overflow.

  .no-touch &:hover {
    color: @link-hover;
    .transition(color @duration ease-in);
  }

  &:visited {
    color: @link-visited;
  }
}

.attribution-text() {
  font-family: @attribution-family;
  font-size: @attribution-size;
  font-weight: @attribution-weight;
  line-height: @attribution-line-height;
  color: @font-color;
  font-style: italic;
}

