p {
  &:first-child {margin-top: 0;}
  &:last-child {margin-bottom: 0;}
}

// CUSTOM: Fake component sub-titles.
h2 {
  .component-sub-title();
  margin-bottom: @component-sub-title-margin;
  &:first-child {margin-top: 0;}
  &:last-child {margin-bottom: 0;}
}

// CUSTOM: On the fly titles.
h3 {
  .item-title();
  font-size: @item-title-size*0.9;
  margin-top: @figure-margin;
  margin-bottom: @item-title-margin;

  &:first-child {margin-top: 0;}
  &:last-child {margin-bottom: 0;}
}

ul,
ol {
  margin-top: 1em;
  margin-bottom: 1em;
  padding-left: 1.2em;

  &:first-child {margin-top: 0;}
  &:last-child {margin-bottom: 0;}
}

