.reorder {

  &__widget-header,
  &__widget-footer {
    font-weight: @font-weight-bold;
  }

  // Items
  // -----------------------------------------
  &__item {
    
    // Drop shadow on grabbed item.
    .transition(filter @duration/2 ease);
    &.is-grabbed {
      .drop-shadow();
    }
  }

  // Border & background.
  &__item-inner {
    border-width: @border-width*2;
    border-radius: @border-radius;
    border-color: @btn-color;
    overflow: hidden;

    // Added background colour to make the grabbed item properly cover the non-grabbed ones while moving.
    background-color: @btn-color-light;

    .transition(border-color @duration*2 ease, background-color @duration*2 ease;);
  }

  &__item.is-correct &__item-inner {
    background-color: mix(@validation-success, @question-color, 10%);
    border-color: @validation-success;
  }

  &__item.is-incorrect &__item-inner {
    background-color: mix(@validation-error, @question-color, 15%);
    border-color: @validation-error;
  }

  &__item.is-disabled.is-correct &__item-inner {
    background-color: mix(@validation-success-disabled, @question-color, 15%);
    border-color: @validation-success-disabled;
  }

  // Touch
  // -------------------------------------------------
  // Additional horizontal item margin (done to make scrolling vertically easier).
  // FIXME: Likely temporary, as the jQuery-ui.draggable() scrolling feature seems bugged.
  // We need to make our own custom version as a result.
  .touch &__items-container {
    padding-left: @block-padding-hoz;
    padding-right: @block-padding-hoz;
  }
  
  // Prevent scrolling if CodeBoxes are used as items
  // -------------------------------------------------
  &__item .codebox {
    overflow-x: visible;
    white-space: normal;

    &, .codebox__content {
      border: none; // And remove the border.
    }
  }
}
