@finder-border-width: @border-width * 2;

.finder {

  // Items container
  // ------------------------------------
  &__items-container {
    
    border: @finder-border-width @btn-color solid;
    padding: @item-padding;

    // Change border color depending on the state of the items within.
    .transition(border-color @duration ease);

    &.is-correct {
      border-color: @validation-success;
    }
    &.is-incorrect {
      border-color: @validation-error;
    }
  }
  
  // Items
  // ------------------------------------

  // Set up checkboxes
  .mcq-item(@smallIcon: true);

  // FIXME: Temp. Hide the tick and cross icons for now.
  &__item-correct-icon,
  &__item-incorrect-icon {
    display: none;
  }  

  &__item-content-inner {
    position: relative;
  }
  &__item {

    .transition(color @duration ease;);
    
    padding: @item-padding;
    @media (max-width: @device-width-large) {
      padding: @item-padding/2; // Reduce padding on smaller devices.
    }

    // Strikethrough
    // ------------------------------------------
    // Add a strikethrough active incorrect items
    .finder__item-content-inner::after {
      content: "";
      position: absolute;
      height: 2px;
      width: 0px;
      left: -0.25em;
      top: 50%;
      margin-top: -1px;

      background-color: currentColor;
      
      .transition(width @duration*2 ease);
    }
    &.is-incorrect.is-active .finder__item-content-inner::after {
      width: calc(100% ~"+" 0.5em);
    }

    &.is-incorrect {
      color: @validation-error;
      &.is-disabled {
        color: mix(@validation-error, darken(@validation-error-disabled,5%), 20%); // Mixed to improve readability.
        .finder__item-answer-icon {
          color: inherit;
        }
      }
    }
  
    &.is-correct {
      color: darken(@validation-success, 5%);
    }

    // FIXME: Temp.
    &:not(.is-disabled) {
      .no-touch &:hover {
        color: tint(@font-color, 20%);
      }
      .no-touch &.is-incorrect:hover {
        color: tint(@validation-error, 20%);
      }
    }
  }
}
