// BlockBackground
// ----------------------------------------------------

// Component background
// ====================
.block.has-bg-image .component__container, // Legacy
.block.has-blockbg .blockbg__group {
  
  position: relative;
  color: @font-color;
  background: transparent;

  &::before {
    content: "";
    position: absolute;
    top: -@item-padding;
    left: -@item-padding;
    right: -@item-padding;
    bottom: -@item-padding;
    
    background-color: fade(@font-color-inverted, 97%);

    .drop-shadow();

    // Enable hardware acceleration (prevents the background having weird graphical bugs on some devices/EPUB mode).
    transform: translateZ(0);
  }
}

// Remove dividers if the previous block also has a blockBackground.
.block.has-prevblockbg {
  border-top: none;
}

// Move the images down to accomodate for the navigation bar height.
.blockbg {

  @media (max-width: @device-width-medium) {
    &__item {
      margin-top: @sidenav-width-small;
    }
  }

  &__item {
    z-index: @blockbg-z-index; // Ensure it's behind EVERYTHING else.
  }
}