// Custom component widths
// -----------------------
// Similar to how bootstrap implements .col-xx-n column classes.
// SHOULD ONLY BE USED WHEN INSIDE OF COMPONENTROW CONTAINERS.
//
// Classes:
// *noclass*            -   The component's flex-basis will be auto, meaning it will enlarge if its componentRow sibling is < 6.
// .component.col-6     -   The component's flex-basis will be 50% when in large view (same as default Adapt 50% width).
// .component.col-x.col-maxwidth    - When in small view, the component will not go larger than x / device-width-medium width.
//                                    A good setting to apply to .graphic components to prevent them becoming too large.

// FIXME: Global vs. component sizing classes are implemented strangely. Should be reworked when time permits.

.sizing(@min-device-size) {

    .component.is-full,
    .component.is-left,
    .component.is-right {

        @media (min-width: @min-device-size) {
            width: auto;
            max-width: none;
            flex: 1 1 auto;
        }

        // Default max-width is 50% * @min-device-size (this is now set by JavaScript).
        &.col-maxwidth:not(.col-maxwidth-inner) {
            .component__inner {
                //max-width: (0.5*@min-device-size);
                margin-left: auto;
                margin-right: auto;
            }
        }
    }

    .component.is-full {
        margin: auto;
        max-width: 100%;
    }
    
    // Non-component classes
    // ------------------------------------------------------

    .col {
        width: 100%;
    }

    // Used mostly for images.
    .col-auto {
        width: auto !important;

        img {
            margin: auto;
            min-width: auto;
        }

        // Tables inside auto columns should also have auto width.
        table {
            width: auto;
            margin: auto;
        }
    }

    .col-maxwidth:not(.component) {
        //max-width: (0.5*@min-device-size);
        margin-left: auto;
        margin-right: auto;
    }

    .col-maxwidth-auto:not(.component) {
        @media (max-width: @min-device-size) {
            width: auto;
            margin-left: auto;
            margin-right: auto;
        }
    }

    // 1 - 12 loop.
    .col-loop(@i) when (@i <= 12) {

        .component.col-@{i}.is-full,
        .component.col-@{i}.is-left,
        .component.col-@{i}.is-right {

            // Flexible widths in large view
            // -----------------------------
            @media (min-width: @min-device-size) {
                width: 100%;
                flex: 0 0 (@i/12*100%);
                max-width: (@i/12*100%);
            }

            // Automatic maxwidth = flexible width in small views
            // --------------------------------------------------
            @media (max-width: @min-device-size) {
                &.col-maxwidth {
                    .component__inner {
                        max-width: (@i/12*@min-device-size);
                    }
                }
            }
        }
        .col-@{i}:not(.component) {
            // Flexible widths in large view
            // -----------------------------
            @media (min-width: @min-device-size) {
                width: 100%;
                flex: 0 0 (@i/12*100%);
                max-width: (@i/12*100%);
            }
            // Automatic maxwidth = flexible width in small views
            // --------------------------------------------------
            @media (max-width: @min-device-size) {
                &.col-maxwidth {
                    max-width: (@i/12*@min-device-size);
                    margin: auto;
                }
                &.col-minwidth {
                    min-width: (@i/12*@min-device-size);
                }
            }
        }
        @media (max-width: @min-device-size) {
            .col-maxwidth-@{i}:not(.component) {
                max-width: (@i/12*@min-device-size);
                margin: auto;
            }
            .col-minwidth-@{i}:not(.component) {
                min-width: (@i/12*@min-device-size);
            }
        }

        // Independant maxwidth to flexible width in small views
        // -----------------------------------------------------
        .component.is-full,
        .component.is-left,
        .component.is-right {
            @media (max-width: @min-device-size) {
                &.col-maxwidth-@{i} {
                    .component__inner {
                        max-width: (@i/12*@min-device-size);
                        margin: auto;
                    }
                }
            }

            // Inner maxwidth (apply at all times).
            &.col-maxwidth-inner.col-maxwidth-@{i} {
                .component__inner {
                    max-width: (@i/12*@min-device-size);
                    margin: auto;
                }
            }
        }

        // MinHeight
        // -----------------------------------------------
        .col-minheight-@{i} {
            min-height: (@i/12*@min-device-size);
        }

        .col-loop((@i + 1));
    }
    .col-loop(1);

    // Floating elements
    // ----------------------------------------------
    // The element with .col-float-* should ALWAYS be the first sibling.
    @media (min-width: @min-device-size) {

        @col-float-margin: @component-padding-hoz/4;

        .col-float {
            z-index: 1; // Have it be clickable through its sibling.
        }

        .col-float-left {
            float: left;
            margin: @col-float-margin 0 @col-float-margin*2 @col-float-margin;
        }
        .col-float-right {
            float: right;
            margin: @col-float-margin @col-float-margin @col-float-margin*2 0;
        }

        // Remove padding from the non-floated sibling.
        .col-float-left + div {padding-left: 0;}
        .col-float-right + div {padding-right: 0;}
    }
}

.sizing(@min-device-size: @device-width-medium);

// PARENT CLASSES
// -------------------------------------------------------------
// Create consistent layouts for accordion + narrative + toggletabs.
.par {

    // Float
    // -------------------------------------------------
    &-contains-float {

        display: block;

        .col-float-right {
            padding-left: @component-padding-hoz;
            &:only-child {padding-left: 0;}

            // Single column
            // ---------------------------------------------
            @media (max-width: @device-width-medium) {
                padding-left: 0;
                padding-bottom: @component-body-margin;
                &:only-child {
                    padding-bottom: 0;
                }
            }
        }
        .col-float-left {
            padding-right: @component-padding-hoz;
            &:only-child {padding-right: 0;}

            // Single column
            // ---------------------------------------------
            @media (max-width: @device-width-medium) {
                padding-right: 0;
                padding-bottom: @component-body-margin;
                &:only-child {
                    padding-bottom: 0;
                }
            }
        }
    }

    // Flex
    // -------------------------------------------------
    &-contains-flex {

        display: flex;
        align-items: center;
        flex-direction: row-reverse;

        & > * {
            flex-grow: 1;
            min-width: 0; // Prevent the children overflowing their parent.

            &:first-child {
                padding-left: @component-padding-hoz;
                &:only-child {padding-left: 0;}
            }
            &:last-child {
                padding-right: @component-padding-hoz;
                &:only-child {padding-right: 0;}
            }
        }

        // Single column
        // ---------------------------------------------
        @media (max-width: @device-width-medium) {

            flex-direction: column;
            align-items: flex-start;

            // Style the non-"graphic" element
            & > :not(.col) {
                padding-right: 0;
            }

            // Style the "graphic" element
            & > .col {
                padding-left: 0;
                padding-bottom: @component-body-margin;
                &:only-child {
                    padding-bottom: 0;
                }
            }
        }
    }
}