// Annotation
// ==========

@annotation-icon-size:      1.25em;

.annotation {

    color: @annotation-color;

    &__inner {
        display: inline;
    }
    
    // Button
    // ======
    &__btn {

        //white-space: nowrap;
        font-weight: @annotation-font-weight;
        cursor: pointer;

        // Quick hover effect.
        /*transition: filter @duration-fast ease;
        .no-touch &:hover {
            filter: brightness(110%);
        }*/
    }

    &__content {
        &:not(:last-child)::after{
            content: "\00a0"; // &nbsp;
        }
    }

    &__icon-container {
        position: relative;

        display: inline-block;
        vertical-align: text-bottom;
    }

    &__icon {

        display: flex;
        align-items: center;
        justify-content: center;
        width:  @annotation-icon-size;
        height: @annotation-icon-size;

        border-radius: @annotation-icon-size;
        border: 1px solid currentColor;

        &::before {
            font-size: @annotation-icon-size * 0.6;
            color: inherit;
        }
    }

    // Hover
    // ----------------------------
    .no-touch &:hover {
        color: @annotation-color-hover;
    }

    // Active
    // ----------------------------
    &.is-active .annotation__icon {
        background-color: currentColor;
        
        /*&::before {
            color: @annotation-color-inverted;
        }*/
    }
}

// Tooltip placeholder
// ----------------------------
.tooltip-fallback {

    &__item {
        color: @item-color;
        background-color: @item-color-inverted;
        border: 1px solid currentColor;
        padding: @tooltip-padding/2;

        // Margins between subsequent items.
        & + .tooltip-fallback__item {
            margin-top: @item-margin;
        }
    }
}