.mcqcode {

  // Items
  // --------------------------------------------------
  &__item {
    position: relative;
  }

  &__item-content-inner {
    display: flex;
    align-items: center;
  }

  // Hide the actual input.
  &__item-input {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    z-index: -1;
    .transform(translate(-50%, -50%));
  }

  &__item-label {
    display: flex;
    justify-content: stretch;
    align-items: center;
    z-index: 1;
  }

  &__item-label:not(.is-disabled) {
    cursor: pointer;
  }

  &__item-icon {
    display: none;
  }

  // Codeboxes
  // --------------------------------------------------
  &__item-content {
    flex-grow: 1;
  }

  // Item icons
  // --------------------------------------------------
  &__item-state {
    flex-shrink: 0;
  }
  
  &__item-answer-icon.is-radio .icon {
    .icon-radio-unchecked;
  }

  &__item-answer-icon.is-checkbox .icon {
    .icon-checkbox-unchecked;
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-radio .icon {
      .icon-radio-checked;
    }
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-checkbox .icon {
      .icon-checkbox-checked;
    }
  }

  &__item-correct-icon .icon {
    .icon-tick;
  }

  &__item-incorrect-icon .icon {
    .icon-cross;
  }
}
