.u-clearfix:before,
.u-clearfix:after {
  content: "\0020";
  display: block;
  height: 0;
  overflow: hidden;
}

.u-clearfix:after { clear: both; }
.u-clearfix { zoom: 1; }

.u-float-left() {
  float: left;

  .dir-rtl & {
    float: right;
  }
}
.u-float-left {
  .u-float-left();
}

.u-float-right() {
  float: right;

  .dir-rtl & {
    float: left;
  }
}
.u-float-right {
  .u-float-right();
}

.u-display-none {
  display: none !important;
}

.u-visibility-hidden {
  visibility: hidden !important;
}

.u-no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

// Columns
// ---------------------------
.u-columns-1, .u-columns-2, .u-columns-3, .u-columns-4 {
  column-gap: @item-padding;
}
// Increase column gap when used on a ul / ol.
ul, ol {
  &.u-columns-1, &.u-columns-2, &.u-columns-3, &.u-columns-4 {
    column-gap: @item-padding * 3;
  }
}

.u-columns-1 {columns: 1;}
.u-columns-2 {columns: 2;}
.u-columns-3 {columns: 3;}
.u-columns-4 {columns: 4;}
// Reduce columns on different devices.
@media (max-width: @device-width-medium) {
  .u-columns-2 {columns: 1;}
  .u-columns-3 {columns: 2;}
  .u-columns-4 {columns: 3;}
}
@media (max-width: @device-width-small) {
  .u-columns-2,
  .u-columns-3 {columns: 1;}
  .u-columns-4 {columns: 2;}
}