// DD ComponentLogo
// -----------------------

@componentlogo-small-media: @device-width-large + @componentlogo-small-size*4;

.componentlogo-small {

  // Prevent the logo from affecting the flow on large devices.
  @media (min-width: @componentlogo-small-media) {
    
  }
}

// FIXME: small componentLogo implementation is extremely crude.
// If it's ever used outside of BU11, it should be cleaned up.
.component__title-inner.has-componentlogo {

  @media (max-width: @componentlogo-small-media) {
    display: flex;

    .componentlogo-small {
      position: relative;
      transform: none;
    }
  }

  @media (max-width: (@stickyblock-maxwidth + @navigation-height + @componentlogo-small-size*4)) {
    .stickyblock & {
      display: flex;
      flex-direction: column;

      .componentlogo-small {
        position: relative;
        transform: none;
        margin-bottom: 1rem;
      }
    }
  }

  @media (max-width: @device-width-small) {
    display: block;
  }
}