// componentRow
// ============

@componentrow-divider-padding: @block-padding-ver/2;

// Adapt allows block_inner to become thinner if the content inside doesn't push it to its max.
// This will prevent that from happening (and keep the margins of the components consistant).
.block__inner {
    //width: @device-width-large;
    width: 100%;
}

.componentrow {
    position: relative;
    width: 100%;

    &__inner {
        display: flex;
        align-items: center;
        width: 100%;
    }

    // Bugfix: prevent very rare occurences where components can overflow their flex container.
    .component {
        min-width: 0;
    }

    // Floating children (no flex)
    // ----------------------------
    // If one of the children of a componentrow has "_sizing._float": true, then the content is rendered differently. 
    @media (min-width: @device-width-medium) {
        .par-contains-float {
            .col-float-left + .component {padding-left: 0;}
            .col-float-right + .component {padding-right: 0;}
        }
    }
    

    // Reverse component order
    // ----------------------------
    &-is-reverse .componentrow__inner{
        flex-direction: row-reverse;
        .component:first-child {padding-right: 0;}
        .component:last-child {padding-left: 0;}
    }
    &-is-not-reverse .componentrow__inner{
        flex-direction: row;
        .component:first-child {padding-left: 0;}
        .component:last-child {padding-right: 0;}
    }


    // The componentrow title will appear on small devices only, and will replace the normal component's title.
    // This is done to allow for the following scenario:
    //
    // [left component with title][right component with no title]
    //
    // \/ on mobile device.
    //
    // [left component's title]
    // [right component]
    // [remainder of left component]
    &__title {
        .component-title();
        margin-bottom: @component-title-margin;
        display: none;
    }
    
    // Margins
    // ----------------------------
    &:not(:first-child) {
        margin-top: @componentrow-margin;
    }
        
    // Divider
    // ----------------------------
    &-has-divider {
        &:not(:first-child) {
            padding-top: @componentrow-divider-padding;
            margin-top: @componentrow-divider-padding;
        }

        &::before {
            content: "";
            
        }
    }
    &__divider {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;

        opacity: 0.2;
    }
    &__divider-line {
        display: block;
        height: 1px;
        background: currentColor;
    }

    // Divider with title.
    &__divider.has-title {
        display: flex;
        justify-content: stretch;
        align-items: center;
        transform: translateY(-50%); // The divider will be taller than 1px. Translate to re-allign vertically between component rows.
    }
    &__divider.has-title &__divider-line {
        flex-grow: 1;
    }
    &__divider-title {
        flex-shrink: 0;
        padding: 0 @item-padding;
    }

    // Single column component spacing
    // -------------------------------
    // Needed to be modified from the default Adapt style of applying the padding only to .component.is-left
    @media (max-width: @device-width-medium) {

        &__inner {
            flex-wrap: wrap;
        }

        // Create space between the two vertical components.
        .component:first-child:not(:last-child) {padding-bottom: @component-body-margin;}

        // Show the componentrow title and hide the component title if this was a double componentRow.
        &.componentrow-is-double {
            .componentrow__title {
                display: block;
            }
            .component__title {
                display: none;
            }
        }
    }
}