// codeBoxFadeBlock
// ================

.codebox {

	.fadeblock.is-accordion {
		filter: none;
		opacity: 1;

		padding: @code-padding / 2;
		margin: -@code-padding / 2;

		background-color: rgba(255, 255, 255, 0.5);
		.transition(@duration*2 background-color ease);

		&.is-active {
			background-color: transparent;
		}
	}

	.fadeblock__header {
		cursor: pointer;

		filter: contrast(0.25);
		opacity: 0.7;
		.transition(@duration opacity ease, @duration*2 filter ease;);
	}

	// Add ... after the greyed out line.
	.fadeblock__header .codebox__line {

		& > :last-child::after {
			content: " ";
			color: @codebox-color-inverted;

			.transition(@duration*2 opacity ease);
		}
	}

	// Active
	// ------------------------------------------
	.fadeblock__header.is-active {
		filter: none;
		opacity: 1;
	}

	.fadeblock__header.is-active .codebox__line {

		& > :last-child::after {
			opacity: 0;
		}
	}
}