// EPUB mode
// ====================================

.mode-epub {
    .sidemenu,
    .nav {
        display: none !important; // Hide the navigation entirely while in EPUB mode.
    }

    .component__container,
    .page__header-inner > * {
        margin-left: 0 !important; // Remove the adjustment for the sidenav.
    }

    #wrapper {
        padding-top: 0 !important;
    }
    #app {
        border-top: none !important;
    }

    .page__header.has-bg-image {
        background-position-y: 0 !important;
    }
}

// Desktop Bookshelf
// ---------------------------------------------------
// Disable background-attachment: fixed everywhere!
// (Causes slowdown in Bookshelf, both Offline and Online).
html.mode-epub {
    * {
        background-attachment: local !important;
    }
}
// Non-desktop Bookshelf
// ---------------------------------------------------
html.mode-epub:not(.desktop-bookshelf) {
    
    // When viewed in browser, browser Bookshelf automatically adds an inline style to the body to give it overflow: auto;
    // This conflicts with Adapt's native scrolling; disable it.

    // EDIT: As of the latest Bookshelf update (09/08/2021), this is no longer needed.
    /*body {
        overflow: visible !important;
    }*/
}