// codeBox
// =======

@codebox-filename-size: 	@body-size * 0.8;
@codebox-filename-padding: 	@item-padding/4;

.codebox {

	text-align: left;

	display: block;
	font-family: @codebox-family;
	white-space: pre-line;

	&__inner {
		height: 100%;
	}

	// Take up all available height if the container's height is manually set by external scripts.
	&__content {
		height: 100%;
	}

	&__content-inner {
		padding: @item-padding;
	}

	// overflow-x
	// -----------------------------------------------------
	// Never wrap codebox lines. Have the codebox scroll horizontally instead.
	&.is-code {
		overflow-x: auto;
		//min-width: 0; // Fixes incorrect width bug in flexboxes.
		white-space: pre; //nowrap;

		// Allow for wrapping to be explicitly enabled. 
		&.wrapping-enabled {
			white-space: normal;
		}
	}
	
	// Elements automatically added by grunt JSONSYNT build task.
	.tab {
		-moz-tab-size: @codebox-tab-size;
		tab-size: @codebox-tab-size;
		white-space: pre;
	}
	// Increased tab size for pseudocode.
	&.lang-pseudo .tab {
		-moz-tab-size: 6;
		tab-size: 6;
	}

	// Code box
	// ------------------------------------------------------
	&.is-code &__content {
		border: @codebox-border-width solid @codebox-border-color;
		background: @codebox-color;
		color: @codebox-color-inverted;
	}

	// Console box
	// ------------------------------------------------------
	&.is-console &__content {
		border: @codebox-border-width solid @codebox-color-inverted;
		background-color: @codebox-color-inverted;
		color: @codebox-color;

		// If HTML, display the initial font.
		&.lang-html {
			font-family: initial;
		}

		// While a stickyElement mark is active, invert the colours of the text inside it.
		.is-se-mark {
			color: @codebox-color-inverted;
		}
	}

	// Psuedocode
	// ------------------------------------------------------
	&__pseudocode {
		font-style: italic;
		// Remove any syntax colouring (added by code prettify) from immediate child <span> elements. 
		& > span {
			color: inherit;
		}
	}

	// EPUB compatibility
	// -----------------------------------------------------
	// Slightly increase vertical padding on horizontally scrolling codeboxes when in Desktop Bookshelf.
	// (This is done to prevent the horizontal scrollbar overlapping the code).
	.mode-epub.desktop-bookshelf &__content-inner {
		//padding-top: @item-padding*1.25;
		padding-bottom: @item-padding*1.25;
	}

	// Line numbering
	// ------------------------------------------------------
	
	// Add a nbsp to empty lines (to make them visible to the user, but still have no selectable text).
	/*&__line:empty::after {
		content: "\00a0" // &nbsp;
	}*/

	// Needed for empty lines.
	&__line.is-empty {
		white-space: pre;
	}

	// Iframe console
	// ------------------------------------------------------
	&__iframe {
		width: 100%;
		height: 100%;
		border: none;

		// BUGFIX: On some older browsers, the contents of the iframe can dissappear when the user scrolls them away.
		// This fixes that.
		transform: translateZ(0px);
	}

	// Optional filename
	// ------------------------------------------------------
	&__filename {
		display: inline-block;
		padding: @codebox-filename-padding @codebox-filename-padding*2;
		line-height: 1.2;
		font-size: @codebox-filename-size;
		background-color: @codebox-border-color;
	}

	// FIXME: This style only exists to accomodate for side-by-side flexbox items.
	&__filename + &__content {
		@a: (@codebox-filename-padding*2) + @codebox-filename-size*1.2;
		@b: @border-width;
		height: calc(100% ~"-" @a ~"-" @b);
	}
}