// Figure
// ---------------------------------------------------

.figure {
  
  .is-question & {
    display: flow-root; // Better layout when used in questionplus headers.
  }

  // Figure spacing
  // --------------

  margin: @figure-margin 0;
  &:first-child {
    margin-top: 0;
  }

  // Reduce vertical margins for sequential figures.
  .figure + & {
    margin-top: -@figure-margin/2;
  }

  // If placed directly after a clearfix, tweak the margins slightly.
  .u-clearfix + & {
    margin-top: 0;

    .figure__inner {
      margin-top: 0;
    }
  }

  // Remove bottom margin unless last child of component header.
  &:last-child {
    margin-bottom: 0;
  }
  .component__body &:last-child:not(:only-child) {
    margin-bottom: @figure-margin;
  }

  // Add margins between figure items.
  &__inner {
    .figure-inner();
  }
  &.is-row &__item {
    .figure-item-spacing();
  }
  
  // direction="wrap"
  // ---------------------------------------
  // Explanation: unfortunately, the "gap" style can't be used, as it won't work inside of EPUBs.
  // As such, negative margins are used as a workaround.
  &.is-wrap &__inner {
    margin: -@figure-margin -@figure-margin/2;
  }
  &.is-wrap &__item {
    margin-top: @figure-margin;
    margin-bottom: @figure-margin;

    padding-left: @figure-margin/2;
    padding-right: @figure-margin/2;
  }

  // FIXME: Only works for single item figures.
  /*&.is-row &__item.has-lightbox {
    @a: @figure-padding-hoz*2;
    max-width: calc(100% ~"-" @a);
    margin: 0 @figure-padding-hoz
  }*/

  &.is-column &__item {
    margin: @figure-padding-ver 0;
    &:first-child {margin-top: 0;}
    &:last-child {margin-bottom: 0;}
  }

  // StickyElement compatibility
  // --------------------------------------------
  // Disable negative vertical margins if inside of a stickyElement clone (fixes misalignment).
  .stickyelement.is-clone & {
    &:first-child .figure__inner {
      margin-top: 0 !important;
    }
    &:last-child .figure__inner {
      margin-bottom: 0 !important;
    }
  }

  // iPhone Safari bugfix(?)
  .notify & {
    transform: translate3d(0,0,0);
  }

  // Add an additional margin if the last child in the figure item is a <p> tag (and the figure is in single column mode).
  @media (max-width: @device-width-medium) {
    &__item > p:last-child {
      margin-bottom: 1em;
    }
  }

  // Lightbox
  // --------------------------------------------------
  // Lightboxes need to use margins instead (for calculation).
  // FIXME: Only one lightbox per figure is allowed at this time.
  &.is-row &__item.has-lightbox:only-child {
    .figure-item-spacing(@useMargin: true);
    padding: 0;

    // Adjust for the negative margins of the .figure__inner element.
    @media (min-width: @device-width-medium) {
      @a: @figure-padding-hoz*2;
      max-width: calc(100% ~"-" @a);
      margin-left: auto;
      margin-right: auto;
    }
  }

  // Fade in the lightbox icon on mouseover.
  .no-touch &__item.has-lightbox:hover &__lightbox-icon {
    .watermark-hover();
  }

  // Custom themes
  // --------------------------------------------------
  
  // Math
  &.is-mode-math, // LEGACY
  &.theme-math {
    background-color: @codebox-color;
    border: @border-width @codebox-border-color solid;
    padding: @item-padding;

    // Slightly offset the magnifying glass.
    .figure__lightbox-icon {
      top: -@item-padding*2;
      right: -@item-padding*2;
    }

    @media (max-width: @device-width-medium) {
      padding: @item-padding*1.5;

      .figure__lightbox-icon {
        top: -@item-padding*2.5;
        right: -@item-padding*2.5;
      }

      .figure__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      &.is-row .figure__item {
        width: auto;
        ._figure-item-spacing(2; 2; 1; false);
      }
    }
  }

  // Math (no background)
  //&.is-mode-math
}