// Global <img> tag rules
// ---------------------------------------------

@broken-img-color: @disabled;
@broken-img-color-inverted: mix(@disabled, @disabled-inverted, 30%);

img {

	// Broken image message
	// -------------------------------------------
  	&::before,
	&::after {
		content: "";
		position: absolute;
		
		top: 50%;
		right: 0;
		left: 0;
    	transform: translateY(-50%);
	}
	
  	// Background.
	&::before { 
		background-color: @broken-img-color;
		border: @border-width*2 dashed @broken-img-color-inverted;
    	min-height: @item-padding * 4;
		height: 100%;
	}

  	// Icon
	&::after { 
		content: "\e9ef";
    	font-family: "vanilla";
    	line-height: 0.8em;
	
		position: absolute;
		font-size: @item-title-size * 1.2;
		font-weight: bold;
		color: @broken-img-color-inverted;
		text-align: center;
		
		width: auto;
	}

	// Safari bugfix:
	// --------------
	// (For unknown reasons) Safari may sometimes clone an image with the wrong path on a touchstart event.
	// To fix this (until the cause is known), only show the custom broken image elements if the image has the class assigned.
	.safari &:not(.has-error) {
		&::after,
		&::before {
			content: none;
		}
	}
}