// Annotation (mark styles)
// ========================

// Mark
// -------------------------------------------
mark.is-annotation-target,
.mark.is-annotation-target{

    //display: inline-block; // Makes the bounce animation work.

    // Transition background colour.
    .transition(background @duration ease, color @duration ease, margin @duration/2 ease, padding @duration/2 ease;);
    transform: translateZ(0); // Enable hardware acceleration.

    &:not(.is-active){
        background-color: transparent;
        border-color: transparent;
        padding-left: 0;
        padding-right: 0;
        margin-left: 0;
        margin-right: 0;
    }

    &.is-active {
        z-index: 1;
    }

    /*&.is-active {
        margin-left: 0;
        margin-right: 0;
    }*/

    position: relative;
    z-index: 0;
}

mark,
.mark {
    // Absolute positioning
    // ---------------------------------------------------
    &.is-absolute {
        position: absolute;
        display: block;
        border-style: solid;
        .transition(margin @duration/2 ease, padding @duration/2 ease, border @duration/2 ease;);
    }
    
    // Custom backgrounds
    // ---------------------------------------------------
    // Used very very rarely (in BU11).
    &.has-custom-background {
        border-color: rgba(0,0,0,0.2);
    }
}