// HotgraphicFlip
// ------------------------------------------

@hotgraphicflip-perspective: @perspective;
@hotgraphicflip-padding: @item-padding;
@hotgraphicflip-duration: @duration * 2;
@hotgraphicflip-border-radius: @item-border-radius;
@hotgraphicflip-card-color: mix(@btn-color, @font-color-inverted, 25%);

.hotgraphicflip {
  
  // Items container
  // -------------------------------------
  &__item-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0px -@item-margin;
  }

  // Item
  // -------------------------------------
  &__item {
    display: block;
    position: relative;
    perspective: @hotgraphicflip-perspective;

    page-break-inside: avoid;

    //flex: 1 1 auto;
    //width: 33.333%;
    //margin: @item-margin/2;
    
    cursor: pointer;
    padding: @item-margin; // Spacing.

    &.is-active {
      cursor: auto;
    }
  }

  &__item-inner {
    //box-shadow: 0 5px 10px rgba(0,0,0,0.5);
    border-radius: @hotgraphicflip-border-radius;
    height: 100%;
    width: 100%;

    //margin: @item-margin/2;

    transform-style: preserve-3d;
    transform: rotateY(0deg);
    .transition(transform ease-in-out @hotgraphicflip-duration);
  }

  // FIXME: Temporary implementation of flip back button.
  &__item-btn {
    position: absolute;
    bottom: -@hotgraphicflip-padding*1.35;
    right: -@hotgraphicflip-padding/2;
    cursor: pointer;
  }

  &__item-front,
  &__item-back {
    backface-visibility: hidden;
  }

  &__item-front-inner,
  &__item-back-inner {
    position: relative;
    width: 100%;
    height: 100%;
  }

  // Front (image)
  // -------------------------------------
  // Have the image adjust itself to be as big as the surrounding wrapper.
  &__item-front {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;

    border-radius: inherit;
    background-color: @hotgraphicflip-card-color;

    transform: rotateY(0deg);

    img {
      width: 100%;
      height: 100%;
    }

    // Reduce padding on smaller devices (allowing the image to be slightly bigger).
    padding: @hotgraphicflip-padding * 1.5;
    @media (max-width: @device-width-large) {padding: @hotgraphicflip-padding * 1.25;}
    @media (max-width: @device-width-medium) {padding: @hotgraphicflip-padding;}
  }

  // Back (text)
  // -------------------------------------
  &__item-back {
    position: relative;
    background-color: @item-color;

    padding: @hotgraphicflip-padding;
    // Add some additional padding at the bottom (so that the back button doesn't overlap the content).
    padding-bottom: @hotgraphicflip-padding * 1.75;

    height: 100%;
    
    border-radius: inherit;
    transform: rotateY(180deg);
    z-index: 1;
  }

  &__item-title {
    margin-bottom: @item-title-margin;
    .item-title();
  }

  // Active
  // -------------------------------------
  &__item.is-active .hotgraphicflip__item-inner {
    transform: rotateY(-180deg);
  }
  
  // Fallback; Something was preventing clicking on the item-back on iOS devices.
  // Ensure the front cannot intefere while the item is flipped.
  &__item.is-active &__item-front {
    pointer-events: none; 
  }
  // And vice-versa
  &__item:not(.is-active) &__item-back {
    pointer-events: none;
  }

  // FIXME: Non-standard implementation of sizing.
  .maxwidth-loop(@i) when (@i <= 12) {
    
    .--maxwidth-inner-@{i} {
      max-width: @i * (@device-width-medium/12);
      min-width: auto;
      margin: auto;
    }
    
    .maxwidth-loop((@i + 1));
  }
  .maxwidth-loop(1);
  
  // FIXME: Non-standard implementation of sizing.
  .minheight-loop(@i) when (@i <= 12) {
    &__item.col-sm-minheight-@{i} &__item-inner {
      min-height: @i * (@device-width-medium/12);
    }
    .minheight-loop((@i + 1));
  }
  .minheight-loop(1);

  // adapt-dd-img compatibility
  &__item-front .ddimg,
  &__item-front .ddimg__inner {
    height: 100%;
  }
}