.sidenav {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);

  // Hover
  // ------------------------------
  .no-touch & {
    .transition(background-color @duration ease-in);
    &:hover {
      background-color: fade(@nav, 70%);
    }
  }

  // Hide back button
  // ------------------------------
  .nav__back-btn {
    display: none !important;
  }

  // Horizontal layout
  // -----------------------------------
  @media (max-width: @device-width-medium) {
    background-color: fade(@nav, 100%);
  }
}
