// Tooltip
// --------------------------------

@tooltip-padding: @item-padding;
@tooltip-margin: @tooltip-padding * 1.5; // How far away (vertically) from the target the tooltip should appear.
@tooltip-tail-size: @tooltip-margin;

.tooltip {

    display: inline;
    position: relative;

    //white-space: nowrap; // Prevent the icon wrapping to the next line without the text besides them.

    &__btn {
        cursor: pointer;

        // Try to "glue" the icon to any text word it comes after (prevents the icon being orphaned on a new line).
        //white-space: nowrap;
    }

    // Tooltip text
    // --------------------------------
    &__text {
        font-weight: bold;
        border-bottom: 1px dotted currentColor;
    }

    // Tooltip icon (icon/img)
    // --------------------------------
    &__icon {

        white-space: nowrap; // Prevent any text that comes directly after/before being orphaned.

        //margin-right: 0.25em;
        .icon {
            vertical-align: text-bottom;
            &::before {
                font-size: 1em;
            }
        }

        img {
            display: inline-block;
            height: 1em;
            min-width: 0;
            vertical-align: text-top;
        }
    }

    .no-touch &:hover &__btn {
        // FIXME: Temp.
        //color: mix(@black, @white, 90%);
        opacity: 0.75;
    }
}

// Tooltip popup
// --------------------------------
.tooltip-popup {

    display: block;
    position: absolute;
    z-index: 100;

    padding: 0 @block-padding-hoz/2; // Have the popup deliberately overlap the block padding.
    
    max-width: @device-width-small; // FIXME: Temp?
    pointer-events: none; // FIXME: Temp? (The .tooltip-popup element blocks pointer events. This fixes that).

    &__inner {

        pointer-events: all; // FIXME: Temp? (The .tooltip-popup element blocks pointer events. This fixes that).

        position: relative;
        display: block;
    
        color: @item-color;
        background-color: @item-color-inverted;
        border: darken(@item-color-inverted, 15%) @tooltip-border-width solid;

        padding: @tooltip-padding;
    
        font-size: @body-size;
    
        transform: translateY(calc(-100% ~"-" @tooltip-padding));
    }

    // Tail
    // FIXME: Very odd CSS used to accomplish this. Make more standard if possible.
    @tail-container-size: ceil(@tooltip-tail-size * 1.414); // Width = hypotenuse of tail.
    @tail-offset: (@tail-container-size - @tooltip-tail-size)/2;

    &__tail-container {
        position: absolute;
        width: 0;      
        height: 0;
        
        top: 100%;
        left: 50%;

        // These styles will be inherited by the tail elements to color them.
        background-color: inherit;
        border-color: inherit;
        border-style: inherit;
        border-width: 0;
    }
    &__tail-bg,
    &__tail-border {
        display: block;
        position: absolute;
    }
    &__tail-bg {
        background-color: inherit;
        transform: rotate(45deg);
        top: -@tooltip-tail-size/2;
        left: -@tooltip-tail-size/2;
        width: @tooltip-tail-size;
        height: @tooltip-tail-size;
    }
    &__tail-border {

        pointer-events: none; // Allow clicking through.

        top: 0;
        left: -@tail-container-size/2;
        height: @tail-container-size/2;
        width: @tail-container-size;
        overflow: hidden;

        border-style: inherit;
        border-width: 0;
        border-color: inherit;

        &::after {
            content: "";
            position: absolute;
            left: 0;
            width: @tooltip-tail-size;
            height: @tooltip-tail-size;
            margin-left: @tail-offset;
            border-style: inherit;
            border-width: @tooltip-border-width;
            border-color: inherit;

            transform: translateY(-50%) rotate(45deg);
        }
    }
}