// CodeBox
// --------------------------------------------------------

// Give special style to parent .figure elements of codeboxes.
.figure.is-figure-codebox .figure__inner {
  .figure-inner-half();

  // {{#se_fold}} bugfix
  // -------------------
  // Keep the figure in row mode with wrapping (instead of its default behaviour to swap to column at this device size).
  // This prevents display issues if the codebox contains {{#se_fold}} helpers, while keeping the figure behaviour visually
  // identical.
  /*flex-direction: row;
  align-items: stretch;
  flex-wrap: wrap;*/
}

.codebox {

  // Keep normal font size inside of feedback notifys.
  .notify__popup.is-component & {
    font-size: @codebox-size;
  }  

  &__inner {
    // Makes the padding still be present if the content scrolls
    // + makes <mark> outlines properly outline entire codelines if overflow-x scrolling is present.
    display: inline-block;
    min-width: 100%;
  }
  &__content-inner {
    padding: @code-padding;
  }

  // Figure compatibility
  // -------------------------------------------------------
  // Important: codeboxes should NOT be wrapped in figureItems for optimal performance.
  .figure.is-row & {
    
    // Consistant widths
    // -----------------------------------------------------
    flex-grow: 1; // If a codebox element is alone inside of a figure, have it take up the full width.
    &.is-code {
      //flex-basis: 100% - @codebox-console-width;
      //flex-shrink: 1;
      &.has-sizing {
        flex-basis: auto;
      }
    }
    &.is-console {
      //flex-basis: @codebox-console-width;
      //flex-shrink: 2;
      min-width: @codebox-console-width;
      &.has-sizing {
        flex-basis: auto;
      }
    }
    .figure-item-spacing-half(@useMargin: true); // Margins

    // Take up full width when in single-column view.
    @media (max-width: @device-width-medium) {
      width: 100%;
    }
  }

  // If the figure is a column, then just add a margin to the console.
  .figure.is-column & {
    &:not(:first-child).is-console {
      margin-top: @figure-margin/2;
    }
  }

  // Codebox
  // -----------------------------------------------------
  // If the codebox has HTML inside it, allow the code to wrap.
  &.is-code.lang-html {
    white-space: normal;
  }

  // Consolebox
  // -----------------------------------------------------
  // If the console is showing HTML output, render it to look like a browser instead.
  &.is-console.lang-html {
    
    // Deliberately revert the text styles to their default; it's meant to look like a fresh webpage.
    color: initial;
    font-family: initial;

    // Values hardcoded to the codebox-browser.png.
    box-sizing: border-box;
    border-width: 37px 100px 1px 100px;
    border-style: solid;
    border-image: url(assets/codebox-browser.png) 37 100 1 100 stretch;
    background-color: white;

    .codebox__content-inner {
      margin-left: -100px;
      margin-right: -100px;
      //margin-top: -37px;
      //margin-bottom: -1px;

      width: calc(100% ~"+" (100px * 2));
    }

    // Reset ALL CSS for ALL Elements.
    // FIXME: Not a proper reset.
    .codebox__content-inner {

      * {
        font-family: inherit;
      }

      h1, h2, h3, h4, h5, h6 {
        font-weight: bold;
        margin-top: 1em;
        margin-bottom: 1em;
      }

      h3 {
        font-size: 1.17em;
      }
    }
  }


  // Line numbering
  // -----------------------------------------------------
  // Hide the line numbering of the .is-code elements (but keep the wrappers; they're used by codeBoxAnim).
  &__lines {
    padding-left: 0;
  }

  &__line {
    margin-top: 0;
    margin-bottom: 0;
    position: relative;
  }

  // Misc.
  // -----------------------------------------------------

  // Comments
  .com {
    font-style: italic;
    color: @codebox-comment-color;
  }

  // Mark compatibility.
  &.is-console {
    
    // Invert colour of codebox output when it's marked.
    .mark.is-annotation-target.is-active,
    .mark:not(.is-annotation-target) {
      color: @codebox-color-inverted;
    }
  }

  // Errors in console (visual choice). Give a slight red tint.
  &.is-console .is-error {
    color: @codebox-console-error-color;
  }

  // Wrap console output in <span> tags with this class to indicate that it's "user input".
  &.is-console .console__input {
    color: @codebox-console-input-color;
  }

  // Psuedocode
	// ------------------------------------------------------
	
  // LEGACY
  &__pseudocode {

    // Slightly different looking italics (easier to read).
    /*font-style: normal;
		transform: skew(-7.5deg);*/
    display: inline-block;

    font-style: italic;
    font-family: @body-family;
	}
 
  // NEW
  &.lang-pseudo, &.lang-pseudocode {
    font-style: italic;
    font-family: @body-family;
  }

  &__filename {
    border-top-left-radius: @border-radius;
		border-top-right-radius: @border-radius;

    .u-no-select;
  }
}