// BlockBackground
// -----------------------------------------

@blockbg-margin: 40vh; // TEMP.
@blockbg-duration: @duration*4;

// Bugfix? Prevent the user zooming out the phone too far and glitching the backgrounds out.
#app {
    width: 100%;
    overflow: hidden;
}

.block.has-blockbg {
    background-color: transparent;
}

// BlockBG styles
// -----------------------------------------
.blockbg {

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &__item {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        // Background attachment fixed set up in this way to greatly improve performance on EPUB and mobile.
        // WARNING: If other blocks don't have a background color, this WILL show through them.
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        position: fixed;
        z-index: -2;

        //background-attachment: fixed;
    }

    &.is-inview &__item {
        //opacity: 0;
    }

    // Stylize component row groups
    // -----------------------------------------
    
    // Container set up in this way to allow for a non-fixed background override.
    &__group-container {
        position: relative;
        padding-top: @blockbg-margin;
        padding-bottom: @blockbg-margin;
        width: 100%;
    }

    &__group {
        display: block;
        background-color: white;
        width: 100%;

        // Remove divider from inner component rows
        .componentrow:first-child .componentrow__divider {
            display: none;
        }
    }

    // Items
    // -----------------------------------------
        
    &__item {
        .transition(opacity @blockbg-duration ease);
    }
    
    // Fade out all items that are after the active item.
    &__item.is-active ~ &__item {
        opacity: 0;
        pointer-events: none;
    }

    // Allow specific items to be hidden manually.
    &__item.is-hidden {
        opacity: 0;
        pointer-events: none;
    }

    // Scrolling background
    // -----------------------------------------
    &__group-img {
        display: none; // Only display when on devices that need it.

        /*position: absolute;
        top: 0;
        left: 0;
        bottom: 0;

        // Cover the entire viewport width.
