// Essay
// -----------------------------------------------------------

.essay {

  &__textarea-container {
    position: relative;
    display: flex;
    margin-bottom: @item-margin;
  }
  
  // Textarea
  // ----------------------------------------------------
  &__textarea {
    
    width: 100%;
    
    font-size: @body-size;
    line-height: @body-line-height;
    padding: @item-padding;
    font-family: @body-family;
    resize: none;
    
    border: @border-width @btn-color dashed;
    border-radius: @border-radius;
    
    &::placeholder {
      font-style: italic;
    }
    
    &:focus {
      border: @border-width*2 @btn-color solid;
      margin-top: -@border-width;
      margin-bottom: -@border-width;
      padding: @item-padding calc(@item-padding ~"-" @border-width);
    }
  }
  
  // Answer
  // ----------------------------------------------------
  &__answer {
    position: absolute;
    inset: 0;

    padding: @item-padding;

    background: lighten(@disabled, 2%);
    border: @border-width darken(@disabled, 10%) solid;
    border-radius: @border-radius;

    font-style: italic;

    //.u-no-select;

    display: none;
  }

  &.is-answer-shown &__answer {
    display: block;
  }
  &.is-answer-shown &__textarea {
    .u-no-select;
  }

  &__answer-btn {
    min-width: 12rem;
  }
  
  &__footer {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &__length {
    
    &.has-error {
      color: @validation-error;
    }
  }
}
