// Page Mode
// =========

// Debug toggle button
// ===================
.debug__toggle
{
	padding: 2px 5px !important;
	margin: 2px !important;

	margin-top: 100px !important;

	// Inactive state
	// ==============
	background-color: gray !important;
	color: white;
	opacity: 0.3;

	// Active state
	// ============
	// Turn purple while .mode-debug is active
	.mode-debug &
	{
		background-color: purple !important;
		opacity: 1;
	}
}
// FIXME: In future, the debug should just be removed upon publishing.
html:not(.mode-debug) .is-debug {
	display: none !important;
}

/*********/
/* DEBUG */
/*********/

// Strikethrough the navigation-block-num
// ======================================
// The .debug-block-id should be referred to when in debug mode.
.navigation-block-num
{
	.mode-debug &
	{
		// Show the current block's ID over the top of the block num.
		&::after {
			content: attr(data-block-num-id) " ";
			position: absolute;

			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: purple;
			color: white;
			font-weight: bold;

			padding: 11px 0px;
			text-align: center;
			z-index: 1;
		}

		.navigation-block-num-text {
			text-decoration: line-through;
			opacity: 0.2;
		}
	}
}

// Debug block ID
// ==============
// Sit in top right corner.
.debug-block-id
{
	display: none;

	.mode-debug &
	{
		display: block;

		position: absolute;
		top: 10px;
		right: 10px;

		font-size: 1.5rem;

		font-family: @code-family;

		color: white;
		text-shadow:
		-1px 0 0 purple,  
		-1px -1px 0 purple,  
		0 -1px 0 purple,
		-1px -1px 0 purple,
		1px 0 0 purple,
		1px -1px 0 purple,
		0 1px 0 purple,
		-1px 1px 0 purple;
		
		opacity: 0.8;
		font-weight: bold;
	}
}