// Content which sits inside the sidemenu__accordion-item-content
// -----------------------------------------

.smcategory {

    height: 100%;
    overflow: hidden;
    color: @drawer-text;

    &__inner {
        padding: @sidemenu-padding;
        overflow-y: scroll;

        // iOS fix:
        // --------
        // If this element doesn't have a fixed height, then it will not be scrollable on iPad Safari.
        // Giving this element a max-height of 100% (as a fallback), and it's parent a fixed height with javaScript seems to fix the problem.
        height: 100%;
        max-height: 100%;
        overscroll-behavior: contain;
        -webkit-overflow-scrolling: touch;
        // --------
    }

    &__item {
        
        // Divider
        &:not(:last-child)::after {
            content: "";
            position: relative;
            display: block;

            width: 100%;
            height: 1px;
            background-color: @drawer;
            opacity: 0.25;
        }
    }

    &__item-inner {
        padding-top: @item-padding;
        padding-bottom: @item-padding;
    }

    &__item-title {
        .drawer-item-title;
        margin-bottom: @drawer-title-margin;

        color: @drawer-title;
    }

    &__item-body {
        //text-align: justify;
    }

    // Placeholder item
    // ----------------------------------------------
    // Added whenever certain categories have no items.
    // Just displays text stating "There are no TITLE for this learning content."
    &__item.is-placeholder &__item-body {
        font-style: italic;
    }

    // Nav items
    // ----------------------------------------------
    &__item-type-nav {
        .smcategory__item-inner {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    .nav__item {
        padding-top: @item-padding / 1.5;
        padding-bottom: @item-padding / 1.5;
        display: flex;
        align-items: center;
        cursor: pointer;

        .transition(background-color @duration ease);

        padding-left: @item-padding / 1.5;
        padding-right: @item-padding / 1.5;

        &-title {
            margin-bottom: 0;
            flex-grow: 1;
            font-weight: @font-weight-regular;
            color: @drawer-text;

            ._removeInlineCodeStyles();
        }
    
        &-num {
            font-weight: bold;
            font-family: @title-family;
            flex-grow: 0;
            flex-shrink: 0;
    
            width: 2.25rem;
            text-align: center;
            margin-left: @item-padding;
            
            color: @drawer-title;

            .transition(color @duration ease);
        }

        &.is-complete .nav__item-num {
            color: @validation-success; // FIXME: Temp.
        }

        &.is-inview {
            background-color: mix(@drawer, @drawer-inverted, 25%);
        }
    }

    // Authors items
    // -------------------------------
    &__item-type-authors {
        .smcategory__item-graphic {
            float: right;
            width: @item-padding * 5;
            border-radius: 50%;
            overflow: hidden;

            margin-left: @sidemenu-padding*2;
            margin-bottom: @sidemenu-padding;
        }
    }

    // Reference items
    // -------------------------------
    &__item-type-references {
        
        // Allow hyperlinks in references to wrap across lines.
        a[href] {
            word-break: break-all;
        }
    }
    
    // Copyright message
    // -------------------------------
    &__copyright {
        margin-top: @item-padding*2;
        text-align: right;
        color: fade(@font-color, 70%);
        font-size: @copyright-size;
    }
}