// TryHtml
// --------------------------------------

@tryhtml-input-color: lighten(saturate(@question-color, 10%), 3%); // Use a background to indicate interactivity.
@tryhtml-input-height-extra: 2.5rem; // Additional height added to height of the input element.
@tryhtml-input-max-height: 24.25rem; // Allow for approx. 24.25 lines of code before scrolling.

@tryhtml-head-color: @item-color-selected;
@tryhtml-head-color-correct: hsl(hue(@validation-success), saturation(@tryhtml-head-color), lightness(@tryhtml-head-color));

.tryhtml {

	// UI
	// ----------------------------------
	// Have the header use similar styles to an active accordion button.
	&__head {
		position: relative;
		padding: @item-padding/2.5 @item-padding;
		text-align: left;
	
		//background-color: @item-btn-color;
		//color: @item-btn-color-inverted;
		background-color: @tryhtml-head-color;
    	color: @item-color-inverted-selected;
		
		border-top-left-radius: @item-border-radius;
		border-top-right-radius: @item-border-radius;
		font-weight: @btn-weight;
		font-family: @btn-family;
	
		.dir-rtl & {
		  	text-align: right;
		}
	}

	&__output-correct-head {
		background: @tryhtml-head-color-correct;
	}

	// Show answer
	&__ui-head {
		.transition(background-color @duration ease);
	}
	&__widget.show-correct-answer {

		// Turn the UI head green.
		.tryhtml__ui-head {
			background: @tryhtml-head-color-correct;
		}
	}
	
	// Have the text in the update btn be italic when it's disabled
	// (the text on it changes to show a message while it's in this state).
	&__ui-btn-update.is-disabled {
		font-style: italic;
	}

	// FIXME: Temp. Rough width used to keep the "Show answer" button's width consistent.
	&__ui-btn-toggleanswer {
		width: 10rem;
		flex-grow: 0;
		flex-shrink: 0;

		@media (max-width: @device-width-small) {
			width: calc(50% ~"-" (@item-margin/2));
		}
	}

	// Content
	// ----------------------------------
	&__content-container {
		
	}

	&__content-scroll {
		max-height: @tryhtml-input-max-height;

		overflow-y: scroll; // Alway offer a scrollbar.
		border: @codebox-border-width @codebox-border-color solid;
		background: hsl(hue(@validation-success), saturation(@tryhtml-input-color), lightness(@tryhtml-input-color));

		.transition(background-color @duration ease);
	}

	&__widget:not(.show-correct-answer) &__content-scroll {
		background: @codebox-color;
	}
	
	&__content-user {

		// Have the input field resemble a codebox.
		background: @codebox-color;
		//background: @tryhtml-input-color; 
		
		padding: @code-padding;
	}

	&__content-correct {
		.codebox__content {
			background: transparent !important;
			border: none !important;
		}

		padding-bottom: @tryhtml-input-height-extra;
	}

	
	// OUTPUT
	// ----------------------------------
	&__output {
		//min-height: 15rem; // TEMP.
	}

	&__output-iframe {
		border: none;
	}

	&__output-iframe-container {
		border: @codebox-border-width @codebox-console-html-color solid;
	}

	
	// CodeBox
	// ----------------------------------
	
	// Fixes a bug where blank lines have 0 height.
	.codebox__line {
		&::before {
			content: "\200B"; // ZWS.
			// FIXME: If the user copy and pastes from the correct script, these lines WON'T be copied. Minor bug.
		}
	}
	
	// CodeMirror
	// ----------------------------------
	.CodeMirror {
		font-family: @code-family;
	}

	// Remove padding that came with the plugin.
	.CodeMirror-lines {
		padding-top: 0 !important;
		padding-bottom: 0 !important;
	}

	.CodeMirror-line {
		padding-left: 0 !important;
		padding-right: 0 !important;
	}
}