// --------------------------------------------------
// Screen sizings
// --------------------------------------------------
@device-width-small: unit(@adapt-device-small, rem);
@device-width-medium: unit(@adapt-device-medium, rem);
@device-width-large: unit(@adapt-device-large, rem);

@max-width: 90rem;
// --------------------------------------------------

// --------------------------------------------------
// Navigation height
// --------------------------------------------------
@navigation-height: @icon-size + (@icon-padding * 2);
// --------------------------------------------------

// --------------------------------------------------
// Menu container and header padding
// --------------------------------------------------
@menu-padding-ver: 0;
@menu-padding-hoz: 0;

@menu-header-padding-ver: 2rem;
@menu-header-padding-hoz: 0.5rem;
// --------------------------------------------------

// --------------------------------------------------
// Page container and header padding
// --------------------------------------------------
@page-padding-ver: 0;
@page-padding-hoz: 0;

@page-header-padding-ver: 2rem;
@page-header-padding-hoz: 0.5rem;

// Banner height (desktop).
@page-header-height-large: 55vw; // vw use intentional; it ensures that the whole image is always visible on desktop.

// --------------------------------------------------

// --------------------------------------------------
// Article header padding
// --------------------------------------------------
@article-header-padding-ver: 0;
@article-header-padding-hoz: 0.5rem;
// --------------------------------------------------

// --------------------------------------------------
// Block padding
// --------------------------------------------------
@block-padding-ver: 4rem;
@block-padding-hoz: 1.25rem; //0.5rem; // ~20px is the margin you want for a mobile device.

@block-bg-image-min-height: 110vh; // Used on blocks + the header if they have background images.

// --------------------------------------------------

// --------------------------------------------------
// Notify padding
// --------------------------------------------------
@notify-padding-ver: @block-padding-ver;
@notify-padding-hoz: @block-padding-hoz;

@notify-content-padding: @item-padding * 2; // New.

// --------------------------------------------------

// --------------------------------------------------
// Drawer container
// --------------------------------------------------
@drawer-width: 22rem;
// --------------------------------------------------


// Customs
// =======

@perspective: 64rem;

// Codebox
// --------------------------------------------------
@codebox-console-width: 20%; // Used for min-width of console (when next to a codebox). If less wide than this, it will wrap to a new row.

._figure-inner(@largeMulti;){
    
    // New: the "gap" property removes the need for negative margins (which were causing us a LOT of problems).
    // Note: this will NOT work in Desktop Bookshelf.
    //gap: @figure-padding-hoz*@largeMulti;
    
    // Large view
    @media (min-width: @device-width-medium) {

        //row-gap: @figure-padding-hoz*@largeMulti*2;

        display: flex;
        justify-content: space-evenly;
        align-items: stretch;

        // FIXME: These negative margins cause us many problems.
        // However, removing them and using the "gap" property results in "sizing" not being calculated properly,
        // so unfortunately we have to stick with it for a while.
        // May be possible to fix if the parent figure actively knows how many children it contains.
        margin: /*-@figure-padding-ver*@largeMulti*/ 0 -@figure-padding-hoz*@largeMulti;

        // Wrap items if necessary.
        flex-wrap: wrap;

        // adapt-epub support
        // -----------------------------------------
        // MSEdge v18 (which Desktop Bookshelf uses) doesn't support justify-content: space-evenly.
        // This is the fallback. 
        .desktop-bookshelf & {
            justify-content: space-around;
        }
    }
}

// Apply to figure items to add necessary classes.
// Edit: with the new "gap" property added for flexboxes, this is only necessary to do for Desktop Bookshelf now!
._figure-item-spacing(@largeMulti; @smallMulti; @verMulti; @useMargin){
    
    //.desktop-bookshelf & {
        // Large view
        @media (min-width: @device-width-medium) {
            & when (@useMargin = false) {
                padding: @figure-padding-ver*@largeMulti @figure-padding-hoz*@largeMulti;
            }
            & when (@useMargin = true) {
                margin: @figure-padding-ver*@largeMulti @figure-padding-hoz*@largeMulti;
            }
        }

        // Small view
        @media (max-width: @device-width-medium) {
            
            & when (@useMargin = false) {
                &:not(:last-child) {
                    padding-bottom: @figure-padding-ver * @smallMulti * @verMulti;
                }
            }
            & when (@useMargin = true) {
                &:not(:last-child) {
                    margin-bottom: @figure-padding-ver * @smallMulti * @verMulti;
                }
            }
        }
    //}
}
.figure-inner(){
    ._figure-inner(1);
}
.figure-inner-half(){
    ._figure-inner(0.5);
}
.figure-item-spacing(@useMargin: false){
    ._figure-item-spacing(1; 1; 2; @useMargin);
}
.figure-item-spacing-half(@useMargin: false){
    ._figure-item-spacing(0.5; 1; 1; @useMargin);
}