.finder {

  // Container
  // ------------------------------
  &__items-container {
    display: grid;

    // grid-template-columns value set with JavaScript to the size of the largest item (at this time).
    // FIXME: 12rem is a temporary value. 
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
    
    // Grid gaps not used on purpose (if the items use padding for spacing, they'll be easier to click).
    /*grid-column-gap: @item-padding;
    grid-row-gap: @item-padding;*/
  }

  // Items
  // ------------------------------
  &__item {

    font-size: @btn-size;

    padding: @item-padding;
    text-align: left;

    &:not(.is-disabled) {
      cursor: pointer;
    }
  }

  &__item.is-active {
    //transform: scale(1);
    //font-weight: bold; //@font-weight-bold;
  }

  &__item-label {
    display: flex;
    align-items: center;

    cursor: inherit;
  }

  &__item-content {
    margin-left: @item-margin;
  }

  // Disabled
  // ----------------------------
  // Fade out inactive items once the question has been fully completed.
  &__item.is-disabled:not(.is-active) {
    //opacity: @disabled-opacity;
  }

  // Item icons
  // --------------------------------------------------

  // Hide the actual input.
  &__item-input {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    z-index: -1;
    .transform(translate(-50%, -50%));
  }

  &__item-state {
    flex-shrink: 0;
  }
  
  &__item-answer-icon.is-radio .icon {
    .icon-radio-unchecked;
  }

  &__item-answer-icon.is-checkbox .icon {
    .icon-checkbox-unchecked;
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-radio .icon {
      .icon-radio-checked;
    }
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-checkbox .icon {
      .icon-checkbox-checked;
    }
  }

  &__item-correct-icon .icon {
    .icon-tick;
  }

  &__item-incorrect-icon .icon {
    .icon-cross;
  }
}
