// ImgFinder Outlines
// -----------------------------------------------------------------

@imgfinder-outline-img-size: 100%;

.imgfinder-outline {
  position: absolute !important; // MUST be marked as !important (jQuery-ui sets it to 'relative' inline).

  filter: drop-shadow(1px 2px 2px rgba(0, 0, 0, 0.65)); // Help it stand out against the background.

  // These will be overridden immediately by JavaScript.
  width: 100px;
  height: 100px;

  .transition(filter @duration ease;);

  &__inner {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  /* IMPORTANT: If the animation isn't aligning, make sure you've set preserveAspectRatio="none" on the SVG! */
  // TODO: The image needs to be preloaded.
  &__img {
    position: relative;
    height: 100%;
    width: @imgfinder-outline-img-size*8;
    max-width: none;
    max-height: none;

    animation: anim_outline-draw 150ms steps(7) 1 forwards;
  }

  @keyframes anim_outline-draw {
    from { left: 0%; }
    to { left: -700%; }
  }

  @keyframes anim_outline-remove {
    from { transform: scale(1); opacity: 1; }
    to { transform: scale(1.25); opacity: 0; }
  }

  // Dragging
  // -----------------------------------
  &__inner {
    transform: scale(1);
    .transition(transform @duration ease, filter @duration ease;);
  }
  &.is-mousedown &__inner,
  &.is-dragging &__inner {
    transform: scale(1.1);
  }

  // Removed
  // -----------------------------------
  &.is-removed {
    pointer-events: none !important; // Prevent all interaction once the removal animation is triggered.
    animation: anim_outline-remove 250ms ease 1 forwards;
  }

  // Incorrect
  // ------------------------------------
  // Turn red when incorrectly placed.
  &.is-incorrect {
    
    .imgfinder-outline__inner {
      filter: hue-rotate(-45deg);
    }
  }

  // Correct
  // -----------------------------------
  &.is-correct {
    
    // When the outline is correct, it may be moved by the JavaScript to better overlay its _item.
    // Have that use a smooth CSS transition.

    .transition(left @duration ease, top @duration ease;);

    .imgfinder-outline__inner {
      filter: hue-rotate(45deg);
    }
  }

  z-index: 1; // Have the outlines appear over locked outlines.
  &.is-locked {
    z-index: 0;
  }


  // Debug
  .mode-debug & {
    &::after {
      content: "";
      position: absolute;
      background: rgba(128, 0, 128, 0.7);

      left: calc(50% ~"-" 2px);
      top: calc(50% ~"-" 2px);

      width: 4px;
      height: 4px;

      transform: rotate(45deg);
    }
  }
}
