.textinputplus {
  
  @textinputplus-textbox-color-inverted: @font-color-inverted; // Background color.

  &__item {

    //display: flex;

    padding-top: @item-padding;
    padding-bottom: @item-padding;

    &:not(:first-child) {
      .question-item-divider();
    }

    display: flex;
  }

  // Prefix / suffix
  // ------------------------------------
  &__item-prefix-container {
    margin-right: @item-padding * 2;
  }

  // Input
  // ------------------------------------
  &__item-textbox-container {
    .inputfield__input,
    .inputfield__placeholder {
      padding: @inputfield-padding;
    }
  }

  &__item-textbox {

    padding: @item-padding; // (@icon-size + (@item-padding * 2)) @item-padding @item-padding;
    line-height: @body-line-height;
    border: @btn-color @border-width solid;
    background-color: @textinputplus-textbox-color-inverted;

    .transition(border-color @duration ease, background-color @duration ease;);

    &::placeholder {
      font-style: italic;
    }

    // Turn red when containing an incorrect answer (that's already been checked).
    /*.is-incorrect & {
      border-color: @validation-error;
      background-color: mix(@textinputplus-textbox-color-inverted, @validation-error, 75%);
    }*/

    /*.dir-rtl & {
      padding-left: @icon-size; // + (@item-padding * 2);
      padding-right: @item-padding;
    }*/
  }

  // Input overlays
  // --------------------------------------
  // Contain the cross and tick for useability + the reveal answers icon.
  &__item-state {
    position: absolute;
    top: 50%;
    right: @item-padding;
    .transform(translateY(-50%));

    .dir-rtl & {
      right: inherit;
      left: @item-padding;
    }
  }

  // Small devices
  // --------------------------------

  // Stack prefix, input, suffix vertically on phones.
  @media (max-width: @device-width-small) {
    
    &__item-prefix-container {
      margin-right: 0;
      margin-bottom: @item-padding;
    }

    &__item {
      flex-direction: column;
      align-items: stretch;
    }

    &__item-textbox-container {
      flex-basis: auto !important;
    }
  }
}