// (DD) Lightbox icon (for figures and imgs)
// ---------------------------------------------

.watermark(@size: @watermark-icon-size;) {
  position: absolute;
  top: @watermark-icon-margin;
  right: @watermark-icon-margin;
  z-index: 1;

  mix-blend-mode: difference;
  filter: drop-shadow(1px 1px 0px lighten(@black, 20%));
  color: lighten(@black, 5%);
  opacity: 0.9;

  .transition(all @duration ease);

  // Resize the icons within.
  &::before,
  .icon::before {
    font-size: @size;
  }
}

.watermark-hover() {
  filter: drop-shadow(1px 1px 0px lighten(@black, 20%));
  color: lighten(@black, 10%);
}


// Legacy
// ---------------------------------------------
.lightbox-icon {
  .watermark();
}