// GraphicLabel
// ---------------------------------------
// TODO: While an item is active, fade out all other outlines (in case they overlap).

@glabel-label-margin:     @item-margin*1.25;
@glabel-active-duration:  @duration * 2.5;

.glabel {
  
  // Ensure that the .glabel__item-container element is only as wide as the image inside of it.
  &__widget {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  // Items
  // -------------------------------------
  &__item-container {
    position: relative;
  }

  &__item {
    position: absolute;

    // Placeholder values in case of error. Will be overridden by component properties.
    width: 4rem;
    height: 4rem;

    // Items fade in and out as other items become active.
    .transition(opacity @glabel-active-duration ease);
  }

  // Btn-colored outline that will show the user which area of the graphic we want them to label.
  // TODO: Add support for non-regular outlines.
  &__item-outline {
    position: absolute;
    border: 2px solid @btn-color;

    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    cursor: pointer; // The user can click on the outline or the button to activate the item.
  }

  // Active
  // -----------------------------------------------------------------

  // While there's an active item, fade out all other (non-active) items.
  &__item-container.has-active-item &__item:not(.is-active) {
    opacity: 0;
  }

  // Hide the item while it's focused (the popup has an identical copy over the top of it).
  &__item.is-focused {
    .u-visibility-hidden;
  }

  // Item label
  // -------------------------------------
  &__item-tail {
    position: absolute;
  }

  &__item-label {
    position: absolute;
  }
  
  // Alignment (direction = analogue clock hands).
  &__item-label.align-dir-12 {
    left: 50%;
    transform: translateX(-50%);
    bottom: 100%;
    margin-bottom: @glabel-label-margin;

    .glabel__item-tail {
        
      height: @glabel-label-margin;
      width: 2px;
      
      left: 50%;
      top: 100%;
      
      transform: translateX(-50%);
      background-color: @btn-color;
    }
  }
  
  // Present the text in the label in a circle.
  &__item-tooltip {

    .u-no-select;
    
    padding: @item-padding;
    text-align: center;

    border-radius: 50%;
    height: @btn-circle-size;
    width: @btn-circle-size;

    background: @btn-color;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  // Key
  // -------------------------------------------
  &__key {
    //display: flex;
    //flex-wrap: wrap;
    padding: @item-padding 0;
  }
  &__option {
    .item-title();
    font-weight: normal;
    padding: @item-padding;
  }
  
  &__option-key {
    display: inline-block;
    font-weight: bold;
  }
  
  &__option-body {
    display: inline-block;
  }

  // Correct
  // -------------------------------------------
  &__item.is-correct &__item-outline {
    color: @validation-success;
  }
  
  // Incorrect
  // -------------------------------------------
  &__item-incorrect-icon {
    display: none;
  }
  &__item.is-incorrect .js-glabel-item-tooltip {
    display: none;
  }
  &__item.is-incorrect &__item-incorrect-icon {
    display: block;
  }
}
