// MediaPlus
// ------------------------------

@mejs-time-rail-divider-width: 0.125rem;

.mediaplus {

	// Chapters
	// --------------------------------------
    &__chapters {
		background: @item-color;
		padding: @item-padding;
	}

	&__chapters-item-inner {
		display: flex;
	}

	&__chapters-item-time {
		white-space: nowrap;
		flex: 0 1 auto;
	}

	.mejs-time-rail {
		position: relative;
	}

	.mejs-time-rail-divider {
		position: absolute !important;
		top: 0 !important;
		bottom: 0 !important;
		height: auto !important;

		margin-left: -(@mejs-time-rail-divider-width/2) !important;
		width: @mejs-time-rail-divider-width !important;

		background: @mejs-color;
		opacity: 0.8;

		pointer-events: none; // Purely decorative.
	}
}
