// PDF mode
// ====================================
// These styles will only apply when the content is built using "grunt build-pdf" / "grunt dev-pdf".

.mode-pdf {

  // Hide menu and navigation
  // ------------------------
  .sidemenu,
  .nav {
      display: none !important;
  }
  .component__container,
  .page__header-inner > * {
      margin-left: 0 !important; // Remove the adjustment for the sidenav.
  }
  #wrapper {
      padding-top: 0 !important;
  }
  #app {
      border-top: none !important;
  }
  .page__header.has-bg-image {
      background-position-y: 0 !important;
  }

  // Disabled background-attachment: fixed
  // -------------------------------------
  * {
    background-attachment: local !important;
  }
  
  // Page breaks
  // -------------------------------------
  // All elements which shouldn't be split across pages (if possible).

  // VERY IMPORTANT!
  // page-break-* properties only work if all parent elements are display: block;
  .component__container {
    display: block;
  }
  .block {
    //page-break-after: always;  // Have blocks start at the start of the PDF page.
    page-break-inside: avoid;  // Try to avoid breaking blocks across pagebreaks if possible.

    //-webkit-box-decoration-break: clone;
    //box-decoration-break: clone;
  }
  // Try not to split up componentrows if it can be helped.
  .componentrow {
    display: block;

    &.componentrow-is-double {
      page-break-inside: avoid;
    }
  }
  
  // Hide debug elements when printing
  // -------------------------------------
  @media print{
    .debug-block-id {
      display: none !important;
    }
  }
}