// component
// ============

.mode-pdf .component {

	// Don't split titles across pages,
	// and don't seperate the title from its following paragraph.
	&__title {
		page-break-inside: avoid;
		.avoid-break-after();
	}

	// Don't split the header instruction text and what it refers to.
	&__instruction {
		page-break-inside: avoid;
		.avoid-break-after();
	}
}