// GraphicLabel Popup
// --------------------------------------------------------

@glabel-popup-min-width: @device-width-small / 2;

.glabel-popup {

  // Content
  // ------------------------------------------

  // Prevent the content going too narrow.
  &__content {
    min-width: @glabel-popup-min-width;
  }

  .glabel__item-tooltip {
    &::after {
      content: none;
    }
  }

  // If the screen is wide enough, display the content items in two columns.
  /*&__content-inner {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  }*/

  // Items
  // ------------------------------------------
  .mcq-item();

  &__item-label {
    display: flex;
    align-items: center;
    padding: @item-padding/2 @item-padding;
    margin-bottom: 0;
  }

  &__item-state {
    position: relative;
    flex-basis: 0;
    margin-right: @item-padding;
  }

  // Show X icons instead of key when incorrect
  // ------------------------------------------
  // FIXME: Very crude. These radio/checkbox buttons should be made into helpers given how much we use them.
  &__item-label.is-incorrect {
    
    .glabel-popup__item-key-icon {
      display: none;
    }

    .glabel-popup__item-incorrect-icon {
      display: block;
      color: @validation-error;
    }
  }

  &__item-label.is-selected.is-incorrect {
    .glabel-popup__item-incorrect-icon {
      color: @white;
    }
  }
}