.media {

  // Volume handle
  // -----------------------------------------
  .mejs-volume-handle {
    cursor: pointer !important;
  }

  // Transcript
  // -----------------------------------------
  &__transcript-container {
    margin-top: @item-margin;
    .drop-shadow-item();
  }

  &__transcript-btn {
    padding: @item-padding;
    width: 100%;

    font-weight: @btn-weight;
    font-family: @btn-family;
    background-color: @item-btn-color;
    color: @item-btn-color-inverted;

    text-align: left;
    
    border-radius: @item-border-radius;

    .no-touch &:not(.is-selected):hover {
      background-color: @item-color-hover;
      color: @item-color-inverted-hover;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }

    // Selected
    // --------------------------------------
    &.is-selected {
      background-color: @item-color-selected;
      color: @item-color-inverted-selected;
    }
  }
  
  // Content
  // --------------------------------------
  &__transcript-body-inline {
    padding: @item-padding;

    margin-top: -@item-border-radius;
    padding-top: calc(@item-padding ~"+" @item-border-radius);

    background-color: @item-color;
    color: @item-color-inverted;

    border-bottom-left-radius: @item-border-radius;
    border-bottom-right-radius: @item-border-radius;
  }

  &__transcript-body-inline a {
    .link-text;
  }
}
