// Narrative--pdf
//--------------------
// Stack the reveals one above the other.

.mode-pdf .narrativeplus {
  
  &__slider {
    display: block;

    background-color: @item-color;
  }

  &__slider-item:not(:first-child) {
    position: relative;
    padding-top: @block-padding-ver/2;
    margin-top: @block-padding-ver/2;

    &::before {
        content: "";
        position: absolute;
        left: @block-padding-hoz/2;
        right: @block-padding-hoz/2;
        top: 0;
        height: 1px;
        background: currentColor;
        opacity: 0.2;
    }
  }

  // Hide the controls entirely.
  &__controls-container {
    display: none;
  }
}