// MatchingPlus
// ---------------------------------------

@matchingplus-option-color: @font-color-inverted;
@matchingplus-option-color-inverted: @font-color;
@matchingplus-option-color-hover: @item-color; //darken(@matchingplus-option-color, 5%);

.matchingplus {

    &__item {
        .question-item-divider();
        padding: @item-padding 0;
    }

    &__item-select {
        .drop-shadow-btn();
    }

    // z-index fix. The filter drop-shadow causes the dropdown to not be in front of everything. This fixes it.
    &__item.is-active &__item-select {
        z-index: 1;
    }

    // Select + Dropdown
    // ----------------------------
    &__select,
    &__dropdown {
        background-color: @btn-color-light;
        border: @border-width @btn-color solid;

        &.is-disabled {
            color: fade(@font-color, 80%);
        }
    }

    // Placeholder text.
    &__select,
    &__dropdown-trigger {

        .transition(background @duration ease);
        background: @btn-color-light;

        &:not(.is-visited) {
            color: fade(@font-color, 80%);
            font-style: italic;
            background: @matchingplus-option-color;
        }
    }

    // Select
    // ----------------------------
    &__select[disabled] {
        opacity: 1;

        // Hide the dropdown arrow when disabled.
        /*-moz-appearance:none;       // Firefox
        -webkit-appearance:none;    // Safari and Chrome
        appearance:none;*/
    }
    &__select-option {
        font-style: normal;
        color: @matchingplus-option-color-inverted;
        background-color: @matchingplus-option-color;

        &.is-correct {
            background: mix(@validation-success-light, @matchingplus-option-color-hover, 50%);
        }

        &.is-incorrect,
        &.is-eliminated {
            background: mix(@validation-error-light, @matchingplus-option-color-hover, 50%);

            // \/ Doesn't work consistently.
            /*&:active,
            &:checked,
            &:focus {
                //background: darken(@validation-error-light, 7%);
                background: linear-gradient(darken(@validation-error-light, 7%), darken(@validation-error-light, 7%));
            }*/
        }
    }
    &__select-option.is-disabled {
        font-style: italic;
    }

    // Dropdown
    // ----------------------------
    &__dropdown {
        //overflow: hidden;
        border-width: 0;
    }

    &__dropdown.is-active &__dropdown-trigger {
        //border-bottom: @border-width @btn-color solid;
        //border-bottom-color: inherit;
        //.drop-shadow-btn();
    }

    &__dropdown-trigger {
        border: @border-width @btn-color solid;
        border-color: inherit;
    }

    &__dropdown-options {
        top: calc(100% ~"-" @border-width);
        border: @border-width @btn-color solid;
        border-color: inherit;

        // Add a slight shadow at the top.
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 0.25rem;
            pointer-events: none;
            
            background-image: linear-gradient(rgba(0, 0, 0, 0.2), transparent);
        }
    }

    &__dropdown-option {
        color: @matchingplus-option-color-inverted;
        background-color: @matchingplus-option-color;

        .no-touch &:hover {
            background: @matchingplus-option-color-hover;
        }

        .no-touch &.is-correct:hover {
            background: mix(@validation-success-light, @matchingplus-option-color-hover, 50%);
        }
        .no-touch &.is-incorrect:hover,
        .no-touch &.is-eliminated:hover {
            background: mix(@validation-error-light, @matchingplus-option-color-hover, 50%);
        }

        &.is-selected {
            color: @matchingplus-option-color-inverted;
            background: @visited;

            .no-touch &:hover {
                background: darken(@visited, 10%);
            }

            &.is-correct {
                background: darken(@validation-success-light, 7%);
                .no-touch &:hover {
                    background: mix(darken(@validation-success-light, 7%), @validation-success, 90%);
                }
            }
            &.is-incorrect,
            &.is-eliminated {
                background: darken(@validation-error-light, 7%);
                .no-touch &:hover {
                    background: mix(darken(@validation-error-light, 7%), @validation-error, 90%);
                }
            }
        }
    }

    // Item states
    // ---------------------------------------------
    &__item.is-correct {
        .matchingplus__select {
            border-color: @validation-success;
            background: @validation-success-light;
        }

        .matchingplus__dropdown {
            border-color: @validation-success;
            .matchingplus__dropdown-trigger {
                background: @validation-success-light;
            }
        }
    }

    &__item.is-incorrect {
        .matchingplus__select {
            border-color: @validation-error;
            background: @validation-error-light;
        }

        .matchingplus__dropdown {
            border-color: @validation-error;
            .matchingplus__dropdown-trigger {
                background: @validation-error-light;
            }
        }
    }
}
