@block-has-bg-image-padding: 65vh;

.block {

  overflow: hidden;
  background-color: @white;

  &.has-bg-image {
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;

    // Center the content vertically.
    display: flex;
    align-items: center;

    // Ensure the block takes up the entire view.
    min-height: @block-bg-image-min-height;
    //background-size: auto 100vh;
    background-size: cover;

    // On larger devices, ensure that the background image covers everything.
    /*@media (min-width: @device-width-large) {
      background-size: cover;
    }*/

    // On smaller devices, ensure that the background image can be mostly seen before the block inner scrolls into view.    
    @media (max-width: @device-width-medium) {
      background-size: cover;
      padding-top: @block-has-bg-image-padding;
      padding-bottom: @block-has-bg-image-padding;
    }

    // On mobile devices, contract the image further.
    @media (max-width: @device-width-small) {
      padding-top: round(@block-has-bg-image-padding / 5);
      padding-bottom: round(@block-has-bg-image-padding / 5);
    }
  }

  &__inner {
    .l-container-padding(@block-padding-ver, @block-padding-hoz);
  }

  &__title {
    margin-bottom: @block-title-margin;
    .block-title;
    color: @heading-color;
  }

  &__body {
    margin-bottom: @block-body-margin;
  }

  &__instruction {
    margin-bottom: @block-instruction-margin;
    .instruction-text;
  }

  &__body a,
  &__instruction a {
    .link-text;
  }

  // Divider
  border-top: 1px solid mix(@font-color, @white, 15%);

  // Fix rare bug where BlockBackgrounds can be seen through the divider in EPUB.
  .mode-epub & {
    margin-top: -1px;
  }
}
