.mmcq {

  // Table
  // --------------------------------
  &__widget {
    display: table;
    table-layout: fixed; // Important. Prevents the rows overflowing the container.
    width: 100%;
  }

  // Rows
  // --------------------------------
  &__items-header,
  &__item {
    display: table-row;
  }

  // Cells
  // --------------------------------
  &__items-header-spacer,
  &__items-header-label,
  &__item-text,
  &__item-input-container {
    display: table-cell;
    vertical-align: middle;

    padding-top: @item-padding;
    padding-bottom: @item-padding;
  }

  // Ensure the leftmost column takes up as much space as it can.
  /*&__items-header-spacer {
    //width: 100%;
  }*/

  &__item-text {
    padding-right: @item-padding*2;
  }

  &__items-header-label {
    padding: @item-padding;
    text-align: center;
    .item-title();

    box-sizing: content-box;
  }

  &__items-header-label-inner {
    display: inline-block;
    margin: auto;
  }

  // Items
  // --------------------------------------------------
  &__item {
    position: relative;
    width: 100%; // Bugfix: needs to be specified for Offline Bookshelf.
  }

  &__item-input {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    z-index: -1;
    .transform(translate(-50%, -50%));
  }

  &__item-label {
    display: block;
    z-index: 1;
  }

  &__item-label:not(.is-disabled) {
    cursor: pointer;
  }

  &__item-icon {
    display: none;
  }

  // Item icons
  // --------------------------------------------------
  &__item-answer-icon.is-radio .icon {
    .icon-radio-unchecked;
  }

  &__item-answer-icon.is-checkbox .icon {
    .icon-checkbox-unchecked;
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-radio .icon {
      .icon-radio-checked;
    }
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-checkbox .icon {
      .icon-checkbox-checked;
    }
  }

  &__item-correct-icon .icon {
    .icon-tick;
  }

  &__item-incorrect-icon .icon {
    .icon-cross;
  }

  // Small device
  // -------------------------------------
  // Align the radio buttons underneath the label.
  
  // Hidden on large devices.
  &__item-inputs {
    display: contents; // FIXME: Won't work in Offline Bookshelf (old versions of Edge).
  }
  &__item-label-text {
    display: none;
    overflow: hidden; // Prevent going outside the page on mobiles.
  }

  .uniqueOptions() {
    // Table
    // --------------------------------
    .mmcq__widget {
      display: block;
    }

    // Rows
    // --------------------------------
    .mmcq__items-header,
    .mmcq__item {
      display: block;
    }

    // Cells
    // --------------------------------
    .mmcq__item-text,
    .mmcq__item-input-container {
      display: block;
    }

    .mmcq__item-text {
      margin-bottom: @item-margin*2;
    }

    // Hide the header
    .mmcq__items-header {
      display: none;
    }

    .mmcq__item {
      padding-top: @item-padding;
      padding-bottom: @item-padding;

      // Try to combine the options into a grid (if there's enough room).
      display: grid;

      grid-template-columns: repeat(auto-fit, minmax((@device-width-small/2.5), 1fr));
      grid-column-gap: @item-padding*2;

      // Custom column counts
      &.options-columns-2 {
        grid-template-columns: repeat(auto-fit, minmax((@device-width-small/2), 1fr));
      }
      &.options-columns-1 {
        grid-template-columns: 100%;
      }
    }

    .mmcq__item-text {
      grid-column: ~"1/-1"; // Span all the columns that the grid template makes.
      padding: 0;
    }

    // REMOVED DUE TO INCOMPATIBILITY WITH DESKTOP BOOKSHELF.
    /*.mmcq__item-inputs {
      margin-top: @item-body-margin;
      padding: @item-margin 0;

      display: grid;

      // Experimental: display the item options in two columns to save on space.
      grid-template-columns: repeat(auto-fit, minmax(@device-width-medium/2, 1fr));
      grid-column-gap: @item-margin;
      grid-row-gap: @item-margin;
      align-items: center;
    }*/

    .mmcq__item-input-container {
      margin-top: @item-margin;
    }

    .mmcq__item-text {
      padding-right: 0;
    }

    // Inputs + labels
    // ------------------------------
    .mmcq__item-input-container {
      padding: 0;
      //margin: @item-margin 0;
    }

    .mmcq__item-label {
      display: flex;
      align-items: center;
    }

    .mmcq__item-label-text {
      display: block;
      margin-left: @item-margin;
    }
  }

  // Force unique options (each items has its own options buttons) on small devices.
  @media (max-width: @device-width-medium) {
    .uniqueOptions();
  }

  // Allow for uniqueOptions() to be used at all times (when "_combineOptions": false).
  .has-unique-options {
    .uniqueOptions();
  }




  




  

  

  // FIXME: Legacy (possibly defunct) \/

  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the selection icons until the component is complete
  // --------------------------------------------------
  &__widget:not(.is-complete) {
    .mmcq__item-answer-icon {
      display: block;
    }
  }
  // --------------------------------------------------

  // Show the user's selection
  // --------------------------------------------------
  &__widget.show-user-answer .mmcq__item-label.is-selected {
    &.is-correct .mmcq__item-correct-icon,
    &.is-incorrect .mmcq__item-incorrect-icon {
      display: block;
    }
  }

  &__widget.show-user-answer {
    .mmcq__item-label:not(.is-correct):not(.is-incorrect).is-selected .mmcq__item-answer-icon {
      display: block;
    }
  }

  /*&__widget.show-user-answer .mmcq__item-label {
    &.is-correct {
      color: @validation-success;
    }
    &.is-incorrect {
      color: @validation-error;
    }
  }*/

  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the correct answers
  // --------------------------------------------------
  &__widget.show-correct-answer {
    .is-correct .is-selected .mmcq__item-correct-icon,
    .is-incorrect .is-selected .mmcq__item-correct-icon {
      display: block;
    }
  }

  &__widget.show-correct-answer {
    .mmcq__item:not(.is-correct):not(.is-incorrect) .is-selected .mmcq__item-answer-icon {
      display: block;
    }
  }
  // --------------------------------------------------
}
