// Lottie
// ---------------------------------
// (Styles for the wrapper element).

.lottie {
    position: relative;
    display: block;

    // Improve animation sharpness in Chrome (doesn't work).
    svg {
        //image-rendering: high-quality;
        //image-resolution: 300dpi;
        //image-rendering: auto;

        transform: none !important; // Reduces blur in Chrome.
        will-change: auto;          // Note: won't work in EPUB mode.
        .mode-epub & {
            transform: translate3d(0,0,0) !important;
        }
    }

    // When loaded, hide the loading icon.
    &.is-ready &__loading {
        display: none;
    }

    // Lottie plugin container
    // ----------------------------
    &__container {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
    }

    // Controls
    // ----------------------------
    &__controls {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &__controls-inner {
        width: 100%;
        height: 100%;
    }

    // Clicking anywhere on the animation should pause it.
    &__controls-playpause {

        display: block;
        height: 100%;
        width: 100%;
    }

    // Basic controls
    // --------------

    // Icon
    &__controls-playpause-watermark {
        position: absolute;
        top: @item-padding;
        right: @item-padding;

        .transition(opacity @duration ease);
    }

    // Only show the watermark if the animation is complete.
    &:not(.is-complete) &__controls-playpause-watermark {
        opacity: 0;
    }


    // Decorative animation mode
    // -------------------------
    // In this mode, the animation has a reduced level of control.

    // Change cursor to pointer only if the video is paused 
    &.is-decorative.is-paused &__controls-playpause {
        cursor: pointer;
    }


    // Fallback
    // ----------------------------
    // If this element isn't explicitly given any size, at least have it visible.
    min-height: @dd-loading-icon-size;
    min-width: @dd-loading-icon-size;
}