// SideNav progress bar
// -----------------------------

// FIXME: LESSCSS rushed due to time constraints.

.sidenav__progress {

    flex-grow: 1;
    margin-bottom: @item-padding;
    cursor: pointer;

    &-inner {
        display: block;
        width: 100%;
        height: 100%;
    }

    &-indicator {
        display: block;
        height: 100%;
        width: 50%;
        margin: auto;
        border-radius: @sidenav-width;
        background-color: rgba(16,19,58,0.12);
        .transition(background-color @duration ease);

        .no-touch &:hover {
            background-color: rgba(16, 19, 58, 0.253);
        }

        padding: @item-padding/2 0;
    }

    &-indicator-inner {
        position: relative;

        display: block;
        background-color: @drawer;

        width: @sidenav-bar-width;
        height: 100%;

        margin: auto;

        border-radius: @sidenav-width;
        overflow: hidden;
    }

    &-indicator-bar {
        width: @sidenav-bar-width;
        margin: auto;
        height: 0;
        
        .transition(height ease @duration);

        border-bottom-left-radius: @sidenav-width;
        border-bottom-right-radius: @sidenav-width;
        background-color: lighten(@validation-success, 20%);
    }
}
