// adapt-dd-math
// -----------------------------------------

.figure__item > h3:first-child {
  margin-top: 0.25rem;
}

.m-math {

  // FIGURE STYLES
	// ----------------------------------------------------------------
	// Increase size inside of figures.
	.figure & {
		font-size: @body-size*1.8;
    @media (max-width: @device-width-small) {font-size: @body-size*1.4;}

    // Slightly reduce the size of {{#m-text}} in this context.
    .m-math__text {
      font-size: 0.9em;
    }
	}

  // Display as a normal block if the main child of a figure item.
  .figure__item-inner > & {
    display: block;

    & + .m-math {
      margin-top: @item-margin / 2;
    }
  }

  // Don't increase size in these contexts.
  .figure table &,
  .figure .rgraph &,
  .figure p &,
  .figure li & {
    font-size: @math-size;
  }

  // Annotation highlights
  // ---------------------------------------
  .annotation {
    display: inline-block;
    padding: @item-margin;
    margin: 0 @item-margin/2;
    position: relative;
    cursor: pointer;

    .u-no-select;

    &::before {
      position: absolute;
      content: "";
      top: @item-margin;
      bottom: @item-margin;
      left: 0;
      right: 0;
      border: @border-width solid @annotation-color;
    }

    &,
    .no-touch &:hover {
      color: @font-color;
    }
  }

  // Nested input fields
  // ---------------------------------------
  
  .inputfield__input {
    text-align: center;

    // Reduce padding.
    padding: @item-padding/2;
  }

  .inputfield__placeholder {
    // Reduce padding.
    padding: @item-padding/2;

    // Increase padding again if it contains an {{#m-text}} element.
    .m-math__text {
      padding: 0 @item-padding/2;
      line-height: 1em;
    }
  }

  // Increase margins of the input field when it's inside a fraction.
  &__num .inputfield {
    margin-bottom: 0.25em;
  }
  &__den .inputfield,
  &__den &__sqrt {
    margin-top: 0.25em;
  }

  // Increase margins of the input field when it's inside a sqrt.
  &__sqrt .inputfield {
    margin-top: 0.25em;
  }

  // Move the hint icon to be more in the corner
  .inputfield__overlay {
    top: 0;
    right: @item-padding/8;
  }

  // Other
  // ---------------------------------------

  // Slightly increase size of .is-dot-above diacritic characters (the default font size makes them hard to see).
  &__diac.is-dot-above .m-math__diac-fallback {
    font-size: 1.1em;
    margin-top: -0.05em;
  }
}

// Inline math styles (similar to how inline <code> styles appear).
// FIXME: Hardcoded to BU11 standards.
p > .is-math,
p > nobr > .is-math,
li > .is-math,
li > nobr > .is-math {
  
  & > .m-math__inner {
    .code();
    font-family: @math-family;
  }


  // Prevent inline math orphaned from non-math characters that are directly before/after it.
	// Example: (x), "x"
  // -----------------------------------------
  display: inline; // The joiner only works if display is inline.
  & > .m-math__inner {
    display: inline-block;
  }
  &::before,
  &::after {
    content: "\2060"; // Word-joiner (&#8288;). Prevents a break after the tooltip.
    white-space: nowrap;
  }
  // -----------------------------------------


  // If inside of the static column of a stickyblock, darken it slightly.
  .stickyblock .is-static & > .m-math__inner {
    background: darken(@codebox-color, 4.5%);
  }
}