// ddLoadingIcon
// ----------------------------------------------------

.ddloading-icon {
	display: block;
	width: @dd-loading-icon-size;
	height: @dd-loading-icon-size;

	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transform-origin: center;

	// Ensure that the icon is visible against most backgrounds.
	filter: brightness(50%);
	opacity: 0.75;

	.ddloading__graphic {
		position: relative;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		margin-left: 0;
		margin-top: 0;

		pointer-events: none;
	}
}