// FreeThinking
// ------------------------------------------

// Add a custom background for FreeThinking templates.
.block.is-freethinking {
  background: @question-color;
}

.freethinking {

  // Stylize TextArea
  // TODO: Make the text area styles global.
  &__content-textarea {

    font-size: @body-size;
    line-height: @body-line-height;
    padding: @item-padding;
    font-family: @body-family;
    resize: none;

    border: @border-width @btn-color dashed;

    &::placeholder {
      font-style: italic;
    }

    &:focus {
      border: @border-width*2 @btn-color solid;
      margin-top: -@border-width;
      margin-bottom: -@border-width;
      padding: @item-padding calc(@item-padding ~"-" @border-width);
    }
  }
}