@blockquote-char-size: @icon-size * 1.25; //@pull-quote-title-size * 4;

@blockquote-padding-hoz: @item-padding * 2.5;
@blockquote-padding-ver: @item-padding * 1.5;

blockquote {

  position: relative;
  padding: @blockquote-padding-ver @blockquote-padding-hoz;
  background-color: @blockquote-color;
  color: @blockquote-color-inverted;
  
  margin: 0;
  &:not(:first-child) {
    margin-top: @item-margin * 2;
  }
  &:not(:last-child) {
    margin-bottom: @item-margin * 2;
  }

  .drop-shadow-half();

  .pull-quote-body();

  li & {
    margin-left: 0;
  }

  // Decorative quotes
  // -------------------------------------
  // FIXME: Needs a cleanup.

  &::after, &::before {
    //font-size: @blockquote-char-size;
    //line-height: 1em;
    //height: 2rem;

    //background-color: pink;

    position: absolute;
    
    // Copy of core icons.less
    // FIXME: Try to inherit rather than duplicating styles somehow.
    display: block;
    font-family: "vanilla";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    //font-size: @icon-size;
    font-size: @blockquote-char-size;
    text-align: center;
    text-decoration: none;

    // Better font rendering
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  &::before {
    content: "\e9b5"; // quote-close-2
    text-align: left;
    
    top: 0; //(@blockquote-padding-ver - @blockquote-char-size)/2;
    left: (@blockquote-padding-hoz - @blockquote-char-size)/2;

    //margin-top: -@item-padding/3;
    //margin-left: -@item-padding;
  }

  &::after {
    content: "\e9b4"; // quote-open-2
    text-align: right;

    //margin-bottom: -@item-padding/3;
    //margin-right: -@item-padding;

    bottom: 0; 
    right: (@blockquote-padding-hoz - @blockquote-char-size)/2;
  }
}
