// TryHtml (codemirror overrides)
// --------------------------------------
// CodeMirror syntax highlighting colours overridden here to match syntax highlighting colours of Prettify.
// (// after a line means it has been updated).

.cm-s-prettify .cm-keyword       {color: #708;}
.cm-s-prettify .cm-atom          {color: #219;}
.cm-s-prettify .cm-number        {color: #164;}
.cm-s-prettify .cm-def           {color: #00f;}
.cm-s-prettify .cm-variable,
.cm-s-prettify .cm-punctuation,
.cm-s-prettify .cm-property,
.cm-s-prettify .cm-operator {}
.cm-s-prettify .cm-variable-2    {color: #05a;}
.cm-s-prettify .cm-variable-3,
.cm-s-prettify .cm-type          {color: #085;}
.cm-s-prettify .cm-comment       {color: #6e6e6e; font-style: italic;} //
.cm-s-prettify .cm-string        {color: #080;} //
.cm-s-prettify .cm-string-2      {color: #f50;}
.cm-s-prettify .cm-meta          {color: #555;}
.cm-s-prettify .cm-qualifier     {color: #555;}
.cm-s-prettify .cm-builtin       {color: #30a;}
.cm-s-prettify .cm-bracket       {color: #997;}
.cm-s-prettify .cm-tag           {color: #008;} //
.cm-s-prettify .cm-attribute     {color: #606;} //
.cm-s-prettify .cm-hr            {color: #999;}
.cm-s-prettify .cm-link          {color: #00c;}

.cm-s-prettify .cm-error         {color: #f00;}