// DD Download
// --------------------------------

.download {

    &__anchor {
        text-decoration: none;
    }
    
    .icon::before {
        display: inline;
        vertical-align: bottom;
        //margin-right: 0.25rem;
    }
    
    &__content {
        text-decoration: underline;
    }

    // FIXME: Temp.
    &__tooltip {
        font-family: monospace;
        font-weight: bold;
        cursor: pointer;
        color: @font-color;

        margin-left: 0.2em;

        .no-touch &:hover {
            color: lighten(@font-color, 10%);
        }
    }
}