// StickyElement
// ------------------------------

.stickyelement {

	will-change: transform;
	//transform: translateZ(0);
	backface-visibility: hidden;

	z-index: 1;

	.is-se-hide {
		opacity: 0;
		pointer-events: none;
	}
	.is-se-mark {
		background-color: yellow; // Override this.
	}

	// Inactive
	/*&:not(.is-clone) [data-stickyelement-slidedown-idx] {
		max-height: 0;
		overflow: hidden;
	}
	// Active
	&:not(.is-clone) .is-se-slidedown {
		max-height: none;
	}*/

	// Clone
	&.is-clone {
		position: absolute;
		display: block;
		top: 0;
		left: 0;
	}

	// Default transform transitions.
	will-change: transform;
	.transition(transform @duration ease);
}