// Common mixins (used by multiple plugins)
// ----------------------------------------

// Generic question styles
.question-item-divider() {
  border-top: 2px groove rgba(255, 255, 255, 0.8);
}

// The mcq and mmcq templates both have items which need to use this style.
.mcq-item(@smallIcon: false){
  
  &__item-state {
    position: relative;
  }

  // Radio + Checkbox
  // ----------------------------------------------------------------
  // Modify the radio + checkbox labels to use the .btn-circle style.
  &__item-answer-icon {
    .btn-circle(@small: @smallIcon);
    display: block; // Always visible.

    // Checkbox
    &:not(.is-radio) {
      // Bugfix: in Chrome, if border radius is 0, then the checkbox outline will be slightly off-center on some devices.
      // For whatever reason, having any border-radius fixes it.
      border-radius: 0.1%;
    }

    // Hide the original icon
    .icon {
      display: none;
    }
  }
  // Hover
  // ----------------------------------------------------------------
  .no-touch &__item-label:hover &__item-answer-icon {
    .btn-circle-hover(@hoverColorEnabled: false;);
  }

  // Validation states
  // ----------------------------------------------------------------
  &__item-label.is-incorrect &__item-answer-icon {color: @validation-error;}
  .no-touch &__item-label.is-incorrect:hover &__item-answer-icon:not(.is-disabled):not(.is-locked) {color: @validation-error-hover;}
  
  &__item-label.is-correct &__item-answer-icon {color: @validation-success;}
  .no-touch &__item-label.is-correct:hover &__item-answer-icon:not(.is-disabled):not(.is-locked) {color: @validation-success-hover;}

  // Non-selected state
  // ----------------------------------------------------------------
  &__item-label:not(.is-selected) &__item-answer-icon {
    background: transparent;
  }

  // Usability
  // -----------------------------------------------------------------
  // Move the tick and cross icons to cover the .btn-circle()
  &__item-correct-icon,
  &__item-incorrect-icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    pointer-events: none; // Don't block pointer events.

    .icon {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__item-correct-icon {
    color: @validation-success-inverted;
  }

  &__item-incorrect-icon {
    color: @validation-error-inverted;
  }

  // Disabled
  // ---------------------------------------------------
  // Only disable non-selected inputs.
  &__item-label.is-disabled:not(.is-selected) &__item-answer-icon {
    .btn-circle.is-disabled;
  }

  // Disabled, deselected incorrect inputs should keep their red border (to show where the user got the answers wrong in the past).
  &__item-label.is-disabled:not(.is-selected).is-incorrect &__item-answer-icon {
    color: @validation-error-disabled;
  }
}