// Lottie
// ---------------------------------
// (Styles for the wrapper element).

.lottie {

  // Give lottie elements a border and background.
  //background-color: @lottie-color;
  //border: @border-width solid @lottie-border-color;
  overflow: hidden;

  // Circle style
  // ---------------------------------
  &.style-circle {
    border-radius: 50%;
  }
  
  // Basic controls
  // ---------------------------------
  // (Watermark in the top-right corner when the animation is complete)
  &.controls-type-basic {
    .transition(padding-top @duration*2 ease);

    // Add padding at the top once complete.
    &.is-complete {
      padding-top: @watermark-icon-size;
    }
  }

  // Only show the watermark if the animation is complete.
  &:not(.is-complete) &__controls-playpause-watermark {
    opacity: 0 !important;
  }

  // Stylize the watermark.
  &__controls-playpause-watermark {
    .watermark();
  }
  .no-touch &__controls-playpause:hover &__controls-playpause-watermark {
    transform: rotate(35deg); // Give it a slight rotation too.
    .watermark-hover();
  }

  // Full controls
  // ---------------------------------
  // TBA.
}