// MediaPlus
// -----------------------------------------------

@mejs-time-float-maxwidth: 15rem;
@mejs-time-float-padding: 0.25rem;

.media {

}

.mediaplus {


  // Chapters
  // -------------------------------------
  &__chapters {
    .drop-shadow-item();
    margin-top: @item-margin;

    columns: 2;
    column-gap: @item-padding;
    @media (max-width: @device-width-medium) {
      columns: 1;
    }
  }

  &__chapters-item {
    display: inline-block; // Prevents orphans in columns.
    width: 100%;
  }

  &__chapters-item-time {

    margin-right: @item-margin;

    a {
      .link-text();
      cursor: pointer;
      text-decoration: underline;
    }
  }

  &__chapters-item-title {
    display: inline-block;
  }

  // Float title (tooltip that appears while scrubbing)
	// --------------------------------------
  // FIXME: Very hacky implementation at the moment.
	.mejs-time-float {
		width: auto !important;
		height: auto !important;

    padding: @mejs-time-float-padding/2 @mejs-time-float-padding;

    max-width: @mejs-time-float-maxwidth;
    transform: translateX(-50%);
    margin-left: 0 !important;
    margin-top: -1rem !important;
		//margin-left: -(@mejs-time-float-width/2) !important;

		span:not(.mejs-time-float-corner) {
			width: auto !important;
      height: auto !important;
			position: relative !important;
			margin-left: auto !important;
			margin-right: auto !important;
		}
	}

  .mejs-time-float-title {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    margin-bottom: @mejs-time-float-padding;
  }

	.mejs-time-float-corner {
		left: 50% !important;
		transform: translateX(-50%) !important;
		top: 100% !important;
	}
  

  // Play button overlay
  // -------------------------------------
  // Increase its visibility (it's hidden when against a white background).
  .mejs-overlay-button {
		opacity: 0.8;
		.drop-shadow-half();
	}

  .media__transcript-btn-text {
    font-size: @btn-size;
    font-weight: @btn-weight;
    font-family: @btn-family;
  }
}
