// Reorder
// ---------------------------------------

.reorder {

  &__widget {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  &__items-container {
    position: relative;
    width: @device-width-small;
    max-width: 100%;
  }

  &__widget-header,
  &__widget-footer {
    padding: @item-padding;
    .u-no-select;
  }

  // Items
  // --------------------------------------
  &__item {

    position: relative;
    display: block;
    width: 100%;

    // Reorder items MUST NEVER have margins (they break calculations).
    // Inner items can have margins just fine however.
    padding: @item-margin/2 0;

    cursor: grab;
    .u-no-select;

    transform: scale(1);
    transform-origin: center;
    
    z-index: 0;

    // Debug.
    /*&::before {
      position: absolute;
      bottom: 0;
      right: 0;
      content: attr(data-adapt-index);
      color: red;
    }
    &::after {
      position: absolute;
      top: 0;
      right: 0;
      content: attr(data-adapt-user-index);
    }*/

    // Grabbed.
    &.is-grabbed {
      cursor: grabbing;
      z-index: 2; // The grabbed item should always be above all other items while it's dragged.
      
      .js-reorder-item-transform {
        transform: scale(1.1);
      }
    }

    // Locked.
    &.is-disabled {
      cursor: auto;

      &__item-inner {
        opacity: 0.8;
      }
    }
  }

  // Grabbing & shuffling transitions.
  .js-reorder-item-transform {
    //backface-visibility: hidden;
    position: relative;
    .transition(transform @duration/2 ease, top @duration/2 ease;);
  }

  &__item-inner {
    padding: @item-padding;
    border: 1px solid @btn-color;
  }
}
