// <kbd> tags
// ---------------------------------------------

kbd, .kbd {
  
  background-color: #f1f1f1;

  //display: inline-block;
  display: inline;
  border: 0.125em outset #d3d3d3; //#ebebeb;
  border-radius: 0.25rem;
  min-width: 1.8em;
  text-align: center;

  padding: @item-margin/4 @item-margin;
}