// GraphicLabel
// --------------------------------------------------------

@glabel-border-width: @border-width*3;
@glabel-tail-width:   @border-width*2; // Must be even.

.glabel {
  
  // Items
  // ------------------------------------------------------
  @shadow-size: 1px;

  // Outline
  &__item-outline {

    border-width: @glabel-border-width;
    border-color: @btn-color;
    border-radius: @border-radius;

    .transition(border-color @duration ease, box-shadow @duration ease;);

    // Have the outline stand out more against the graphic behind it.
    @a: lighten(@btn-color, 25%);
    box-shadow: 
      inset @a @shadow-size @shadow-size 0px,
      inset @a -@shadow-size -@shadow-size 0px,
      @a @shadow-size @shadow-size 0px,
      @a -@shadow-size @shadow-size 0px,
      @a @shadow-size -@shadow-size 0px,
      @a -@shadow-size -@shadow-size 0px;

    // Have the outline be on the outside of the item element (improves accuracy when swapping to mobile devices).
    box-sizing: content-box;
    margin-top: -@glabel-border-width;
    margin-left: -@glabel-border-width;
  }

  // Item correct
  &__item.is-correct &__item-outline {
    border-color: @validation-success;
    @a: lighten(@validation-success, 35%);
    box-shadow: 
      inset @a @shadow-size @shadow-size 0px,
      inset @a -@shadow-size -@shadow-size 0px,
      @a @shadow-size @shadow-size 0px,
      @a -@shadow-size @shadow-size 0px,
      @a @shadow-size -@shadow-size 0px,
      @a -@shadow-size -@shadow-size 0px;
  }

  // Item incorrect
  &__item.is-incorrect &__item-outline {
    border-color: @validation-error;
    @a: lighten(@validation-error, 25%);
    box-shadow: 
      inset @a @shadow-size @shadow-size 0px,
      inset @a -@shadow-size -@shadow-size 0px,
      @a @shadow-size @shadow-size 0px,
      @a -@shadow-size @shadow-size 0px,
      @a @shadow-size -@shadow-size 0px,
      @a -@shadow-size -@shadow-size 0px;
  }

  // Label
  // -------------------------------------------
  &__item-tail {
    z-index: -1;
  }
  &__item-tooltip {
    .btn-circle(@small: false);
    
    &::after {
      z-index: -1;
      background-color: @white;

      //border-width: @border-width*2;

      /*box-shadow: 1px 1px 0 @white,
                  -1px -1px 0 @white,
                  1px -1px 0 @white,
                  -1px 1px 0 @white;*/
    }

    &:not(.is-selected) {
      background-color: transparent;
      &::after {
        background-color: @white;
      }
    }

    &.is-selected {
      .glabel__item-text {
        color: @white;
      }
    }
  }

  &__item.is-correct &__item-tooltip    {color: @validation-success !important;}
  &__item.is-incorrect &__item-tooltip  {color: @validation-error !important;}
  &__item.is-correct &__item-tail       {background-color: @validation-success;}
  &__item.is-incorrect &__item-tail     {background-color: @validation-error;}

  &__item {
    .no-touch &:hover .glabel__item-tooltip {
      .btn-circle-hover();
    }
  }

  &__item-text {
    color: @btn-color;
    font-size: 1.5rem;
  }

  &__item-label.align-dir-5,
  &__item-label.align-dir-6,
  &__item-label.align-dir-7,
  &__item-label.align-dir-11,
  &__item-label.align-dir-12,
  &__item-label.align-dir-1 {
    .glabel__item-btn-tail {
      width: @glabel-tail-width;
    }
  }



  // Key
  // -------------------------------------------
  &__key {
    font-style: italic;
  }

  &__key-inner {
    //width: 100%;
    display: flex;
    flex-wrap: wrap;
    //display: grid;
    //grid-template-columns: repeat(auto-fit, minmax(@device-width-medium/4, 1fr));
  }

  &__option-key {
    font-weight: 500;
  }
}