// Block
// ----------------------------------

.mode-pdf .block {

  // Blocks with background images should try to be exactly 1 page in size
  // ---------------------------------------------------------------------
  &.has-bg-image {
    
    min-height: 100vh;
    padding-top: @block-padding-ver;
    padding-bottom: @block-padding-ver;
  }

  // (Temporary) hide all question blocks.
  // ---------------------------------------------------------------------
  &.is-question {
    display: none !important;
  }
}