.inputfield {
  
  @inputfield-color-inverted: mix(@font-color-inverted, @btn-color, 95%); // Background color (faint orange).

  &.is-legacy {
    width: 100%;
  }

  &__input {

    padding: @inputfield-padding; // (@icon-size + (@item-padding * 2)) @item-padding @item-padding;
    line-height: @body-line-height;
    border: @btn-color 1px solid;
    color: @font-color;
    background-color: @inputfield-color-inverted;

    width: 100%;

    .transition(border-color @duration ease, background-color @duration ease;);

    &::placeholder {
      font-style: italic;
    }

    /*.dir-rtl & {
      padding-left: @icon-size; // + (@item-padding * 2);
      padding-right: @item-padding;
    }*/
  }

  // Reduce padding on smaller devices.
  @media (max-width: @device-width-small) {
    &__input,
    &__placeholder {
        padding: @inputfield-padding/2;
    }
  }

  &.is-locked &__input,
  &__input[disabled] {
    color: fade(@font-color, 80%);
  }

  &__icon {
    margin-left: @inputfield-padding / 2;
  }

  // FIXME: Temp.
  // Hide the cross and tick icons (they'll be enabled again when we develop the accessibility modes further).
  &__state {
    display: none;
  }

  // Turn red when containing an incorrect answer (that's already been checked).
  // ---------------------------------------------------------------------------
  &.is-incorrect &__input {
    border-color: @validation-error;
    background-color: mix(@inputfield-color-inverted, @validation-error, 75%);
  }

  // Turn green when containing an incorrect answer (that's already been checked).
  // ---------------------------------------------------------------------------
  &.is-correct &__input {
    border-color: @validation-success;
    background-color: mix(@inputfield-color-inverted, @validation-success, 75%);
  }

  // Hint icon
  // --------------------------------------
  &__hint-icon {
    position: relative;
    color: @btn-color;
    .transition(color @duration ease-in, transform @duration ease;);

    .no-touch &:not(.is-disabled):not(.is-locked):hover {
      color: @btn-color-hover;
      transform: scaleY(1.2) translateZ(0);
    }

    // Increase the hit detection on the icon using a pseudo element (it's a bit small to click otherwise).
    &::before {
      content: "";
      position: absolute;
      top: -@inputfield-padding/2;
      left: -@inputfield-padding/2;
      right: -@inputfield-padding/2;
      bottom: -@inputfield-padding/2;
    }
  }

  &.is-incorrect &__hint-icon {
    color: @validation-error;

    .no-touch &:not(.is-disabled):not(.is-locked):hover {
      color: @validation-error-hover;
      transform: scaleY(1.2) translateZ(0);
    }
  }

  // Show/hide the hint icon with a transition.
  &__hint-icon {
    display: block;

    transform: scaleY(0) translateZ(0);
    pointer-events: none;
  }

  &.hint-enabled &__hint-icon {
    transform: scaleY(1) translateZ(0);
    pointer-events: all;
  }

  &.is-correct &__hint-icon {
    color: @validation-success;

    // "Close" the hint icon when the answer is correct.
    transform: scaleY(0) translateZ(0);
    pointer-events: none;
  }

  // Input overlays
  // --------------------------------------
  // Contain the cross and tick for useability + the reveal answers icon.
  &__overlay {

    display: flex;

    position: absolute;
    top: @inputfield-padding;
    right: @inputfield-padding;

    .dir-rtl & {
      right: inherit;
      left: @inputfield-padding;
    }
  }
}