.mcq {

  &__item {
    &:not(:first-child){
      .question-item-divider();
    }
  }

  &__item-text {
    width: 100%;
  }

  &__item-label {
    margin-bottom: @item-margin / 2;
    //background-color: @item-color;
    color: @item-color-inverted;
    border-radius: @item-border-radius;

    /*.no-touch &:not(.is-disabled):not(.is-selected):hover,
    html:not(.a11y-disable-focusoutline) .mcq__item-input:focus + & {
      background-color: @item-color-hover;
      color: @item-color-inverted-hover;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }*/

    /*.no-touch &:not(.is-disabled):not(.is-selected):hover .mcq__item-icon {
      .effect-scalePulse;
    }*/
  }

  &__item-label.is-disabled {
    //background-color: @disabled;
    color: @disabled-inverted;
  }

  &__item-label.is-selected {
    //background-color: @item-color-selected;
    //color: @item-color-inverted-selected;
  }

  &__item-state {
    /*position: absolute;
    top: 50%;
    left: @item-padding;
    .transform(translateY(-50%));*/

    /*.dir-rtl & {
      left: inherit;
      right: @item-padding;
    }*/
  }

  &__item-text {
    padding: @item-padding @item-padding @item-padding (@icon-size + (@item-padding * 2));

    .dir-rtl & {
      padding-left: @item-padding;
      padding-right: @icon-size + (@item-padding * 2);
    }
  }

  // DD customs
  // -----------------------------------------
  .mcq-item();

  &__item-text {
    padding: 0;
  }

  &__item-label {
    display: flex;
    align-items: center;
    padding: @item-padding;
    margin-bottom: 0;
  }

  // Hovering over the label should apply the hover style to the icon as well.
  .no-touch &__item-label:not(.is-disabled):not(.is-locked):hover &__item-answer-icon {
    .btn-circle-hover();
  }
  .no-touch &__item-label.is-incorrect:not(.is-disabled):not(.is-locked):hover &__item-answer-icon {
    color: @validation-error-hover;
  }
  .no-touch &__item-label.is-correct:not(.is-disabled):not(.is-locked):hover &__item-answer-icon {
    color: @validation-success-hover;
  }

  &__item-state {
    position: relative;
    flex-basis: 0;
    margin-right: @item-padding;
  }
}
