.componentrow {

  // Set all components that have titles (besides the first one) to use the sub-title style instead.
  &-has-title:not([data-componentrow-index='0']) {

    .component__title,
    .componentrow__title {
      margin-bottom: @component-sub-title-margin;
      .component-sub-title;
    }
  }

  // Vertical alignment
  // ------------------
  // Align both at the top by default
  &__inner {
    align-items: flex-start;
  }
  // Verically align to the center if one of the items is a graphic.
  &-contains-graphic &__inner {
    align-items: center;
  }
}
