.hotgraphicplus-popup {
  
  // Popup content
  // --------------------------------------------------
  &__item-content-inner {
    
    // Slide in.
    .effect-slideInRight();
    @media (max-width: @device-width-medium) {
      .effect-slideInTop();
    }
    
    background-color: @notify;
    color: @notify-inverted;
    .drop-shadow();

    // Add padding.
    padding: @notify-content-padding; //@item-padding;
    //margin: -@item-padding;
  }

  &__item-title {
    .notify-title();
    margin-bottom: @notify-title-margin;
  }

  &.is-closed .hotgraphicplus-popup__item-content-inner {
    .effect-slideOutLeft();
    @media (max-width: @device-width-medium) {
      .effect-slideOutTop();
    }
  }

  .hotgraphicplus-popup__item-graphic-small {
    .drop-shadow-half();
  }

  // Single column mode
  // ---------------------------------------------------
  &.is-single &__item-content-inner {
    .effect-slideInTop();
  }
  &.is-single.is-closed .hotgraphicplus-popup__item-content-inner {
    .effect-slideOutTop();
  }
}
