// componentRow
// ============

.mode-pdf .componentrow {

  // Stop displaying items as flex.
  &__inner {
    display: block;
  }

  // Wrap text around graphic components
  // -----------------------------------
  .graphic.is-right {
    float: right;
    padding-bottom: @component-padding-hoz/2;
    z-index: 1;
  }
  .graphic.is-left {
    float: left;
    padding-bottom: @component-padding-hoz/2;
  }
  // Remove the padding from the non-graphic sibling.
  &-contains-graphic {
    .component:not(.graphic).is-left,
    .component:not(.graphic).is-right {
      padding-right: 0;
      padding-left: 0;
    }
  }

  // Don't use the componentrow__title
  // -----------------------------------
  &__title {
    display: none !important;
  }
  .component__title {
    display: block;
  }
}