@extralearning-popup-icon-size: @notify-content-padding * 4;

// Icon
// ------------------------------
// This element is used in both the page button, and in the popup animation
.extralearning__icon {
    
    box-sizing: content-box;
    width: calc(1em ~"+" (@btn-padding*2)); // Ensures the button is roughly the same height as a normal .btn-text element.
    height: calc(1em ~"+" (@btn-padding*2));
    border-radius: 50%;

    background-color: @extralearning-btn-color;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60% 60%;

    flex-shrink: 0;

    color: @extralearning-btn-color-inverted;
    .drop-shadow-btn();

    &-extend	{ background-image: url("assets/extraLearning-extend.svg"); }
	&-dyk		{ background-image: url("assets/extraLearning-dyk.svg"); }
    &-spotlight	{ background-image: url("assets/extraLearning-spotlight.svg"); }
}

// Buttons
// ------------------------------
.extralearning-btn {

    display: block;
    
    &__inner {
        display: inline-block; // Have the button only be as wide as its text.
    }

	&__btn {
		
		display: flex;
		align-items: center;

        color: @extralearning-btn-color;
		cursor: pointer;
	}
	
	// Title
	// ------------------------------
	&__title {
		color: @extralearning-btn-color;
		padding: 0 @item-padding;
		font-weight: @font-weight-bold;
		//font-size: @item-title-size;
	}
}


// Popup
// ------------------------------

// Notify
.notify__popup.extralearning {
    background-color: transparent;
    opacity: 1 !important;

    // Normal view
    // -------------------------
    @media (min-width: @device-width-small) {

        // Remove default padding (needed to align a sticky element within).
        .notify__popup-inner {
            padding-top: 0;
            padding-bottom: 0;
            pointer-events: none; // Allow clicking through the containers.
        }
    }

    .notify__content {
        width: 100%;
    }
}

.extralearning-popup {

    &__widget {
        display: flex;
        align-items: top;
        
    }

    &__icon-container,
    &__content-container {
        padding: @notify-padding-ver 0;
    }

    &__content-container {
        width: 100%;
    }

    // Icon
    // ------------------------------
    // On desktop, the icon will always be vertically centered (even if the content on the right scrolls).
    &__icon-container {
        height: 100vh;

        position: sticky;
        top: 0;

        // Center the icon vertically.
        display: flex;
        align-items: center;
        flex-shrink: 0;
        flex-grow: 0;

        cursor: pointer;
        z-index: 1;
    }

    .extralearning__icon {

        pointer-events: all;

        // Increase the size of the icon.
        width: @extralearning-popup-icon-size;
        height: @extralearning-popup-icon-size;

        flex-shrink: 0;
        flex-grow: 0;

        // Transitions.
        transform-origin: 0% 0%;
        .transition(transform @duration*2 ease);

        &.is-active {
            transform: none !important;
        }
    }

    // Content
    // ------------------------------
    &__content-container {
        display: flex;
        align-items: center;
    }
    &__content {
        
        pointer-events: all;
        .effect-slideInLeft();

        width: 100%;

        color: @extralearning-color-inverted;
        background-color: @extralearning-color;
        
        // Have the content be overlapped by the icon slightly.
        padding: @notify-content-padding;
        padding-left: @notify-content-padding + @extralearning-popup-icon-size/2;
        margin-left: -@extralearning-popup-icon-size/2;
    }

    &.is-closed &__content {
        .effect-slideOutRight();
    }

    &__title {
        .notify-title();
        margin-bottom: @notify-title-margin;
    }

    // Small view
    // -------------------------
    // Align the icon and the content vertically on small devices.
    @media (max-width: @device-width-small) {

        &__widget {
            flex-direction: column;
            align-items: center;
            margin-top: -@notify-padding-ver/2; // Stylistic choice.
        }

        &__icon-container,
        &__content-container {
            padding: 0;
        }

        &__icon-container {
            height: auto;
            position: relative;
        }

        &__content {
            .effect-slideInTop();
            
            // Have the content be overlapped by the icon slightly.
            padding: @notify-content-padding;
            padding-top: @notify-content-padding/2 + @extralearning-popup-icon-size/2;
            margin-left: 0;
            margin-top: -@extralearning-popup-icon-size/2;
        }
        &.is-closed &__content {
            .effect-slideOutBottom();
        }
    }
}