// CodeAnim--pdf
//--------------------
// FIXME: It may be better design to put a message over the inspector box stating: "Please see the web-content to experience this content fully". At the moment, it's sort of hidden (meaning the student won't know what they're missing out on if viewing the PDF).

.mode-pdf .codeanim {

  // Remove display: flex from the containers (because display: flex breaks page-break: avoid).
  &.is-animated .codeanim__code-container,
  &__widget {
    display: block !important;
  }
  &__code-container {
    margin-bottom: @figure-padding-hoz;
  }

  // Start off with the output already revealed
  // ------------------------------------------
  &__anim-container {
    display: block !important;
  }
  &__console-print {
    visibility: visible !important; // Show every line in the console automatically.
  }
  
  // Hide the playbackcontrols and the inspector
  // ------------------------------------------
  &__playbackcontrols-container {
    display: none;
  }
  &__inspector-container {
    display: none;
  }

  // Don't highlight lines of if the animation is playing
  // ----------------------------------------------------
  .codebox__line.is-codeanim-mark {
    background-color: transparent;
    border-color: transparent;
  }

  // Never split a codeline down the middle.
  &__console-print {
    page-break-inside: avoid;
  }
}