// Stylize the annotation button.
.annotation {

  // Icon
  // --------------------------------------
  &__icon {
    .btn-circle(@hoverColorEnabled: false);
    color: inherit;

    border: none;
    width: @annotation-icon-size;
    height: @annotation-icon-size;

    .no-touch &:hover {
      color: inherit;
    }
  }

  // Hover
  // --------------------------------------
  .no-touch &:hover &__icon {
    .btn-circle-hover(@hoverColorEnabled: false);
    color: inherit;
  }

  // Active
  // --------------------------------------
  &:not(.is-active) &__icon {
    background-color: transparent; // Don't show the .btn-circle background while inactive.
  }

  &.is-active &__icon::before {
    color: @font-color-inverted;
  }
}