// DD Component Logo
// --------------------------------

@componentlogo-size: 9rem;
@componentlogo-margin: @component-body-margin*2;

@componentlogo-small-size: @component-title-size*1.8;

.componentlogo {
	width: @componentlogo-size;
	margin-left: @componentlogo-margin;
	margin-bottom: @componentlogo-margin/2;
	.u-float-right();

	// Stop floating when not on desktop size.
	@media (max-width: @device-width-medium) {
		//margin-top: -@component-title-size - @component-title-margin;
		float: none;
		margin-left: auto;
		margin-right: auto;
	}
}

.componentlogo-small {

	height: @componentlogo-small-size;
	display: inline-block;
	vertical-align: middle;
	margin-right: 0.25em;

	&__img {
		width: auto;
		min-width: 0;
		max-width: none;
		height: 100%;
	}

	position: absolute;
    top: 0;
    left: 0;
    transform: translateX(calc(-100% ~"-" @item-padding));

	// Move the logo to be above the title on small devices.
	@media (max-width: @device-width-medium) {
		/*display: flex;
		justify-content: center;
		margin-right: 0;

		height: @componentlogo-small-size*2;*/

		top: auto;
		left: auto;

		float: right;
		margin-left: 0.5em;
		margin-bottom: 0.25em;
		margin-right: 0;

		position: relative;
		transform: none;
	}	
}

// ComponentRow compatibility
// --------------------------------------------------------------
// Stop componentrow swapping the titles.
@media (max-width: @device-width-medium) {
    .component__header.has-componentlogo .component__title {
        display: block;
    }
    .componentrow__title.has-componentlogo {
        .u-display-none();
    }
}