// StickyBlock
// --------------------------------

// FIXME: Clean up classnames.

@stickyblock-item-margin: 70vh; // 50vh // Spacing increased to reduce chance of the user associating the wrong sticky + static child pairing.

// Additional padding added to both columns to make room for the timeline between them.
@stickyblock-timeline-padding: @component-padding-hoz;
@stickyblock-timeline-width: 0.125rem;

.block.is-stickyblock {
    overflow: visible; // REQUIRED to make the position: sticky; child function correctly.
}

.stickyblock {

    width: 100%;
    &__inner {
        display: flex;
    }

    // Columns
    // ----------------------------
    // Columns are full width in order for the inner .component elements to keep their appearance consistant (as if
    // they're still inside .component__container).
    &__col {
        //width: 100%;
        display: block;
        overflow: hidden;
        //flex: 1 1 auto;
        //position: absolute;
        //top: 0;

        .component {
            width: 100%;
        }

        // Add adapt-sizing-like .col-minwidth classes
        // --------------------------------------------------
        .col-loop(@i) when (@i <= 12) {
            &.col-minwidth-@{i} {
                min-width: (@i/12*@device-width-medium) !important; // FIXME! When adapt-sizing styles are reworked, this won't be necessary.
            }
            .col-loop(@i + 1);
        }
        .col-loop(1);
    }
    
    // Static column
    // ------------------------------------
    &__col.is-static {
        //margin-top: -100vh;

        flex: 1 1 50%;
        overflow: visible; // Sometimes (on narrow screen devices) the title can get clipped if overflow: hidden. Allow for it to go outside the bounds.
        z-index: 1; // Always show the static column over the sticky one.
        
        // Individual component container
        // ------------------------------------
        .stickyblock__item {
            margin: @stickyblock-item-margin 0;

            //&:first-child {margin-top: @stickyblock-item-margin;}
            //&:last-child {margin-top: 0;}
            
            // Slightly different rules for the first item.
            /*&:first-child {
                margin-top: @stickyblock-item-margin/1.25;
            }*/
        }
    }

    // Sticky column
    // ------------------------------------
    &__col.is-sticky {
        position: -webkit-sticky;
        position: sticky;
        top: 0;

        flex-grow: 1;
        flex-shrink: 1;

        height: 100vh; // - @stickyblock-item-margin; // Lowering this increases the bottom scroll buffer.
    }

    // Individual component container
    // ------------------------------------
    &__col.is-sticky &__item {

        height: 100vh;
        width: 100%;
        display: flex;
        align-items: center;

        // Copy the same padding the .block__inner has.
        //padding: @block-padding-ver @block-padding-hoz;

        // Have the sticky components overlap each other. When the user scrolls,
        // JavaScript will cross-fade them.
        &:not(:first-child) {
            margin-top: -100vh;
        }

        // When not active, turn invisible (and allow clicking through).
        &.is-hidden {
            visibility: hidden;
        }
    }


    // Items
    // ----------------------------------
    &__item {
        //padding-top: @component-padding;
        //padding-bottom: @component-padding;
        .l-container-padding(@notify-padding-ver/2, 0);
        position: relative;
    }
    &__col.is-sticky &__item-inner {
        margin: auto;
        width: 100%;
        max-height: 100%;
        overflow-y: auto;   // Start scrolling vertically if the graphic is too tall.
        overflow-x: hidden; // Never scroll horizontally.
    }


    // Component specific
    // ----------------------------------
    .component.graphic {
        //max-height: 100%;
        
        .graphic__image {
            max-height: calc(100vh ~"-" (@block-padding-ver*2));
            min-width: 0;
        }
    }
    
    // Sizing
    // ----------------------------------
    // Prevent the component's _sizing._width affecting it while in sticky mode.
    @media (min-width: @device-width-medium) {
        .component {
            max-width: none !important;
        }

        /*.component:not(.col-maxwidth-inner) > .component__inner {
            max-width: none !important;
        }*/
    }

    // Swap layout
    // ------------------------------
    &.layout-right &__inner {
        flex-direction: row-reverse;

        .component.is-right {
            margin-right: auto;
            padding-right: @component-padding-hoz;

            margin-left: 0;
            padding-left: 0;
        }
        .component.is-left {
            margin-left: auto;
            padding-left: @component-padding-hoz;

            margin-right: 0;
            padding-right: 0;
        }
    }

    // Small devices
    // ----------------------------------
    // Disable sticky functionality entirely and lay contents out as if they were normal components.
    /*@media (max-width: @device-width-medium) {
        &__col.is-sticky {
            position: relative;
            height: auto;
            
            .stickyblock__col-inner {
                height: auto;
                
                &:not(:first-child) {
                    margin-top: 0;
                }
            }
        }
    }*/
    
    // Timeline
    // ----------------------------------
    // Each static .stickyblock__item has its own timeline element in it, which draws a
    // vertical line between it and its sibling, along with an optional title.
    &.has-timeline {
        .stickyblock__col.is-static {overflow: visible;}
        &.layout-left {   
            .stickyblock__col-left  {padding-right: @stickyblock-timeline-padding;}
            .stickyblock__col-right {padding-left: @stickyblock-timeline-padding;}
        }
        &.layout-right {
            .stickyblock__col-left  {padding-left: @stickyblock-timeline-padding;}
            .stickyblock__col-right {padding-right: @stickyblock-timeline-padding;}
        }
    }
    
    &__timeline {
        position: absolute;
        top: 0;
        right: -@stickyblock-timeline-padding;
        height: 100%;

        transform: translateX(50%);
    }

    // Ensure the timeline covers the margins between items too.
    &__timeline-inner {
        display: block;
        margin-top: -@stickyblock-item-margin;
        height: calc(100% ~"+" @stickyblock-item-margin);

        display: flex;
        flex-direction: column;
        justify-content: stretch;
        align-items: center;
    }

    &__timeline-title {
        .item-title();
        padding: @item-padding 0;
        
        // New (makes the years easier to see on the timeline):
        font-size: 1.5rem;
        font-weight: 400;
    }

    &__timeline-line {
        width: @stickyblock-timeline-width;
        background-color: currentColor;
    }

    &__timeline-line-top {
        height: calc(@stickyblock-item-margin ~"-" 2rem);
    }
    &__timeline-line-bottom {
        flex-grow: 1;
    }

    // Extend the timeline in the last item down to the end of the block.
    &__item:last-child &__timeline-inner {
        height: calc(100% ~"+" (@stickyblock-item-margin*2));
        margin-bottom: -@stickyblock-item-margin;
    }
}