// TryHtml
// ------------------------------

.tryhtml
{
	// UI
	// ----------------------------------
	&__ui {
		margin-bottom: @component-body-margin;
	}

	&__ui-header {
		font-weight: bold;
		background: @item-color;
	}

	// UI controls
	// ----------------------------------
	&__ui-controls {
		display: flex;
		width: 100%;
		margin-top: @item-margin;
	}

	&__ui-btn {
		flex-grow: 1;

		&:first-child {
			margin-right: @item-margin;
			flex-grow: 4;
		}
	}

	// Show/hide answers
	// ----------------------------------
	.tryhtml__content-user {
		.transition(opacity @duration linear);
		opacity: 1;
	}
	// Show answer
	&__widget.show-correct-answer {

		// Fade out the user's script.
		.tryhtml__content-user {
			opacity: 0;
			pointer-events: none !important;
		}
	}

	// Content
	// ----------------------------------
	&__content-container {
		position: relative;
	}

	&__content {
		position: relative;
		width: 100%;
	}

	&__content-user {
		// Have the textarea's height be determined by the correct code example hidden behind it.
		position: absolute;
		min-height: 100%;

		// Have the answer code be fully covered up by the user code (until toggled). 
		z-index: 3;
	}

	&__content-correct {
		
	}

	// Output
	// ----------------------------------
	&__output {
		display: flex;

		& > * {
			flex-basis: 50%;
		}
	}

	&__output-user {
		margin-right: @item-margin;
	}
	
	&__output-user,
	&__output-correct {
		display: flex;
		flex-direction: column;
	}

	// Stack vertically on smaller devices.
	@media (max-width: @device-width-medium) {

		&__output {
			display: block;
		}

		&__output-user {
			margin-right: 0;
			margin-bottom: @item-margin;
		}
	}

	// HTML will be rendered into here (to prevent it affecting anything else).
	&__output-iframe {
		display: block;
		width: 100%;
		height: 100%;
	}

	// CodeMirror
	// ----------------------------------
	.CodeMirror {
		height: auto; // Change height based on the amount of content inside (it will start to scroll once it's taller than the correct code it's paired with).
		width: 100%;
		background: transparent;
	}
}