@hotgraphicplus-tile-gap: 4%;

.hotgraphicplus {
  
  &__widget {
    position: relative;
  }

  &__pin,
  &__tile {
    padding: 0;
    background-color: transparent;

    // Hide while active (large mode only)
    @media (min-width: @device-width-medium) {
      &.is-hidden {
        visibility: hidden;
      }
    }
  }

  // With background graphic (pins)
  // ==============================

  // Pins
  // --------------------------------------------------
  &__pin {
    width: 100%;
  }
  
  &__pin-item {
    position: absolute;
    top: 0;
    left: 0;
  }
  /*&__pin {
    position: absolute;
    
  }*/

  &__pin-item-container {
    position: relative;
  }

  // Finder mode
  // --------------------------------------------------
  // Render a circular counter to keep track of how many items have been visited.
  &__counter {
    position: absolute;
    top: 0;
    left: 0;

    border-radius: 50%;
    border: @border-width*2 @btn-color solid;

    text-align: center;

    z-index: 1; // Always be above the rest of the widget.

    cursor: pointer;

    // When hovered, fade out the content element and fade in the hint element.
    // Do this even on phones.
    /*.no-touch */&:hover {

      .hotgraphicplus__counter-content {
        opacity: 0;
      }

      .hotgraphicplus__counter-hint {
        opacity: 1;
      }
    }
  }

  // Ensures that the counter is always a perfect circle.
  &__counter-sizer {
    display: block;
    width: 100%;
    padding-bottom: 100%;;
  }

  &__counter-content,
  &__counter-hint {
    .transition(opacity @duration ease);
  }

  &__counter-content {
    text-align: center;
  }

  &__counter-hint {
    opacity: 0; // Hidden until hovered.
  }

  &__counter-inner,
  &__counter-hint {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  // This class is applied to an item whenever the user clicks the "hint" button.
  .is-finder-hint {
    border: 1px solid red; // Override this.
  }

  // Without background graphic (tiles)
  // ==================================

  // Tiles
  // --------------------------------------------------
  // When tiled, the items may be distributed like flex space-between or space-evenly.
  // Reason for using both: if only 2 or 3 items are used, it doesn't look very good.
  &__tile-item-container {
    /*display: flex;
    flex-wrap: wrap;
    //justify-content: space-between;
    justify-content: space-evenly;
    align-items: center;*/

    //padding: 0px @item-padding; 
    
    /*display: grid;
    //grid-template-columns: repeat(auto-fit, minmax(25%, 1fr));
    grid-template-columns: repeat(auto-fit, minmax(8rem, 1fr));
    //grid-gap: @hotgraphicplus-tile-gap;
    justify-items: center;
    align-items: center;*/

    //align-content: space-between;
    //justify-content: space-around;

    // If a custom column count is used, force responsiveness on smaller devices.
    // FIXME: !important used.
    /*@media (max-width: @device-width-small) {
      grid-template-columns: repeat(auto-fit, minmax(8rem, 1fr)) !important;
    }*/
  }

  &__tile-item {

    // Take up the full width of the grid cell, and center the contents inside.
    width: 100%;
    line-height: 0;
    //border: 1px solid black;

    /*display: flex;
    align-items: center;
    justify-content: center;*/

    //margin-left: @hotgraphicplus-tile-gap;
    //margin-right: @hotgraphicplus-tile-gap;

    padding: @item-padding;
    align-self: center;

    &.align-start   {align-self: flex-start;}
    &.align-center  {align-self: center;}
    &.align-end     {align-self: flex-end;}

    // Override adapt-sizing .col max-widths
    // --------------------------------------------------
    /*.col-loop(@i) when (@i <= 12) {
      &.col-@{i} {
        max-width: (@i/12*@device-width-medium) !important; // FIXME! When adapt-sizing styles are reworked, this won't be necessary.
      }
      .col-loop(@i + 1);
    }
    .col-loop(1);*/
  }

  &__tile-item-inner {
    width: 100%;
    max-width: 16rem;
    margin: auto;
  }

  &__tile {
    width: 100%;
    margin: auto;
    display: block;
  }

  // Tabs mode
  // --------------------------------------
  // FIXME: Should show an error if the items go across multiple rows.
  // FIXME: Tail is hard to see with the current colourscheme.
  &__tabs {
    margin-top: @component-body-margin;
    background: @item-color;
    padding: @item-padding;

    display: none; // Hide until first reveal.
  }

  // Tail
  // Moved left and right with JavaScript.
  &__tabs-tail {
    position: absolute;
    top: @item-padding;
    left: @item-padding*2;

    width: @item-padding*2;
    height: @item-padding*2;
    transform: translateX(-50%) rotate(45deg);
    
    pointer-events: none;
    z-index: -1;

    .transition(left @duration ease, top @duration ease;);

    background-color: inherit;

    // Slide-up out of the content when first activated.
    &.is-active {
      top: -@item-padding;
    }
  }

  // Items
  &__tabs-item-content-title {
    .item-title();
    margin-bottom: @item-title-margin;
  }

  &__tabs-item {
    height: 0;
    overflow: hidden;
    opacity: 0;
  }

  &__tabs-item.is-active {
    height: auto;
    opacity: 1;
  }

  // Placeholder
  &__tabs-placeholder {
    min-height: 5rem;

    display: flex;
    align-items: center;
    justify-content: center;

    color: fade(@item-color-inverted, @disabled-opacity*100);
    font-style: italic;
  }

  // Hide the placeholder once any item becomes active.
  &__tabs-item.is-active ~ &__tabs-placeholder {
    display: none;
  }

  // Hide the tabs on small screen sizes (tabs mode is set to only work on medium or higher screen sizes).
  // FIXME: Hardcoded for now.
  @media (max-width: @device-width-small) {
    &__tabs {
      display: none !important;
    }
  }

  // Single column mode
  // -----------------------------------------------------
  &.is-single &__pin,
  &.is-single &__tile {
    &.is-hidden {
        visibility: visible;
    }
  }
}
