// Playback controls
// =================

@pbcontrols-margin:     @item-margin;
@pbcontrols-btn-margin: @item-margin/2;
@pbcontrols-btn-size:   @btn-padding * 2;
@pbcontrols-track-size:     @pbcontrols-btn-size/8;

.playbackcontrols
{
    position: relative;
    overflow: hidden; // In case the timeline becomes hidden.

    &__inner {
        display: flex;
        justify-content: stretch;
        align-items: center;

        margin-top: @pbcontrols-margin;
        //transition: margin-top @duration ease;
    }

    // Controls
    // ------------------------------------------
    &__controls {

        flex-grow: 0;
        margin-right: @pbcontrols-margin;

        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: @pbcontrols-btn-margin;
    }

    @sprite-size: -@pbcontrols-btn-size;

    &__button {
        background-size: auto @pbcontrols-btn-size*2;
        background-repeat: no-repeat;
        background-image: url("assets/playbackControls-controls.svg");

        background-color: @btn-color;
        color: @btn-color-inverted;

        width: @pbcontrols-btn-size;
        height: @pbcontrols-btn-size;
        padding: 0;
    }

    &__back {
        background-position: @sprite-size 0;

        &[disabled]{
            background-position: @sprite-size @sprite-size;
        }
    }

    &__next {
        background-position: @sprite-size*2 0;

        &[disabled]{
            background-position: @sprite-size*2 @sprite-size;
        }
    }

    &__playpause {

        background-position: 0 0;
        
        &.playbackcontrols__play {
            background-position: 0 0;
        }

        &.playbackcontrols__pause {
            background-position: 0 @sprite-size;
        }

        &.playbackcontrols__replay {
            background-position: @sprite-size*3 0;
        }
    }

    // Range
    &__range-container {
        flex-grow: 1;
        display: flex;
        align-items: center;

        height: @pbcontrols-btn-size; // Height explicitly set for EPUB compatibility.
    }
    &__range {

        -webkit-appearance: none;
        outline: none;

        width: 100%;
        height: @pbcontrols-btn-size/8;
        border-radius: 0;
        background-color: darken(@btn-color, 20%);

        &::-webkit-slider-thumb {
            -webkit-appearance: none;
            appearance: none;
        
            width: @pbcontrols-btn-size/2.25;
            height: @pbcontrols-btn-size;
            background: @btn-color;
            cursor: pointer;
            border-radius: 0;
        }
    }
}