// DD Helpers
// --------------------------------

// This element should never be seen by the user.
.registerhelperel__placeholder {
    visibility: hidden;
}
// ...but if we're in debug mode, it should be visible if something goes wrong.
.mode-debug .registerhelperel__placeholder {
    color: magenta;
    visibility: visible;
}

// Hyperlink
// --------------------------------
a.hyperlink {
    .link-text();
    text-decoration: underline;

    .hyperlink__content::after {
        content: "\00a0"; // &nbsp;
    }

    .icon::before {
        display: inline;
        vertical-align: baseline;
        font-size: 1em;
        //padding-left: @icon-margin;
    }
}

a.tel {
    .icon::before {
        font-size: 1em;
        display: inline;
    }
}

// Inline icons
// --------------------------------
.inline {
    &__icon::before {
        display: inline;
        vertical-align: bottom;
    }

    &__img {
        display: inline;
        min-width: 0;
        width: auto;
        vertical-align: middle;
    }
}