// Text (button)
// --------------------------------------
.tooltip {

  // Glossary
  // --------------------------------------
  &.is-glossary {
    color: @glossary-color;
    .no-touch &:hover {
      color: @glossary-color-hover;
    }
  }

  &.is-reference {
    .tooltip__btn::before {
      content: "\00a0"; // &nbsp;
      white-space: nowrap;
    }
    .tooltip__btn::after {
      content: "\2060"; // Word-joiner (&#8288;). Prevents a break after the tooltip.
      white-space: nowrap;
    }
  }

  // When inside of blockquote
  // --------------------------------------
  // In blockquotes, add a slight margin
  // (tooltip btns are often followed by ".", this prevents the "." being too close to the tooltip__btn when the text is italic).
  .blockquote & &__btn {
    margin-right: 1px;
  }
}

// Popups
// --------------------------------------
.tooltip-popup {

  .drop-shadow-tooltip();

  // Annotation tooltip
  // -----------------------------------------------------
  &.is-annotation &__inner {
  
    //@annotation-color: mix(@btn-color, @font-color-inverted, 25%);

    background-color: @annotation-tooltip-color;
    border-color: @annotation-tooltip-border-color;
    color: @annotation-tooltip-color-inverted;
  }

  // Glossary tooltip
  // -----------------------------------------------------
  &.is-glossary &__inner {
    background-color: @glossary-tooltip-color;
    border-color: @glossary-tooltip-border-color;
    color: @glossary-tooltip-color-inverted;
  }

  &.is-glossary &__title {
    font-weight: @font-weight-bold;
    margin-bottom: @body-text-margin / 2;
  }
}

.tooltip-fallback {
  &__item {
    background-color: @annotation-tooltip-color;
    border-color: @annotation-tooltip-border-color;
    color: @annotation-tooltip-color-inverted;
  }
}