// <code> styles
// ==================================

//@code-inline-font-color: mix(@font-color, @font-color-inverted, 75%); // Default color if no prettyPrint class is provided.

.code()
{
	font-family: @code-family;
	background: @codebox-color;
	color: @codebox-color-inverted;

	//border: @codebox-border-width solid @codebox-border-color;

	padding: 0.1em 0.2em; // em used instead of rem to ensure consistant sizing inside figure-text elements.
	margin: 0em 0.08em; // Very slight margin (to make adjacent characters easier to see).

	mix-blend-mode: multiply;

	// Figure compatibility
	.figure__text & {
		padding: 0.4em 0.8em; // Increase padding when used as figure text.
	}

	// EPUB: Desktop Bookshelf fix
	// -----------------------------------------
	// Desktop Bookshelf doesn't support mix-blend-mode at this time.
	// Immitate it with a simple transparent background instead.
	.desktop-bookshelf & {
		mix-blend-mode: normal;
		background: rgba(0,0,0,(100% - luminance(@codebox-color)));
	} 

	// Don't use mix-blend-mode when against a feedback notify popup
	// (the bright coloured background makes the inline code look bad).
	.notify__popup.is-correct &,
	.notify__popup.is-incorrect &
	{
		mix-blend-mode: normal;

		// EPUB: Desktop Bookshelf fix
		// -----------------------------------------
		.desktop-bookshelf & {
			background: @codebox-color;
		}
	}
}
code {
	.code();
}

// Context considerations:
// -------------------------------------------------------------------------
// Don't apply the background and padding if used inside certain contexts.
.aria-label,
.annotation__btn,
.extralearning-btn,
.dropdown {
	._removeInlineCodeStyles();
}