// Modify the appearance of the btn__container to suit the DD behaviour created by questionPlus.
// FIXME: These styles may look nice, but they may also be bad for accessibility.
.component.is-questionplus .btn__container {

  .btn__response-container {
    justify-content: center;
  }

  // Buttons
  // --------------------------------------
  // Only one button will be visible at a time, so the margins can be removed.
  .btn__action:not(.is-full-width) {
    margin-right: 0;
  }
  .btn__feedback:not(.is-full-width) {
    margin-left: 0;
  }

  // Not submitted (complete)
  // --------------------------------------
  &:not(.is-submitted) .btn__feedback {
    display: none;
  }
  
  // Submitted
  // --------------------------------------
  &.is-submitted {

    // Hide the disabled submit button.
    .btn__action {
      display: none;
    }

    // Hide the default marking icon.
    .btn__marking {
      display: none;
    }
  }
}