.inputfield {

    position: relative;
    display: inline-block;
    
    &__input {
        /*
        * Hide 'X' icon for IE11 and Edge 12+
        * Issue:https://github.com/adaptlearning/adapt_framework/issues/1913
        */
        &::-ms-clear {
            display: none;
        }
    }

    // Icons
    // --------------------------------------------------
    &__icon {
        display: none;
    }

    &__correct-icon .icon {
        .icon-tick;
    }

    &__incorrect-icon .icon {
        .icon-cross;
    }

    &__overlay {
        pointer-events: none;
    }
    &__hint-icon {
        cursor: pointer;
    }
    &__hint-icon .icon {
        .icon-eye;
    }

    // Show states
    &.is-correct &__correct-icon,
    &.is-incorrect &__incorrect-icon {
      display: block;
    }

    // Show hint (unless correct).
    &.hint-enabled:not(.is-correct) &__overlay {
        pointer-events: all;
    }

    // {{#textInput}} helper
    // --------------------------------------------------------------------------
    width: auto;
    
    &__input {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        
        padding: @inputfield-padding;

        overflow: hidden;
        text-overflow: ellipsis; // Make it clear when the user has typed in values which are too long.
    }

    // Remove the default up and down arrows from number input.
    // Chrome, Safari, Edge, Opera
    &__input[type=number]::-webkit-outer-spin-button,
    &__input[type=number]::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }
    // Firefox
    input[type=number] {
        -moz-appearance: textfield;
    }
    
    // Placeholder
    // --------------------------------------
    // The placeholder is what will dictate the height of the <input> element.
    &__placeholder {
        padding: @inputfield-padding;
        pointer-events: none !important; // Don't block clicks to the input behind it.
        box-sizing: content-box;

        color: @placeholder;
        text-align: center;

        transform: translateZ(0); // Ensure complex elements don't disappear.
        .transition(opacity @duration ease);
    }
    // Fade out the placeholder whenever the input is focused / has any characters in it.
    &.is-focused &__placeholder {
        opacity: 0;
    }
    &.has-val &__placeholder {
        opacity: 0;
    }

    // LEGACY
    // --------------------------------------------------------------------------
    &.is-legacy {
        width: 100%;
        .inputfield__input {
            position: relative;
        }
    }
}