// ddLoading
// ----------------------------------------------------
// FIXME: General clean up needed.

@dd-loading-color: white;
@dd-loading-graphic-size: 160px;
@dd-loading-graphic-back-opacity: 0.25;
@dd-loading-idle-duration: @duration * 6;

.ddloading {
	&.has-error {
		background-color: fade(darken(@validation-error, 10%), 90%);
		.transition(background-color @duration ease);
	}
}

.ddloading__header {

	@a: (@dd-loading-graphic-size/2) + @item-padding;
	position: absolute;
	bottom: calc(50% ~"+" @a);
	width: 100%;

	color: @dd-loading-color;

	&-inner {
		.l-container-padding(@block-padding-ver/2, @block-padding-hoz);
	}

	&-text {
		font-size: 1.25rem;
		text-align: center;

		@media (max-width: @device-width-small) {
			font-size: 1rem;
		}
	}
}

.ddloading__graphic {

	position: absolute;
	left: 50%;
	top: 50%;
	margin-left: -@dd-loading-graphic-size/2;
	margin-top: -@dd-loading-graphic-size/2;
	width: @dd-loading-graphic-size;
	height: @dd-loading-graphic-size;

	&-img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transform-origin: center;

		opacity: 0.1; // Same value used in JavaScript.
		.transition(opacity @duration*2 ease);

		&[data-logo-index="0"] 	{transform: rotate(0deg);};
		&[data-logo-index="1"] 	{transform: rotate(22.5deg);};
		&[data-logo-index="2"] 	{transform: rotate(45deg);};
		&[data-logo-index="3"] 	{transform: rotate(67.5deg);};
		&[data-logo-index="4"] 	{transform: rotate(90deg);};
		&[data-logo-index="5"] 	{transform: rotate(112.5deg);};
		&[data-logo-index="6"] 	{transform: rotate(135deg);};
		&[data-logo-index="7"] 	{transform: rotate(157.5deg);};
		&[data-logo-index="8"] 	{transform: rotate(180deg);};
		&[data-logo-index="9"] 	{transform: rotate(202.5deg);};
		&[data-logo-index="10"] {transform: rotate(225deg);};
		&[data-logo-index="11"] {transform: rotate(247.5deg);};
		&[data-logo-index="12"] {transform: rotate(270deg);};
		&[data-logo-index="13"] {transform: rotate(292.5deg);};
		&[data-logo-index="14"] {transform: rotate(315deg);};
		&[data-logo-index="15"] {transform: rotate(337.5deg);};
	}

	&-percent {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		display: flex;
		align-items: center;
		justify-content: center;

		font-size: 1.25rem;
		font-weight: 500;

		animation: anim-ddloading-percent @duration*7 ease infinite alternate-reverse;
	}
	@keyframes anim-ddloading-percent {
		from {opacity: 0.4;}
		to {opacity: 1;}
	}

	// Idle animation
	// --------------------------------------------------------
	// This animation will play if the .loading element appears again after the initial pageload.
	&.is-idle &-percent {
		display: none; // Don't show the 0%-100% progress text.
	}
	@keyframes anim-ddloading-idle {
		0% {opacity: 0.1;}
		30% {opacity: 1;}
		70% {opacity: 1;}
		100% {opacity: 0.1;}
	}

	&.is-idle &-img {

		opacity: 0;
		
		animation: anim-ddloading-idle @dd-loading-idle-duration ease infinite normal;
		@d: @dd-loading-idle-duration / 16;

		&[data-logo-index="0"] 	{animation-delay: @d;};
		&[data-logo-index="1"] 	{animation-delay: @d*2;};
		&[data-logo-index="2"] 	{animation-delay: @d*3;};
		&[data-logo-index="3"] 	{animation-delay: @d*4;};
		&[data-logo-index="4"] 	{animation-delay: @d*5;};
		&[data-logo-index="5"] 	{animation-delay: @d*6;};
		&[data-logo-index="6"] 	{animation-delay: @d*7;};
		&[data-logo-index="7"] 	{animation-delay: @d*8;};
		&[data-logo-index="8"] 	{animation-delay: @d*9;};
		&[data-logo-index="9"] 	{animation-delay: @d*10;};
		&[data-logo-index="10"] {animation-delay: @d*11;};
		&[data-logo-index="11"] {animation-delay: @d*12;};
		&[data-logo-index="12"] {animation-delay: @d*13;};
		&[data-logo-index="13"] {animation-delay: @d*14;};
		&[data-logo-index="14"] {animation-delay: @d*15;};
		&[data-logo-index="15"] {animation-delay: @d*16;};
	}
}