// ImgFinder
// -----------------------------------------------------------------

.imgfinder {

  // Container + image
  // -----------------------------------------------
  &__item-container {

    // Makes the inner element only as wide as the image inside it (applicable if the image is a non SVG with auto-width applied).
    display: flex;
    justify-content: center;

    .u-no-select;
  }

  &__item-container-inner {
    position: relative;
  }

  // Items (invisible hotspots).
  // -----------------------------------------------
  &__item {
    position: absolute;
    
    // Show the hotspots only while adapt-debug Interact is active.
    .mode-debug & {
      border: 2px solid rgba(128, 0, 128, 0.7);
      //mix-blend-mode: hue;
    }
    .mode-debug &.debuginteract {
      background: rgba(128, 0, 128, 0.7);
    }
  }
  
  // Outlines
  // -----------------------------------------------
  
  // This is the area that the user will be able to click to create outlines.
  &__outline-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    cursor: pointer; // TODO: Is there a more appropriate cursor for this?

    z-index: 0;
  }

  // Disable interaction while in debuginteract mode (from adapt-dd-debug).
  .debuginteract + &__outline-container {
    pointer-events: none;
  }

  // Once submitted, change the cursor back to normal.
  &.is-submitted &__outline-container {
    cursor: auto;
  }
}
