// DD Sidebar
// ==========

// Core overrides
// --------------------------------------
.sidenav {
    width: @sidenav-width; // Temp.
    
    height: 100%;
    height: -webkit-fill-available; // Mobile browser fix.

    background-color: white;

    .nav__inner {
        padding: @sidenav-padding;
    }

    // SideNav widget.
    &__widget {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: inherit;

        display: flex;
        flex-direction: column;

        height: 40vh;
    }

    // Horizontal layout
    // -----------------------------------
    @media (max-width: @device-width-medium) {
        width: 100%;
        height: @sidenav-width-small; // FIXME: Temp.
        overflow: hidden;

        &__widget {
            display: none; // FIXME: Temp.
        }
    }
}

// Compatibility with scrollfix extension
// --------------------------------------
html.adapt-scrolling {
    .sidenav {
        position: fixed;
    }

    #app {
        height: 100%;
    }
}

// Medium device width
// --------------------------------------
// Add an additional margin to the main content to avoid the navbar on the left.
@media (max-width: @sidenav-media-width) and (min-width: @device-width-medium) {

    .component__container {
        margin-left: @sidenav-width;
    }

    // FIXME:
    /*.block {
        background-position-x: calc(50% ~"+" (@sidenav-width/2)) !important;
    }*/
}

// Horizontal nav spacing
// --------------------------------------
// If .adapt-scrolling is disabled (which adapt-scrollfix causes to happen on mobile devices),
// disable the core #wrapper padding styles.
@media (min-width: @device-width-medium) {
    #wrapper {
        padding-top: 0;
    }
}

// Small device width
// --------------------------------------
// Add an extra margin above the page to adjust for the sideNav being aligned to the top.
// Only apply when the config._scrollingContainer._isEnabled == true.
@media (max-width: @device-width-medium) {

    // When on small, non-mobile device.
    .adapt-scrolling {
        #app {
            //margin-top: @sidenav-width-small;
            border-top: @sidenav-width-small solid transparent; // Border used instead of margin (margin causes the bottom of the page to clip).
        }
    }
    // FIXME: Temp.
    .page__header {
        background-position-y: @sidenav-width-small !important;
    }
}