// DD Sidenav blocknum
// ===================
// Updating circle with the block number that sits at the bottom of the navbar.

@sidenav-blocknum-margin:       -@sidenav-padding * 0.25;
@sidenav-blocknum-size:         @sidenav-width - (@sidenav-padding * 2) - (@sidenav-blocknum-margin * 2);
@sidenav-blocknum-font-size:    round(@sidenav-blocknum-size/2) - 0.5rem;	// Number in the circle.

@sidenav-complete-color:         @validation-success;
@sidenav-incomplete-color:       @drawer;

.sidenav__blocknum
{
    position: relative;
    width: @sidenav-blocknum-size;
    height: @sidenav-blocknum-size;
    margin: @sidenav-blocknum-margin;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50%;
    overflow: hidden;

    .transition(background-color @duration ease);

    &-inner {
        width: 100%;
    }

    // When hovered, slightly darken the background.
    .no-touch &:hover
    {
        background-color: rgba(16,19,58,0.12);
    }

    // Completion bar halves
    // -------------------------------------------------
    &-half {
        position: absolute;
        width: 50%;
        height: 100%;
        top: 0;

        &.is-left {
            left: 0%;
        }

        &.is-left &-rot {
            transform-origin: 100% 50%;
            transform: rotate(180deg);

            &::before {
                right: -@sidenav-bar-width/2;
            }

            .sidenav__blocknum-bar.is-complete {left: 0%;}
            .sidenav__blocknum-bar.is-incomplete {left: 100%;}
        }

        &.is-right {
            left: 50%;
        }

        &.is-right &-rot
        {
            transform-origin: 0% 50%;
            transform: rotate(-180deg);

            &::before {
                left: -@sidenav-bar-width/2;
            }

            .sidenav__blocknum-bar.is-incomplete {left: -100%;}
            .sidenav__blocknum-bar.is-complete {left: 0%;}
        }

        // Clipping
        // ---------------------------------------
        &-clip
        {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            overflow: hidden;
        }

        // Rotation
        // ----------------------------------------
        &-rot
        {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            transition: transform @duration ease;

            // Small circles that give the tips of the completion bar border a rounded edge.
            &::before
            {
                content: "";
                position: absolute;

                width: @sidenav-bar-width;
                height: @sidenav-bar-width;
                background-color: @sidenav-complete-color;
                border-radius: 50%;

                bottom: 0;
            }
        }
    }

    // Bars
    // ----------------------------------------
    &-bar
    {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;

        overflow: hidden;

        &::before
        {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 200%;
            height: 100%;

            box-sizing: border-box;
            border-radius: 50%;
            border: @sidenav-bar-width solid @sidenav-complete-color;

            z-index: -1;
        }

        &.is-complete {
            z-index: 1;
        }
        &.is-incomplete::before
        {
            border-color: @sidenav-incomplete-color;
        }
    }

    // Fix alignment of bars
    // ---------------------------------
    &-half.is-left &-bar {
        &:nth-child(1)::before
        {
            left: -100%;
        }
    }
    &-half.is-right &-bar {
        &:nth-child(2)::before
        {
            left: -100%;
        }
    }

    // Input
    // ---------------------------------
    &-text {
        position: relative;
        z-index: 1;

        &-input {
            font-family: @title-family;
            font-size: @sidenav-blocknum-font-size;

            text-align: center;

            width: 100%;
            height: 100%;
            margin-top: -1px;
            padding: 0;
            border: none;
            
            color: @drawer;
            background-color: transparent;

            // Chrome , Safari, Edge, Opera
            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button
            {
                -webkit-appearance: none;
                margin: 0;
            }

            // Firefox
            &[type=number] {
                -moz-appearance: textfield;
            }
        }
    }

