// Apply to a .codebox.is-console element to have it blur under specific conditions.
// FIXME: Use of this is a bit niche at this time.
.consolebox-blur(){

	// Blur the console box until the user activates this component.
	// FIXME: Animation likely temporary.
	// FIXME: JS needs to apply a class to the console element instead of it being purely CSS as done here.
	.codebox__inner {
		transform: translateZ(0); // Reduces the chance of rendering issues.
		filter: blur(3.5px) brightness(1.5);
		.transition(filter @duration ease);
	}

	// Bounce and unblur the console element when activated.
	.is-activator-target.is-activator-active & {
		.effect-bounceUpOnce();
		.codebox__inner {
			filter: none;
		}
	}
}

.codeanim {

	// Layout
	// -------------------------------------------------------
	// Have the same layout that .codebox uses inside of a figure.
	&__widget {
		
		@media (min-width: @device-width-medium) {
			display: flex;
		}

		.figure-inner-half();
	}

	// Consistant widths
	// -----------------------------------------------------
	&__content {
		flex-grow: 1; // If a codebox element is alone inside of a figure, have it take up the full width.
		flex-shrink: 1;
		.figure-item-spacing-half(@useMargin: true); // Margins

		max-width: 100%;

		// Have the code, console, and inspector boxes be the same height.
		.is-code,
		.is-console {
			height: 100%;
		}
	}

	&__code-container {
		//flex-basis: 100% - @codebox-console-width;
		//flex-shrink: 1;
		overflow: hidden; // Fixes a width bug caused by "white-space: nowrap" on the child codebox on large devices.
	}
	&__console-container {
		flex-grow: 2;
	}

	// Inspector
	// ----------------------------
	&__inspector-container {
		background-color: @codebox-inspector-color;
	}

	&__inspector-cell {
		padding: 0 @code-padding;

		&.is-header {
			padding: @code-padding;
		}
	}
	&__inspector-cell-value {
		padding: 0;
	}
	&__inspector-cell-value-inner {
		padding: 0 @code-padding;
	}

	&__inspector-container {
		border: 1px solid @codebox-color-inverted;
		font-family: @code-family;
		flex-basis: 75%;
	}

	&__inspector-row.is-header &__inspector-cell {
		border: 1px solid @codebox-color;
		font-family: @body-family;
		padding-top: @code-padding / 2;
		padding-bottom: @code-padding / 2;

		&.codeanim__inspector-cell-name {
			min-width: 15%;
		}
	}

	/*&__inspector-cell-var {
		padding-top: 0;
		padding-bottom: 0;
		overflow: hidden
	}*/

	// Flicker inspector rows whenever they change.
	.effect-flicker() {
		.keyframes(flicker; {
		  0% { background-color: mix(@mark-color, @mark-border-color, 80%); }
		  100% { background-color: @codebox-inspector-color; }
		});
	  
		.animation(flicker 0.25s 0s 1 normal ease-out none);
		// name, duration, delay, iteration count, direction, timing-function, fill mode
	}

	&__inspector-row,
	&__inspector-cell-value-inner {
		.effect-flicker();
	}

	// Animation
	// ------------------------------------
	&.is-animated &__widget {
		flex-wrap: wrap;
	}
	&.is-animated &__code-container {
		flex-basis: 100%;
		//margin-right: 0;
		margin-bottom: @figure-padding-hoz;
	}
  
	// Activator compatibility
	// ------------------------------------
	&__console-container.has-blur {
		.consolebox-blur();
	}

	// Playback controls
	// -----------------

	// Hide the playbackControls extension until activated.
	// FIXME: Temp. Poor encapsulation.
	&.is-activator-target .playbackcontrols__inner {
		margin-top: -@pbcontrols-btn-size;
		.transition(margin-top @duration ease);
	}

	&.is-activator-target.is-activator-active .playbackcontrols__inner {
		margin-top: @pbcontrols-margin; // FIXME: Temp. Poor encapsulation.
	}

	// Marked lines
	// ------------
	.codebox__line.is-codeanim-mark {

		// Validation error
		&[data-codeanim-anim="validation-error"] {
			.effect-validationError();
			& > span {
				color: @validation-error;
			}
		}

		// Validation success
		&[data-codeanim-anim="validation-success"] {
			.effect-validationSuccess();
			& > span {
				color: mix(@validation-success, @codebox-color-inverted, 50%);
			}
		}
	}

	// Debug only
	// ------------------------------------
	.mode-debug & {

		/*.codebox {
			overflow-x: visible;
		}*/

		.codebox__lines {
			counter-reset: codeAnimLineNum;
		}

		.codebox__line::before {
			
			position: absolute;
			left: -2rem;
			top: 0;

			color: rgba(129, 12, 129, 0.35);
			counter-increment: codeAnimLineNum;
			content: counter(codeAnimLineNum);
		}
	}
	

	// New layout idea: flex-wrap
	// ------------------------------------
	// Wrap the consolebox to another line if more room is needed.
	&__widget {
		flex-wrap: wrap;
	}


	// Layout (mobile)
	// -------------------------------------------------------
	// All in one column.
	@media (max-width: @device-width-medium) {
		&__anim-container {
			display: block;
		}
	}
	
	// Max height (desktop only)
	// -------------------------------------------------------
	// Sometimes the codebox is just too tall. Add a limit and have it scroll.
	@media (min-width: @device-width-medium) {
		&__code-container {
			max-height: @device-width-medium;

			.codebox {
				overflow: auto;
				
				// Have the code container have the background, and remove the background from inner codebox.
				background: @codebox-color;
				border: @codebox-border-color @border-width solid;

				.codebox__content {
					border: none;
					background: none;
				}
			}
		}
	}
}