// GraphicLabel Popup
// ---------------------------------------
// This component replaces the default notify popup shadow with its own one to create a "highlighting" effect on the outline of the item that was just clicked.

@glabel-popup-duration: @duration * 1.5;

// Modify notify styles
// ---------------------------------------------------------
// FIXME: Hardcoded to DD's notify overrides (we should port those overrides to thier own extension, and then have glabelPopup require that extension).
.notify__popup.glabel {
  background-color: transparent;
  opacity: 1 !important;

  .notify__popup-inner {
    align-items: start;
    background-clip: content-box;
  }

  & + .shadow {
    background: fade(@shadow, 80%); // Reduce shadow opacity slightly so the user can see the context a bit better.
  }
}

// Allow clicking through everything that isn't the content.
.glabel.notify__popup .notify__content {
  pointer-events: none;
}

.glabel-popup {

  position: relative;

  &__content-container {
    display: flex;
    justify-content: flex-start;
  }

  // Item (the copy that needs to align itself with the one in the component).
  // ----------------------------------------------------------
  .glabel__item {
    position: relative;
    display: block;
    margin-top: @glabel-label-margin;
  }

  // Copy of the graphic from the parent component, positioned within the item's outline.
  .glabel__item-outline {
    overflow: hidden;
  }
  &__image {
    position: absolute;
    left: 0;
    top: 0;
    max-width: none;
  }

  .glabel__item-btn {
    border-color: transparent;
    background-color: transparent;

    // FIXME: Temp.
    &::after {
      content: none;
    }
  }

  // Content
  // ----------------------------------------------------------
  &__content {
    z-index: 0;

    position: relative;
    pointer-events: all;
    display: inline-block; // Use minimal width for the content.
    padding: @item-padding;
  }

  &__content-inner {
    opacity: 0;
    .transition(opacity @glabel-popup-duration ease);
  }
  &.is-active &__content-inner {
    opacity: 1;
    transition-delay: @glabel-popup-duration;
  }


  // Content animation
  // ----------------------------------------------------------
  &__content-anim {
    z-index: -1;

    position: absolute;
    border: @border-width solid @btn-color;

    border-radius: @icon-size;
    background-color: white;

    .transition(
      top ease @glabel-popup-duration,
      left ease @glabel-popup-duration,
      width ease @glabel-popup-duration,
      height ease @glabel-popup-duration,
      border-radius ease @glabel-popup-duration,
      border-color ease @glabel-popup-duration;);
    transition-delay: 100ms;
    
    &.is-active {
      top: 0 !important;
      left: 0 !important;
      width: 100% !important;
      height: 100% !important;
      border-radius: @border-radius !important;
    }

    &.is-correct {
      border-color: @validation-success;
    }
    &.is-incorrect {
      border-color: @validation-error;
    }
  }

  .glabel__item-tooltip {
    .transition(opacity @glabel-popup-duration ease);

    &.is-active {
      opacity: 0;
    }
  }
  
  // Options items
  // ----------------------------------------------------------
  &__item {
    position: relative;
    width: 100%; // Bugfix: needs to be specified for Offline Bookshelf.
  }

  &__item-state {
    .u-no-select;
  }

  &__item-input {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    z-index: -1;
    .transform(translate(-50%, -50%));
  }

  &__item-label {
    display: flex;
    align-items: center;
    z-index: 1;
  }

  &__item-label:not(.is-disabled) {
    cursor: pointer;
  }

  &__item-icon {
    display: none;
  }

  &__item-key-icon {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;

    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    pointer-events: none;

    .item-title();
    font-weight: normal;
  }

  &__item-label.is-selected &__item-key-icon {
    color: @white;
  }

  // Item icons
  // --------------------------------------------------
  &__item-answer-icon.is-radio .icon {
    .icon-radio-unchecked;
  }

  &__item-answer-icon.is-checkbox .icon {
    .icon-checkbox-unchecked;
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-radio .icon {
      .icon-radio-checked;
    }
  }

  &__item-label.is-selected {
    .mmcq__item-answer-icon.is-checkbox .icon {
      .icon-checkbox-checked;
    }
  }

  &__item-correct-icon .icon {
    .icon-tick;
  }

  &__item-incorrect-icon .icon {
    .icon-cross;
  }
}