.mmcq {

  // Add dividers between the items.
  /*&__item-text,
  &__items-header-spacer,
  &__items-header-label,
  &__item-input-container {*/

  &__widget {
    border-collapse: collapse;
  }

  // Headers
  // -------------------------------------------
  
  // Prevent the input columns for the combined layout from being less wide than the icon inputs inside them.
  /*&__items-header-label,
  &__item-input-container {
    //min-width: @btn-circle-size;
  }*/

  // Items
  // -------------------------------------------

  &__item {
    //border-top: 2px ridge grey;// rgba(255, 255, 255, 0.8);
    //border-top: 2px groove rgba(255, 255, 255, 0.8);// rgba(0,0,0,0.2);
    .question-item-divider();
    //border-style: ridge;
    //border-width: 2px;
    //border-image-slice: 1;
    //border-image-source: linear-gradient(to left, #743ad5, #d53a9d);
  }

  // Input
  // -----------------------------------------

  &__item-state {
    position: relative;
  }

  &__item-input-container {
    padding-left: @item-padding;
    padding-right: @item-padding;

    // Small device
    // -------------------------------------
    // Align the radio buttons under the label.
    @media (max-width: @device-width-medium) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  // DD customs
  .mcq-item();

  // Codebox in item bugfix:
  // --------------------------------
  // If a codebox is used inside of the "display: table" element, it can sometimes have issues being scrolled (happens in Desktop Bookshelf).
  // This fixes that.
  &__widget .codebox {
    transform: translateZ(0);
  }
}
