// --------------------------------------------------

// --------------------------------------------------
// Font family
// --------------------------------------------------
@title-family: /*'Roboto', Helvetica, Arial, sans-serif;*/ 'Roboto', Helvetica, Arial, sans-serif;
@body-family: 'Roboto', Helvetica, Arial, sans-serif;
@btn-family: /*'Roboto', Helvetica, Arial, sans-serif;*/ 'Roboto', Helvetica, Arial, sans-serif;
@code-family: 'Courier New', Courier, monospace;
@math-family: 'Times New Roman', Times, serif;
// --------------------------------------------------

// --------------------------------------------------
// Font weights
// --------------------------------------------------
@font-weight-light: 300;
@font-weight-regular: 400;
@font-weight-bold: 500; //700;

// 100	Thin (Hairline)
// 200	Extra Light (Ultra Light)
// 300	Light
// 400	Normal
// 500	Medium
// 600	Semi Bold (Demi Bold)
// 700	Bold
// 800	Extra Bold (Ultra Bold)
// 900	Black (Heavy)
// --------------------------------------------------

// --------------------------------------------------
// Line heights
// --------------------------------------------------
@title-line-height: 1.2;
@body-line-height: 1.5;
@btn-line-height: 1.5;
// --------------------------------------------------

// --------------------------------------------------
// Globals
// --------------------------------------------------
@body-size: 1rem;
@body-weight: @font-weight-regular;

@instruction-size: 1rem;
@instruction-family: @body-family;
@instruction-weight: @font-weight-bold;
@instruction-line-height: @body-line-height;

@btn-size: 1rem;
@btn-weight: @font-weight-bold;

@icon-size: 1.5rem;
@icon-weight: @font-weight-regular;
@icon-line-height: 1;

