// MatchingPlus
// ----------------------------------------------------------------------

// Allow the custom select dropdowns to extend past the block like a real <select> dropdown would. 
.block.has-matchingplus {
  overflow: visible !important;
}

.matchingplus {
  
  // Ensure that the .dropdown__options elements don't get overlapped by the .btn__container element under them.
  &__widget {
    position: relative;
    z-index: 1;
  }
  
  // Items
  // --------------------------------------------
  //@media (min-width: @device-width-small) {
    &__item {
      /*display: grid;
      grid-template-columns: repeat(auto-fit, minmax(@device-width-small/2, 1fr));
      grid-auto-columns: auto 1fr;
      align-items: center;
      grid-column-gap: @item-padding;
      grid-row-gap: @item-padding;*/

      position: relative;
      display: flex;
      //flex-wrap: wrap;
      align-items: center;

      &:not(:last-child) {
        margin-bottom: @item-margin;
      }
    }

    &__item-text {
      flex-grow: 1;
      flex-shrink: 1;
      margin-right: @item-padding*1.5;
    }

    &__item-select {
      flex-grow: 0;
      flex-shrink: 0;
      max-width: 50%;

      // Show the <select> and dropdown side-by-side. Then, the dropdown will give itself a negative margin to overlap the <select>. 
      display: flex;
    }

    @media (max-width: @device-width-small) {
      &__item {
        display: block;
      }

      &__item-select {
        margin-top: @item-padding;
        max-width: none;
        width: 100% !important; // Override the inline width style set by JavaScript.
      }
    }

    /*&__item-title {
      flex-grow: 1;
    }
  
    &__select-container {
      width: 50%;
    }*/
  //}

  // Selectboxes & Dropdowns
  // --------------------------------------------

  // Overlap the <select> with the dropdown.
  &__select-container,
  &__dropdown {
    width: 100%;
  }
  &__dropdown {
    margin-left: -100%;
  }

  &__select {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__select-container {
    position: relative;
    //flex-shrink: 0;
    //flex-grow: 0;
  }

  // (dropdowns = stylized <select> proxy element).
  &__select,
  &__dropdown {
    position: relative;
    line-height: @body-line-height;
    text-align: left;
    background: @item-color;
    color: @item-color-inverted;
    border: none;
    width: 100%;

    .no-touch :hover {
      background-color: @item-color-hover;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }

    .dir-rtl & {
      //padding-right: @item-padding;
      //padding-left: @icon-size + (@item-padding * 2);
      text-align: right;
    }
  }

  // Trigger button
  &__select,
  &__dropdown-trigger {
    padding: @item-padding;
    padding-right: @item-padding*3.5; // Add additional space on the right for the dropdown icon and the item state icon.
  }

  // Option
  // (Most of these styles won't work for the .__select-option. This is intentional).
  &__select-option,
  &__dropdown-option {
    padding: @item-padding;
  }

  // Dropdowns have item states in them too.
  &__dropdown-option {
    position: relative;
    padding-right: @item-padding*3.5; // Add additional space on the right for the dropdown icon and the item state icon.
  }

  // Dropdowns
  // -----------------------------------------------------------------
  // Overlay the real <select> with this stylized alternative.
  // Mobile and aria devices will still be able to access the <select> element under it.
  // I'm referring to it as a 'dropdown' to make it easy to distinguish from the <select>.
  &__dropdown {
    //position: absolute;
    //top: 0;
    //left: 0;
    //margin-top: -@item-padding*2 - @body-line-height; 
    display: none;

    cursor: pointer;

    .u-no-select;

    &.is-disabled {
      cursor: auto;
    }
  }

  // Dropdown arrow
  &__dropdown-arrow {

    position: absolute;
    right: @item-padding/2;
    top: 0;
    height: 100%;

    display: flex;
    align-items: center;

    .icon {
      .icon-triangle-down;
    }
  }

  &__dropdown.is-active &__dropdown-arrow {
    .icon {
      .icon-triangle-up;
    }
  }

  &__dropdown.is-disabled &__dropdown-arrow {
    display: none;
  }

  // If there's currently an item-state visible, move the dropdown icon to the left.
  &__item.is-incorrect &__dropdown-arrow {
    padding-right: @item-padding*1.25;
  }

  // If this device has a mouse, show the custom dropdown.
  //@media (hover: hover) {
  .no-touch &__dropdown {
    display: block;
  }
  //}

  // If the user focuses on the real select element (likely using a keyboard); hide the dropdown and show the real <select> dropdown
  // (accessibility consideration);
  &__select:focus + &__dropdown {
    display: none;
  }

  // Dropdown options
  // -----------------------------------------------------------------
  &__dropdown-options {

    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;

    display: none;
    background-color: @white;
  }

  // Ensure the dropdown always covers up everything behind it.
  &__item {position: relative;}
  &__item.is-active {z-index: 1;}
  &__dropdown.is-active {z-index: 1;}

  &__dropdown.is-active &__dropdown-options {
    display: block;
  }

  &__dropdown-option {
    
    // Selected
    &.is-selected {
      background-color: @visited;
      color: @visited-inverted;
    }
  }

  &__dropdown-trigger {
    &.is-correct {
      background-color: @validation-success;
    }

    &.is-incorrect {
      background-color: @validation-error;
    }
  }

  // Item states
  // ---------------------------------------------
  &__item-state {
    position: absolute;
    right: @item-padding/2;
    top: 0;
    height: 100%;

    display: flex;
    align-items: center;
  }
  &__item-icon {
    display: none;
  }
  &__item-incorrect-icon .icon {.icon-cross;}
  &__item-correct-icon .icon {.icon-tick;}

  &__item.is-incorrect &__item-incorrect-icon {display: block;}
  &__item.is-eliminated &__item-incorrect-icon {display: block;}
  &__item.is-correct &__item-correct-icon {display: block;}

  // Dropdown option icons.
  &__dropdown-option:not(.is-visited) &__item-icon {
    display: none;
  }
  &__dropdown-option.is-visited.is-correct &__item-correct-icon,
  &__dropdown-option.is-visited.is-incorrect &__item-incorrect-icon,
  &__dropdown-option.is-eliminated &__item-incorrect-icon {
    display: block;
  }

  // <select> item state (overlays).
  &__select {
    padding-right: @item-padding*3;
  }
  &__select-container &__item-state {
    right: @item-padding;
    pointer-events: none;
  }
}