// DD Img
// -----------------------

.ddimg {
  
  .style-circle {
    border-radius: 50%;
    overflow: hidden;
  }
  
  // Annotation target.
  &.is-annotation-target {
    opacity: 0;
    .transition(opacity @duration ease);

    &.is-active {
      opacity: 1;
    }
  }

  // Error handling
  // ------------------------------
  &.has-error &__img {
    min-height: @item-padding * 4;
    min-width: @item-padding * 4;
		height: 100%;
  }
}