// Use on .component elements
// --------------------------
// PDF mode: drop-shadow filters may prevent the text inside them being selectable. To fix this, a fallback is used. 
// FIXME: These mixins can't be used in the --pdf styles (due to the .mode-pdf .* selectors that the --pdf styles have).

.drop-shadow() {
  filter: drop-shadow(2px 4px 6px rgba(0,0,0,0.35));
  .mode-pdf & {
    filter: none;
    box-shadow: 2px 4px 6px rgba(0,0,0,0.35);
  }
}

// Use in rare occassions
// ----------------------
.drop-shadow-double() {
  filter: drop-shadow(2px 4px 6px rgba(0,0,0,0.75));
  .mode-pdf & {
    filter: none;
    box-shadow: 2px 4px 6px rgba(0,0,0,0.75);
  }
}

.drop-shadow-half() {
  //filter: drop-shadow(1px 2px 1px rgba(0,0,0,0.35));
  filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.35));
  .mode-pdf & {
    filter: none;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.35);
  }
}

.drop-shadow-item() {
  .drop-shadow-half();
}

.drop-shadow-tooltip() {
  filter: drop-shadow(2px 4px 2px rgba(0, 0, 0, 0.1));
  .mode-pdf & {
    filter: none;
    box-shadow: 2px 4px 2px rgba(0, 0, 0, 0.1);
  }
}

// FIXME: May be fully replaced by .drop-shadow-btn-img() later.
.drop-shadow-btn() {
  .drop-shadow-half();
}
.drop-shadow-btn-text() {
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.35);
}

