// bounceUpOnce animation
// --------------------------------------------------
.effect-bounceUpOnce() {
  .keyframes(bounceUpOnce; {
    0% { transform: translateY(0); }
    50% { transform: translateY(-0.5rem); }
    100% { transform: translateY(0); }
  });

  .animation(bounceUpOnce 0.25s 0s 1 normal ease none);
  // name, duration, delay, iteration count, direction, timing-function, fill mode
}

// bounceOnce animation
// --------------------------------------------------
.effect-bounceOnce() {
  .keyframes(bounceOnce; {
    0% { transform: scale(1); }
    50% { transform: scale(1.1); }
    100% { transform: scale(1); }
  });

  .animation(bounceOnce 0.5s 0s 1 normal ease none);
  // name, duration, delay, iteration count, direction, timing-function, fill mode
}



// validationError animation
// --------------------------------------------------
.effect-validationError() {
  .keyframes(validationError; {
    0%  { transform: translateX(-0rem); }
    20% { transform: translateX(0.25rem);}

    40% { transform: translateX(-0.25rem); }
    60% { transform: translateX(0.25rem); }

    80% { transform: translateX(-0.25rem); }
    100% { transform: translateX(0rem); }
  });

  .animation(validationError 0.25s 0s 1 normal linear none);
  // name, duration, delay, iteration count, direction, timing-function, fill mode
}

// validationSuccess animation
// --------------------------------------------------
.effect-validationSuccess() {
  .keyframes(validationSuccess; {
    0%  { transform: translateY(0rem); }
    50% { transform: translateY(-0.25rem);}
    100% { transform: translateY(0rem); }
  });

  .animation(validationSuccess 0.075s 0s 1 normal linear none);
  // name, duration, delay, iteration count, direction, timing-function, fill mode
}

// Refresh animation
// --------------------------------------------------
// Used in tryHtml to indicate that the output has been updated.
.effect-refresh() {
  .keyframes(refresh; {
    0%  { opacity: 1; }
    50% { opacity: 0;}
    100% { opacity: 1; }
  });

  .animation(refresh 0.5s 0s 1 normal linear none);
  // name, duration, delay, iteration count, direction, timing-function, fill mode
}

// Alert animation
// --------------------------------------------------
// Used whenever the users attention needs to be drawn to an element.
.effect-alert() {
  .keyframes(alert; {
    0%  { transform: scale(1); filter: none; }
    100% { transform: scale(1.2); filter: brightness(1.2) grayscale(80%);}
  });

  .animation(alert 0.15s 0s 6 alternate ease-out none);
  // name, duration, delay, iteration count, direction, timing-function, fill mode
}

// More intense alert animation (used by hotgraphicPlus finder mode).
.effect-alert-glow() {
  @a: lighten(@btn-color, 20%);
  @b: fade(@btn-color, 0);

  .keyframes(alert-glow; {
    0%  { transform: scale(1);
          filter: grayscale(80%)
            drop-shadow(1px 1px 1px @b) 
            drop-shadow(-1px -1px 1px @b) 
            drop-shadow(-1px 1px 1px @b) 
            drop-shadow(1px -1px 1px @b) 
            drop-shadow(1px 1px 0px @b) 
            drop-shadow(-1px -1px 0px @b) 
            drop-shadow(-1px 1px 0px @b) 
            drop-shadow(1px -1px 0px @b); }
    100% { transform: scale(1.2); 
           filter: grayscale(80%)
                   drop-shadow(1px 1px 1px @btn-color) 
                   drop-shadow(-1px -1px 1px @btn-color) 
                   drop-shadow(-1px 1px 1px @btn-color) 
                   drop-shadow(1px -1px 1px @btn-color) 
                   drop-shadow(1px 1px 0px @a) 
                   drop-shadow(-1px -1px 0px @a) 
                   drop-shadow(-1px 1px 0px @a) 
                   drop-shadow(1px -1px 0px @a);}
  });

  .animation(alert-glow 0.15s 0s 6 alternate ease-out none);
}