.accordion {

  &__item {
    margin-bottom: @item-margin;
    .drop-shadow-item();
  }

  &__item-btn {
    position: relative;
    padding: @item-padding;
    padding-right: @icon-size + (@item-padding * 2);
    text-align: left;

    background-color: @item-btn-color;
    color: @item-btn-color-inverted;
    
    border-radius: @item-border-radius;
    font-weight: @btn-weight;
    font-family: @btn-family;

    .no-touch &:not(.is-selected):hover {
      background-color: @item-color-hover;
      color: @item-color-inverted-hover;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }

    .dir-rtl & {
      padding-right: @item-padding;
      padding-left: @icon-size + (@item-padding * 2);
      text-align: right;
    }
  }

  &__item-btn.is-visited {
    background-color: @visited;
    color: @visited-inverted;
  }

  &__item-btn.is-selected {
    background-color: @item-color-selected;
    color: @item-color-inverted-selected;
  }

  &__item-icon {
    position: absolute;
    right: @item-padding;
    
    /*top: 50%;
    .transform(translateY(-50%));*/

    // Vertical centering modified to remove blur.
    top: 0;
    height: 100%;
    display: flex;
    align-items: center;

    .dir-rtl & {
      right: inherit;
      left: @item-padding;
    }
  }

  // Content
  // -----------------------------------------
  &__item-content {
    padding: @item-padding;
    background-color: @item-color;
    color: @item-color-inverted;
    border-radius: @item-border-radius;

    position: relative;

    // Expand the content to go under it's button's border radius.
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    padding-top: calc(@item-padding ~"+" @item-border-radius);
    margin-top: -@item-border-radius;
    z-index: -1;
  }

  &__item-body a {
    .link-text;
  }

  &.has-img-zoom &__item-image-container {
    .img-zoom();
  }

  // DD CUSTOMS:
  // -----------------------------------------------------

  // Image
 .style-circle {
      border-radius: 50%;
      overflow: hidden;
  }
}
