// Annotation--pdf
//--------------------
// Replace the (?) with [x].

// FIXME: Temp.
.pdf-annotation-after() {
  content: attr(data-annotation-num);
  pointer-events: none;

  // Appear like a <sup> element (but don't affect line height).
  position: relative;
  vertical-align: baseline;
  font-size: 0.6em;
  top: -0.5em;
  padding-left: 0.3em;
  padding-right: 0.3em;
  margin-left: 0.2em;

  background: @annotation-mark-border-color;
  color: @annotation-mark-color;
  font-family: @body-family;
}

.mode-pdf .annotation {
  
  // Hide the (?) icon
  // ------------------------------
  &__icon-container {
    display: none;
  }
  
  // Show square brackets with a number in them corresponding to the highlight.
  // ------------------------------
  &.has-icon::after {
    .pdf-annotation-after();
  }
}

.mode-pdf {
  // Show the same number next to the annotation target.
  .is-annotation-target::after {
    .pdf-annotation-after();
  }

  // Show the number next to tooltip-fallback elements.
  .tooltip-fallback__item::before {
    .pdf-annotation-after();
    margin-left: 0;
    margin-right: 0.4em;
    font-size: 0.8em;
    vertical-align: text-bottom;
    top: auto;
  }
}