// (DD) Table styles
// ---------------------------------------------
// Important information:
// - A lot of styles below are applied to both the <td> and <th> elements, AS WELL AS the <tr> element, even tho they will have no
//   effect on the latter. This is done on purpose, as it allows the "default" styles to be retrieved by custom styles by using
//   "inherit" on <td> and <th>.

table {

  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;

  background-color: @table-color;
  color: @table-color-inverted;
  border: @table-border-width @table-border-color double; // The outer table border should have priority over the cells inside it.

  // Add stripes to the rows by default.
  /*tr:nth-child(odd) {
    background-color: @table-color;
  }
  tr:nth-child(even) {
    background-color: mix(@table-color, @table-header-color, 90%);
  }*/

  tr, td, th {
    padding: @table-padding;
    border: @table-border-width @table-header-color solid;
    text-align: left;

    // Firefox bugfix:
    // ---------------
    // On Firefox, the background color can sometimes overlap the borders of the cells.
    // The following fixes it.
    background-clip: padding-box;
  }

  th {
    background-color: @table-header-color;
    color: @table-header-color-inverted;
    font-weight: @font-weight-bold;
    text-align: center;
  }
  
  td {
    vertical-align: top;
  }

  td, th {
    position: relative;

    &[data-invis] {
      visibility: visible;
      color: transparent;
      pointer-events: none;
      .u-no-select;

      & > * {
        visibility: hidden;
      }
    }
  }

  tr.theme-head th {
    background: @table-header-color;
    color: @table-header-color-inverted;
  }

  &.align-top {
    tbody {
      vertical-align: top;
    }
  }
  &.align-middle {
    tbody {
      vertical-align: middle;
    }
  }

  // Unique style: If there's an inputField in a table cell, have it fill the whole cell.
  .inputfield:only-child {
    position: absolute;
    top: -@table-border-width;
    bottom: -@table-border-width;
    left: 0;
    width: 100%;

    .inputfield__inner,
    .inputfield__input-container {
      height: 100%;
    }

    // Move the hint.
    .inputfield__overlay {
      top: 0;
      right: 0.25em;
    }

    .inputfield__placeholder {
      padding: 0 @table-padding;
      text-align: left;

      position: relative;
      top: @table-border-width;
      left: @table-border-width;

      box-sizing: border-box;
      
      // Align the placeholder text vertically inside of tables
      // (done due to the fact that the <input> element is forced to center its text vertically; so this prevents a mismatch).
      min-height: calc(100% ~"-" (@border-width*2));
      display: flex;
      align-items: center;
    }

    .inputfield__hint-icon .icon:before {
      font-size: 1.25rem;
    }

    .inputfield__input {
      padding: @table-padding;
      font-weight: inherit;
    }
  }

  // Prevent the changing outputfield from affecting the table width.
  .outputfield:only-child {
    position: absolute;
    width: calc(100% ~"-" (@table-padding*2));

    margin-left: -@outputfield-padding;

    .outputfield__inner {
      width: 100%;
    }

    .outputfield__content {
      width: auto;
    }
  }

  .markchain {
    z-index: 1;

    // Special rule for ghost markChains.
    &.theme-ghost .markchain__label {
      color: @annotation-mark-border-color;
      background: fade(@table-color, 50%);
  
      //font-family: @math-family;
      //font-size: 1.25em;
    }
  }

  // Base
  // -------------------------------------------
  // Return the table to its base style (TODO: WIP).
  &.base,   // Legacy; do not remove.
  &.theme-base {
    &, td, th, tr {
      border: 1px solid #252525;
      background: transparent;
    }
  }

  // FIXME: Remove "none" class ("ghost" is the new name being used for this).
  &.none,   // Legacy; do not remove.
  &.ghost,  // Legacy; do not remove.
  &.theme-ghost {
    &, td, th, tr {
      background: transparent;
      border: none;
      padding: 0;
    }
  }

  // Other themes
  // -------------------------------------------------
  
  // Frequency table
  &.theme-frequency {
    
    border-color: @blueDark;
    border-style: double;
    background: @white;

    tr,th,td {
      padding: @table-padding/2 @table-padding;
      border-bottom: none;
      border-top: none;
    }

    tr:first-child th {
      background: @table-header-color;
      color: @table-header-color-inverted;
      border-bottom: @table-border-width double @blueDark;
    }

    tr:nth-child(2) th {
      background: transparent;
      text-align: left;

      &.mark {
        background: @annotation-mark-color;
      }
    }

    tr:nth-child(2) {
      border-bottom: @table-border-width double @blueDark;
    }

    tr:last-child {
      border-top: @table-border-width double @blueDark;
      font-weight: bold;
    }

    .inputfield__placeholder {
      padding-top: @table-padding/2;
      padding-bottom: @table-padding/2;
    }
  }

  // Probability table
  &.theme-probability {

    @a: desaturate(lighten(@table-header-color, 12%),100%);

    tr {
      border-color: inherit;
    }

    border-color: desaturate(@table-border-color, 100%);
    
    &, tbody {
      padding: 0rem 0.25rem;
    }

    tr, th, td {
      //padding: 0.1rem 0.25rem;
      padding: 0rem 0.25rem;
      font-size: @body-size * 0.9;
      border-width: 0;
      text-align: left;

      border-color: inherit;
    }

    td:nth-child(1),
    td:nth-child(2),
    td:nth-child(3)
    {
      background-color: @a;
      font-weight: 500;
    }

    tr:first-child {
      border: @table-border-width solid blue;
      border-color: inherit;

      background-color: @a;
      font-weight: 500;

      td:nth-child(2) {
        border-right: @table-border-width solid blue;
        border-color: inherit;
      }

      td:nth-child(1),
      td:nth-child(2) {
        font-weight: normal;
      }
    }

    tr:not(:first-child) {
      td:nth-child(3) {
        border-right: @table-border-width solid blue;
        border-color: inherit;
      }
    }
  }
  
  // TODO: 
  &.theme-math {

    border-color: @blueDark;
  
    tr,th,td {
      padding: @table-padding;
    }

    td,th {
      position: relative;
      border: none;
      //border: @border-width transparent solid;
      border-width: 0;
    }

    tr {
      border-left: none;
      border-right: none;
    }
  
    tr:first-child th {
      background: @blueDark;
      color: @white;
    }
  
    tr:last-child td {
      border-top: @table-border-width @table-header-color solid;
    }
  }

  // Unique rules for table elements + marks/annotation targets
  // ----------------------------------------------------
  tr, td, th {

    &.mark {
      background: @annotation-mark-color;
      border-color: @annotation-mark-border-color;
      padding: inherit;

      // Firefox bugfix:
      // ---------------
      // On Firefox, the background color can sometimes overlap the borders of the cells.
      // The following fixes it.
      background-clip: padding-box;
    }

    &.mark.is-annotation-target {
      &.is-active {
        border-style: double; // Gives the marked border color priority over its neighbours.
      }

      &:not(.is-active) {
        background-color: inherit;
        border-color: inherit;
        padding: inherit; // This is why I set padding on the <tr> element above.
      }
    }

    // Normal (non annotation toggled) mark.
    &.mark:not(.is-annotation-target) {

      border-style: double; // Gives the marked border color priority over its neighbours.
      border-width: inherit;

      // Filter used because tables don't work with non-inset box-shadows consistently.
      filter: drop-shadow(-@table-border-width 0 0 @annotation-mark-border-color)
              drop-shadow(0 -@table-border-width 0 @annotation-mark-border-color)
              drop-shadow(@table-border-width 0 0 @annotation-mark-border-color)
              drop-shadow(0 @table-border-width 0 @annotation-mark-border-color);

      &.theme-1 {
        filter: drop-shadow(-@table-border-width 0 0 @mark-border-color-1)
                drop-shadow(0 -@table-border-width 0 @mark-border-color-1)
                drop-shadow(@table-border-width 0 0 @mark-border-color-1)
                drop-shadow(0 @table-border-width 0 @mark-border-color-1);
      }
      &.theme-2 {
        filter: drop-shadow(-@table-border-width 0 0 @mark-border-color-2)
                drop-shadow(0 -@table-border-width 0 @mark-border-color-2)
                drop-shadow(@table-border-width 0 0 @mark-border-color-2)
                drop-shadow(0 @table-border-width 0 @mark-border-color-2);
      }
      &.theme-3 {
        filter: drop-shadow(-@table-border-width 0 0 @mark-border-color-3)
                drop-shadow(0 -@table-border-width 0 @mark-border-color-3)
                drop-shadow(@table-border-width 0 0 @mark-border-color-3)
                drop-shadow(0 @table-border-width 0 @mark-border-color-3);
      }
    }
    &.is-se-mark {
      
      border-width: inherit !important;
      filter: drop-shadow(-@table-border-width 0 0 @annotation-mark-border-color)
              drop-shadow(0 -@table-border-width 0 @annotation-mark-border-color)
              drop-shadow(@table-border-width 0 0 @annotation-mark-border-color)
              drop-shadow(0 @table-border-width 0 @annotation-mark-border-color);
    }
  }
  // FIXME: td elements which with mark transitions need a background set for the filter to transition nicely.
  td[data-stickyelement-mark-idx] {
    background-color: @table-color;
  }

  tr.mark.is-annotation-target.is-active > * {
    border-color: inherit;
  }

  // ScaleDown Figure rules
  // -------------------------------------------
  // If the {{#figure}} containing this table scales down, there's a chance it may make the table borders disappear.
  // EDIT: Nevermind, there seems to be no way to fix this.

  // Small
  // -------------------------------------------
  // When a table is too large, add this.
  &.theme-small {
    tr, td, th {
      padding: @table-padding/2;
    }
  }

  // EPUB
  // -------------------------------------------------
  // When inside of a .figure element in an EPUB (specifically, Desktop Bookshelf), parts of the table border sometimes don't get rendered.
  // This should fix it.
  .figure & {
    transform: translateZ(0);
  }

  // (Programming) Array
	&.theme-array {

		border: none;
		width: auto;
		margin: auto;

		tr {
			border: none;
		}

    table, tr {
      border: none;
    }

		th, td {
			text-align: center;
			//border: @table-border-color @table-border-width solid;
			width: 5rem;
      border-color: @table-border-color;

      &.u-visibility-hidden {
        border: none;
      }
		}

    th {
      background-color: @table-border-color;
      color: @table-color;
    }

		th.is-label {
			text-align: left;
			background: transparent;
			color: @table-color-inverted;
			border: none;
			text-align: right;
		}

		th.theme-subheader,
		td {
			font-family: @code-family;
			font-size: 1.125em;

			vertical-align: middle;
		}
	}
}
