// ExtraLearning (PDF)
// ------------------------------
// Have the ExtraLearning button now resemble the popup it reveals when in its dormant state.

@extralearning-popup-icon-size-pdf: @extralearning-popup-icon-size/2;

.mode-pdf .extralearning {
    
    // Add a bit of extra margin.
    display: block;
    margin-top: @component-body-margin;
    page-break-inside: avoid;

    // Increase the size of the icon
    // -----------------------------
    &__icon {
        width: @extralearning-popup-icon-size-pdf;
        height: @extralearning-popup-icon-size-pdf;
        z-index: 1;
    }

    &-content {
        padding: @notify-content-padding/2;
        margin-left: -@extralearning-popup-icon-size-pdf/2;
        padding-left: (@notify-content-padding + @extralearning-popup-icon-size-pdf)/2;

        color: @extralearning-color-inverted;
        background-color: @extralearning-color;
    }

    // Add the title to the popup, and reduce its size.
    &-content__title {
        .item-title();
        margin-bottom: @item-title-margin/2;
        font-size: @body-size;
    }
}

.extralearning-content {
    .drop-shadow-half(); // FIXME: Has to be declared outside of a .pdf-mode selector.
}