.textinputplus {
  &__item {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
  }

  &__item-prefix-container,
  &__item-suffix-container {
    /*width: 25%;
    max-width: 15%;*/
    flex-grow: 1;
    flex-basis: 50%; // Prevents the item-textbox flex-basis from overpowering the prefix too much.
  }

  &__item-textbox-container {
    width: inherit;

    // Override {{#inputField}}s default styling.
    .inputfield {
      width: 100%;
    }
    .inputfield__placeholder {
      min-width: 0 !important;
      text-align: left;
    }
  }

  &__item-textbox {
    width: inherit;

    /*
     * Hide 'X' icon for IE11 and Edge 12+
     * Issue:https://github.com/adaptlearning/adapt_framework/issues/1913
     */
    &::-ms-clear {
      display: none;
    }
  }

  &__item-icon {
    display: none;
  }

  // Text input item icons
  // --------------------------------------------------
  &__item-correct-icon .icon {
    .icon-tick;
  }

  &__item-incorrect-icon .icon {
    .icon-cross;
  }

  // Class to show the users selection
  // --------------------------------------------------
  &__widget.show-user-answer {
    // Whenever the user checks the wrong answer,
    // or whenever they type in the same wrong answer that has already been checked,
    // the textinput container will gain the .is-incorrect class.
    .is-correct .textinputplus__item-correct-icon,
    .is-incorrect .textinputplus__item-incorrect-icon {
      display: block;
    }
  }


  // Class to show the correct answers
  // --------------------------------------------------
  &__widget.show-correct-answer {
    .is-correct .textinputplus__item-correct-icon,
    .is-incorrect .textinputplus__item-correct-icon {
      display: block;
    }
  }
}
