// StickyElement
// ------------------------------

.stickyelement {

	// Margins
	// ------------------------------------
	// Use the same margins as the figure elements.
	margin: @figure-margin 0;
	&:first-child {margin-top: 0;}
	&:last-child {margin-bottom: 0;}

	// Hide
	// ------------------------------------
	.is-se-hide-transition {
		.transition(opacity @duration ease);
	}
	.is-se-hide {
		opacity: 0;
		pointer-events: none;
	}

	// SlideLeft
	// ------------------------------------
	.is-se-slideleft-transition {
		.transition(transform @duration*2 ease, opacity @duration*2 ease;);
	}
	[data-stickyelement-slideleft-idx] { // Inactive
		transform: translateX(100%);
		opacity: 0;
	}
	.is-se-slideleft { // Active
		transform: translateX(0%);
		opacity: 1;
	}

	// SlideDown
	// ------------------------------------
	/*.is-se-slidedown {
		max-height: 0;
		overflow: hidden;
	}*/

	// Mark
	// ------------------------------------
	.is-se-mark-transition {
		.transition(margin @duration ease, padding @duration ease, border-width @duration ease, background-color @duration ease;);
	}
	// FIXME: The following needs to select anything that isn't a table element.
	/*div.is-se-mark,
	p.is-se-mark,
	span.is-se-mark,
	blockquote.is-se-mark,
	strong.is-se-mark,
	em.is-se-mark*/
	.is-se-mark:not(tr):not(td):not(th) {
		.mark();
	}
	tbody.is-se-mark,
	tr.is-se-mark,
	td.is-se-mark,
	th.is-se-mark {
		.mark-colors-inverted();
	}
	// Unique color in blockquotes (may not be a permanent thing?).
	blockquote .is-se-mark {
		background: @blockquote-mark-color;
		border-color: @blockquote-mark-border-color;
		
		-webkit-box-decoration-break: clone;
		box-decoration-break: clone;
	}

	// Don't affect layout.
	div.is-se-mark {
		/*margin-right: calc(-@mark-padding ~"-" @mark-border-width);
		margin-left: calc(-@mark-padding ~"-" @mark-border-width);*/

		position: relative;
		margin-right: -@mark-padding;
		margin-left: -@mark-padding;
		border: none !important; // FIXME: Needed to counter the .is-se-mark:not() selector above.

		&::after {
			content: "";
			pointer-events: none;
			position: absolute;

			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			border-width: @mark-border-width;
			border-color: @mark-border-color;
			border-style: solid;
		}
	}

	// Fold
	// ------------------------------------
	.is-se-fold-transition {
		//.transition(opacity @duration ease);
		.effect-bounceUpOnce();
	}
	[data-stickyelement-fold-idx] {
		//.transition(opacity @duration ease);
		overflow: hidden;
	}
	.is-se-fold {
		visibility: hidden;
		height: 0; // FIXME: temporary way of getting this element to take up no vertical height.
		//visibility: hidden;
		pointer-events: none;
	}
}