// HotgraphicPlus
// -----------------------------------------

.hotgraphicplus {
  
  // Items
  // ---------------------------------
  &__tile {
    .drop-shadow-btn-img();
  }

  &__widget:not(.is-finder) &__pin {
    .drop-shadow-btn-img();
  }

  // Finder mode
  // ---------------------------------

  // Counter
  &__counter {
    color: @btn-color;
    .transition(color (@duration/2) ease;);
    
    .u-no-select;

    // Give the counter a border similar to .btn-circle
    border: none;
    &::before {
      position: absolute;
      content: "";
      border-radius: inherit;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: @border-width*3 currentColor solid;

      .transition(all (@duration/2) ease;);
    }
    .no-touch &:hover {

      color: @btn-color-hover;

      &::before {
        top: -0.5rem;
        left: -0.5rem;
        right: -0.5rem;
        bottom: -0.5rem;
      }
    }

    // Complete
    &.is-complete {
      color: @validation-success;

      .no-touch &:hover {
        color: @validation-success-hover;
      }
    }
  }

  &__counter-num {
    //font-size: @component-title-size;
    .component-title();
    line-height: 1.2em;
    margin-top: -0.18em;
  }

  &__counter-hint {
    .component-sub-title();
    color: @btn-color; // Don't make the hint text brighter on hover.
  }

  &.is-complete &__counter-hint {
    color: @validation-success;
  }

  &__counter-suffix {
    .item-title();
  }

  // Small counter
  &__counter.col-maxwidth-1 {
    @media (max-width: @device-width-small) {

      .hotgraphicplus__counter-num {
        .component-sub-title();
      }
      .hotgraphicplus__counter-suffix {
        font-size: @body-size * 0.7;
        font-weight: normal;
        margin-top: -0.4em;
      }
      .hotgraphicplus__counter-hint {
        .item-title();
      }
    }
  }

  // Hints
  .is-finder-hint {
    border: none; // Override this.
    .effect-alert-glow();
  }

  // Items
  &__pin.is-finder-visited {

    @v1: darken(@validation-success, 8%);
    @v2: @validation-success;

    filter: drop-shadow(1px 1px 1px @v1)
            drop-shadow(-1px -1px 1px @v1)
            drop-shadow(-1px 1px 1px @v1)
            drop-shadow(1px -1px 1px @v1)
    
            drop-shadow(1px 1px 0px @v2)
            drop-shadow(-1px -1px 0px @v2)
            drop-shadow(-1px 1px 0px @v2)
            drop-shadow(1px -1px 0px @v2);

    //filter: sepia(100%) saturate(300%) brightness(70%) hue-rotate(80deg);
  }

  // Tabs
  // --------------------------------------
  &__tabs {
    border-radius: @border-radius;
    .drop-shadow-half();
  }
}
