// Stylize components that get revealed by the activator button click.
// -------------------------------------------------------------------

.activator {
  
  // Have a slightly reduced margin for the activator button.
  // (The same margin as body text).
  &__widget {
    margin-top: -(@component-body-margin - @body-text-margin);
  }
}

.component.is-activator-target {

  // Text component (default stylization)
  // ------------------------------------
  &.text[data-activator-activation="default"] {
    background-color: @activator-target-color;
    padding: @item-padding;
    //margin-left: -@item-padding;
    //margin-right: -@item-padding;
    //box-sizing: content-box;

    .drop-shadow();
  }

  // Activator transitions (custom)
  // ------------------------------
  [data-activator-transition="show"] {
    pointer-events: none;
    opacity: 0;

    .transition(opacity @duration ease);
  }

  // Active
  /*&.is-activator-active {

    [data-activator-transition="show"] {
      pointer-events: all;
      opacity: 1;
    }


  }*/
}