// CodeAnim
// ------------------------------
// Much of this template is created using the adapt-codeBox extension.
// You'll find many styles for the code box and console box there.

.codeanim {

    // Stylize the playbackControls
    // ----------------------------
    &.is-animated &__code-container {
        display: flex;
        flex-direction: column;
    }

    // Inspector
    // ----------------------------
    &__inspector-inner {
        display: table;
        table-layout: fixed;
        width: 100%;
    }
    &__inspector-group {
        display: table-row-group;
    }
    &__inspector-row {
        display: table-row;
    }
    &__inspector-cell {
        display: table-cell;
        vertical-align: top;
    }
    &__inspector-container {
        border: 1px solid @codebox-color-inverted;
    }

    // Inspector val spacer
	// ------------------------------------
	// Display all possible values this variable will take in a single row.
	// This will make the parent element's height = to the greatest height required.
	&__inspector-cell-var-val {
		width: 100%;
		flex-shrink: 0;
	}
	&__inspector-cell-var-val-spacer {
		visibility: hidden;
	}
    &__inspector-cell-value-inner {
        display: flex;
        max-width: 100%;
        overflow: hidden;
    }

    // Animation
    // ---------------------------

    // This container is only added if there is an inspector window.
    &__anim-container {
        display: flex;
        width: 100%;
    }

    // Codebox line highlight.
    // Tweak default mark styles a bit for a clearer line highlight.
    .codebox__line.is-codeanim-mark {
        background-color: @mark-color;
        border: @mark-border-width @mark-border-color solid;
        
        @a: @code-padding/2;

        padding-left: @a;
        padding-right: @a;
        
        margin: -@mark-border-width calc((-@a) ~"-" 1px);
    }

    // Console
    // TODO: Add support for inline prints (if necessary).
    &__console-print {
        display: block;
    }

    // Modify the layout
}
