// Global page rules
// ---------------------------------
@_pdf-page-margin:  7mm; 
@pdf-page-margin:   unit(((@_pdf-page-margin * 3.779527559px) / 16px)) * 1rem; // Get the page margin in REM.

@page
{ 
  size: A4 portrait; // All Adapt units should use this layout for all/most of their pages (landscape may be allowed if needed).
  
  /* this affects the margin in the printer settings (if set to default) */ 
  margin: @pdf-page-margin; // Average margin for printer.
}

.mode-pdf {

  // Page margins
  // -------------------------------------
  .page__header-inner,
  .block__inner {
    padding-left: (@block-padding-hoz * 2.5) - @pdf-page-margin;
    padding-right: (@block-padding-hoz * 2.5) - @pdf-page-margin;
    padding-top: (@block-padding-ver / 1.5) - @pdf-page-margin;
    padding-bottom: (@block-padding-ver / 1.5) - @pdf-page-margin;
  }
  
  // Overview
  // -------------------------------------
  .page__overview {

    // Extend the overview element to the bottom of the page.
    height: calc(100vh ~"-" @page-header-height-large);
  }
}

p, span, div {
  // Prevent paragraphs leaving only a few lines of text at the start/end of a page.
  widows: 2;
  orphans: 2;
}

// Temp (remove logo)
// -------------------------------------
.page::after {
  content: none;
}


// Debug
// -------------------------------------
// Show a ruler at the bottom of the page that will show the size the browser needs to be set to to get an
// "accurate" preview of how the page will look when previewed.
// This is purely for the development; the end user will never see it.
@media screen
{
  @debug-ruler-size: 2px;
  @debug-ruler-width: 800px;

  .mode-pdf .mode-pdf__debug-ruler {
    position: fixed;
    bottom: @debug-ruler-size*2;
    left: 0;
    height: @debug-ruler-size;
    width: @debug-ruler-width;
    max-width: 100%;

    background-color: red;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: inherit;
      width: @debug-ruler-size;
      height: @debug-ruler-size*3;
      top: -@debug-ruler-size;
    }

    &::after {right: 0;}
    &::before {left: 0;}
  }

  // Turn the ruler green when the device width is appropriate
  @media (max-width: (@debug-ruler-width + 20px)) and (min-width: (@debug-ruler-width)) {
    .mode-pdf .mode-pdf__debug-ruler {
      background-color: rgba(0, 128, 0, 0.5);
    }
  }
}

// Print mode
// ---------------------------
@media print
{
  .mode-pdf .mode-pdf__debug-ruler {
    display: none;
  }
}