// HotgraphicFlip--pdf
//--------------------
// Have the cards show both sides at once.

.mode-pdf .hotgraphicflip {
  
  // Combine both sides of the card into one
  // ------------------------------------------------
  &__item-inner {
    background-color: @item-color;
  }
  &__item-front,
  &__item-back {
    height: auto;
    background-color: transparent;
  }
  &__item-front {position: relative;}
  &__item-back {transform: none;}

  // Consistent image dimensions (4:3)
  // ------------------------------------------------
  &__item-front-inner {
    padding-bottom: 75%;
  }
  &__image {
    position: absolute;
  }

  // Move the shadow
  // ------------------------------------------------
  &__item {
    box-shadow: none;
  }
  &__item-inner {
    .drop-shadow-btn();
  }
  
  // Hide the "flip back" button
  // ------------------------------------------------
  &__item-btn {
    display: none;
  }
}