// DD Menu
// ------------------------------------------------------
// Menu custom tailored to our specifications; made (mostly) from the ground up while using Adapt's code PageLevelProgress extension
// as a reference.
//
// Styles for the menu + the accordion (but not its content).

@sidemenu-duration: @duration * 2.5;
@sidemenu-padding: @btn-padding;

// Compress the content on active.
// ------------------------------------------------------
// Compress the content if there is room for it.
// (Allow both the sideMenu and the content to be viewed at the same time).
#wrapper {

    @d: @device-width-large + @drawer-width;
    @media (min-width: @d) {

        .transition(margin-left @sidemenu-duration ease);

        &.is-sidemenu-active {
            margin-left: @drawer-width;
        }
    }
}

.sidemenu {

    z-index: 99;
    //-webkit-overflow-scrolling: touch;

    // Menu
    // ------------------------------------------------------
    &__menu {
        position: fixed;
        width: @drawer-width;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 99;

        transform: translateX(-100%);
        .transition(transform @sidemenu-duration ease);

        display: block;
        background-color: white;

        overscroll-behavior: contain;
        -webkit-overflow-scrolling: touch;

        // Take up full width on phones.
        @media (max-width: @device-width-small) {
            width: 100vw;
        }
    }

    &__menu-inner {
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    &.is-active .sidemenu__menu {
        transform: translateX(0);
    }

    &__shadow {
        pointer-events: none;
        .transition(opacity @sidemenu-duration ease);
    }

    @d: @device-width-large + @drawer-width;
    @media (max-width: @d) {
        &.is-active .sidemenu__shadow {
            opacity: 1;
            pointer-events: all;
        }
    }

    // Header
    // -----------------------------------
    &__header {
        display: flex;
        align-items: flex-start;
        flex: 0 0 auto;
    }
    
    &__header-title {

        color: @drawer;
        padding: @sidemenu-padding;
        padding-right: 0;
        
        // FIXME: TEMP.
        .item-title();
        font-size: 1.5rem;
        flex-grow: 1;
    }

    &__btn {
        flex: 0 0 auto;
    }

    // Accordion
    // ---------------------------------------------
    &__accordion {
        flex-grow: 1;
        overflow: hidden;
        
        // Flex will be temporarily disabled while the jQuery slide animation plays out.
        display: flex;
        flex-direction: column;
    }

    // Accordion headers
    // ---------------------------------------------
    &__accordion-item-btn {

        display: block;
        flex-shrink: 0;
        flex-grow: 0;

        background: @drawer;
        color: @drawer-inverted;

        width: 100%;
        padding: @sidemenu-padding;

        text-align: left;
        .button-text;
        font-weight: normal !important; // FIXME:

        .u-no-select;

        border-bottom: 1px solid transparent; // Adds an invisible border-bottom to the last button (fixes flickering caused by unever heights otherwise).
        &:not(:last-of-type) {
            border-bottom: 1px solid @drawer-inverted;
        }
    }

    // Accordion content
    // ---------------------------------------------
    &__accordion-item-content {

        display: none;
        overflow: hidden;
        position: relative;
        
        flex-grow: 1;
        flex-shrink: 1;
        
        max-height: 0; // JS will modify this value.
        .transition(max-height @duration*2 ease);

        &.is-active {
            //flex-shrink: 0;
        }
    }
}