// DD Figure
// --------------------------------
.figure {
  
    position: relative;

    &.is-column &__inner {
        flex-direction: column;
        align-items: center;

        .figure__item {
            margin-left: auto;
            margin-right: auto;
        }
    }

    // When on small devices; single-column, centered horizontally.
    &__inner {
        position: static;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // Unless direction="wrap" is used.
    &.is-wrap &__inner {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }


    // Large view
    // -----------------------------------------
    @media (min-width: @device-width-medium) {

        // Warning: don't make this relative.
        &__inner {
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            align-items: stretch;

            // Wrap items if necessary.
            flex-wrap: wrap;
        }

        // options.column
        // FIXME: !important being used.
        &.is-column &__inner {
            flex-wrap: nowrap !important;
            margin: 0 !important;
            
            .figure__item {
                margin-left: 0 !important;
                margin-right: 0 !important;
            }
        }

        // options.reverse
        &.is-reverse &__inner {
            flex-direction: row-reverse;
        }
        // options.column + options.reverse
        &.is-reverse.is-column &__inner {
            flex-direction: column-reverse;
        }
    }

    // No wrap (options.wrap === false)
    // -----------------------------------------
    // FIXME: !important being used.
    &.no-wrap &__inner {
        flex-wrap: nowrap !important;
    }

    // Vertical alignment
    // -----------------------------------------
    &.align-center &__inner {
        align-items: center;
    }
    &.align-stretch {
        display: flex;
    }
    &.align-stretch &__inner {
        align-items: stretch;
    }

    // Horizontal alignment
    // -----------------------------------------
    &.justify-center &__inner {
        justify-content: center;

        // FIXME: Need to modify .col-maxwidth items for this to work.
        & > .figure__item.col-maxwidth {
            margin-left: 0;
            margin-right: 0;
        }
    }

    // Items
    // -----------------------------------------
    // Behave similarly to component.is-left + component.is-right & the componentRow extension.
    &__item {
        position: relative;
        max-width: 100%;

        // Fallback: sometimes certain elements (tables) can be too wide on some devices.
        // This lets the user have a way to fully view them.
        /*overflow-x: auto;
        overflow-y: hidden;*/

        &.has-overflow-scrolling &-inner {
            overflow-x: auto;
            overflow-y: hidden;
        }

        // Default width if no "sizing" property is given.
        &:only-child {
            width: 100%;
        }

        // Don't shrink if auto width explicitly set.
        &.col-auto {
            flex-shrink: 0;
        }
    }

    &__item.is-stack &__item-inner {
        position: relative;
        display: flex;

        & > * {
            width: 100%;
            &:nth-child(n+2) {
                margin-left: -100%;
            }
        }
    }

    // Caption
    // -----------------------------------------------
    .figure__caption {
        text-align: center;

        margin-top: @attribution-margin;
        .attribution-text();

        opacity: @attribution-opacity;
    }

    // FigureText item.
    // -----------------------------------------------
    // Makes normal text inside of it large. 
    .figure__text {
        font-size: @item-title-size;
        width: auto;
        
        // Center the figure text horizontally at all times.
        //display: flex;
        //justify-content: center;
        
        .figure__text-inner {
            display: inline-block;
        }

        .figure__text-inner > code {
            display: block;
        }
    }
    
    // Scaledown.
    // -----------------------------------------------

    &__item.is-transformed {
        backface-visibility: hidden;
        -webkit-font-smoothing: subpixel-antialiased; // Will only work on iOS devices.
    }

    &__item.has-scaledown &__item-inner {

        min-width: 100%;
        display: inline-block; // Very important. Ensures this element fully wraps the contents.

        transform-origin: top left;
        transform: translateZ(0);
    }

    // Lightbox icon.
    // --------------

    // Show a magnifying glass to indicate that this figure has a lightbox.
    &__lightbox-icon {
        position: absolute;
        top: @item-margin;
        right: @item-margin;

        &::before {
            font-size: 3rem;
        }

        display: none; // Hide unless enabled.
    }

    &__item.lightbox-active {
        cursor: zoom-in;
        .u-no-select;
    }

    &__item.lightbox-active &__lightbox-icon {
        display: block;
    }
}