.graphic {
  &.has-img-zoom &__image-container {
    .img-zoom();
  }

  &__image-container {
    margin: auto;
  }

  &__image-container.style-circle,
  &__image.style-circle {
    border-radius: 50%;
    overflow: hidden;
  }

  &__image {
    margin: auto;
  }

  // If inside of a component row that has a title, add additional padding.
  @media (max-width: @device-width-small) {
    .componentrow-has-title & .img-only {
      padding-top: @component-body-margin / 2;
    }
  }

  // Give .jpg and .png images auto width.
  &__image[src$=".png"], &__image[src$=".jpg"] {
    min-width: 0;
  }
}
