// .sizing-grid classes
// ---------------------------
// Arranges .col-* items in a grid element that will handle wrapping in a consistent manner.
// Used by hotgraphicmini. TODO: Apply to HotgraphicPlus and HotgraphicFlip.

.sizing-grid {

  @column-gap: @item-margin*3;

  display: grid;
  justify-items: center;
  column-gap: @column-gap;
  row-gap: @item-margin*2;

  // Set the maximum number of columns
  // --------------------------------------------------
  .maxcols-loop(@i) when (@i <= 12) {
    &.sg-maxcols-@{i} {
      @span: 2;
      
      & > * {
        grid-column: span @span;
      }

      @c: (@device-width-medium - (@column-gap * (@i - 1)))/(@i*@span*1.1);
      grid-template-columns: repeat(auto-fit, minmax(@c, 1fr));
      // Sharply increase grid sizing on medium devices to prevent items being orphaned on their own row.
      @media (max-width: @device-width-large) {grid-template-columns: repeat(auto-fit, minmax(@c*1.5, 1fr));}
      // Revert if the screen is small enough to have it not matter.
      @media (max-width: @device-width-small) {grid-template-columns: repeat(auto-fit, minmax(@c, 1fr));}
    }
    .maxcols-loop(@i + 1);
  }
  .maxcols-loop(1);

  // Orphan classes (these are added to the first child of the last row with JavaScript in order to center that row horizontally).
  // The number == columnCount - (itemCount % columnCount).
  .sg-orphan-0  {} // Do nothing if an even division.
  .sg-orphan-1  {grid-column: 2 / span 2;}
  .sg-orphan-2  {grid-column: 3 / span 2;}
  .sg-orphan-3  {grid-column: 4 / span 2;}
  .sg-orphan-4  {grid-column: 5 / span 2;}
  .sg-orphan-5  {grid-column: 6 / span 2;}
  .sg-orphan-6  {grid-column: 7 / span 2;}
  .sg-orphan-7  {grid-column: 8 / span 2;}
  .sg-orphan-8  {grid-column: 9 / span 2;}
  .sg-orphan-9  {grid-column: 10 / span 2;}
  .sg-orphan-10 {grid-column: 11 / span 2;}
  .sg-orphan-11 {grid-column: 12 / span 2;}

  // Set the sizing for the grid items.
  // --------------------------------------------------
  .col-loop(@i) when (@i <= 12) {

    &.sg-cols-@{i} .sg-cols-item {

      // Take up the full width of the grid cell, and center the contents inside.
      width: 100%;
      max-width: (@i/12*@device-width-medium);
    }

    // Min height.
    &.sg-minheight-@{i} .sg-cols-item {
      min-height: (@i/12*@device-width-medium);
    }

    .col-loop(@i + 1);
  }
  .col-loop(1);
}