.tabs {

  &__widget {
    .drop-shadow-item();
  }
    
  // Tabs
  // --------------------------------------
  &__nav-item-btn {

    border-top-left-radius: @item-border-radius;
    border-top-right-radius: @item-border-radius;
    
    background-color: @item-btn-color;
    color: @item-btn-color-inverted;
    
    &.is-selected {
      color: @item-color-inverted-selected;
      z-index: 1; // Cover up the drop shadow from the content.
    }

    // Drop a shadow over the bottom of the button (as if the content was casting it on it).
    &:not(.is-selected) {
      box-shadow: inset 0 -1px 2px rgba(0, 0, 0, 0.15);
    }
    
    &:not(:last-child) {
      margin-right: @item-margin;
    }

    // Reduce padding on smaller devices (make the vertically stacked tabs take up less room).
    // FIXME: Style inconsistency (accordion tabs don't do this).
    @media (max-width: @device-width-medium) {
      padding-bottom: @item-padding/1.5;
      padding-top: @item-padding/1.5;
    }
  }

  // Content
  // --------------------------------------
  &__content {
    box-shadow: none;
    background-color: @item-color;
    //.drop-shadow-item();

    border-bottom-left-radius: @item-border-radius;
    border-bottom-right-radius: @item-border-radius;
  }

  // adapt-sizing compatibility
  // --------------------------------------
  &__content-item-graphic.col-auto {
    flex-shrink: 0;
    margin: auto;
  }

  // Fix for stickyElement extension:
  // --------------------------------
  // Ensure that hidden tabs are still in the same location while hidden.
  &__content-inner {
    position: relative;
  }
  
  &__content-item:not(.is-active) {
    position: absolute;
    top: @item-padding;
    left: @item-padding;
    right: @item-padding;
  }

  // Image styles
  // --------------------------------------
 .style-circle {
    border-radius: 50%;
    overflow: hidden;
  }

  // Improve tab sizing to reduce text wrapping.
  &__nav-inner {
    justify-content: stretch;
  }
  &__nav-item-btn {
    flex-grow: 1;
    //width: auto !important;
  }

  // Ensure the text in the tabs always aligns to the top (if the other tab text wraps).
  &__nav-item-btn-inner {
    height: 100%;
  }
}