@mcqcode-line-padding: @item-margin / 4;

.mcqcode {

  // Codebox
  // -----------------------------------------
  &__items-container {
    border: @codebox-border-color @border-width solid; // Same border as codebox.
    background-color: @codebox-color;
    overflow-x: auto;
  }

  // Item codeboxes
  // -----------------------------------------
  // Prevent the codeboxes scrolling, and modify their appearance.
  .mcqcode__codebox {
    overflow-x: visible;
  }
  .mcqcode__codebox .codebox__content {
    border: none;
    background: transparent;
  }
  .mcqcode__codebox .codebox__content-inner {
    //padding: @item-padding/2 @item-padding @item-padding/2 (@mcqcode-btn-size + @item-padding);
    padding-top: @mcqcode-line-padding;
    padding-bottom: @mcqcode-line-padding;
  }

  // Prevent the user from selecting the code while clicking on it.
  &__item-content {
    .u-no-select;
  }

  // Input
  // -----------------------------------------
  &__item-input-container {
    padding-left: @item-padding;
    padding-right: @item-padding;
  }

  // DD customs
  .mcq-item(@smallIcon: true);

  &__item-state {

    // Add some spacing between the checkbox and the line.
    padding-left: @mcqcode-line-padding*2;
    padding-right: @mcqcode-line-padding*2;

    // Appear over the codebox if it scrolls horizontally.
    position: sticky;
    left: 0;
    z-index: 1;

    // Give it a background (so that the code can't be seen behind it after scrolling).
    background-color: inherit;
  }

  // Neutral
  // -----------------------------------------
  &__item-label {
    background-color: @codebox-color;
  }

  &__item:first-child &__item-label {
    padding-top: @mcqcode-line-padding*2;
  }
  &__item:last-child &__item-label {
    padding-bottom: @mcqcode-line-padding*2;
  }

  // Selected
  // -----------------------------------------
  &__item-label.is-selected {
    background-color: mix(@btn-color, @codebox-color, 7.5%);
  }

  // Hover
  // -----------------------------------------
  .no-touch &__item-label:not(.is-disabled):hover {
    background-color: mix(@btn-color, @codebox-color, 15%);
  }

  // Incorrect
  // -----------------------------------------
  &__item-label.is-incorrect {

    // Turn red while incorrectly selected.
    &.is-selected {
      background-color: mix(@validation-error, @codebox-color, 7.5%);
    }

    // Turn the selection hover red if the user mouses over an incorrect item again.
    .no-touch &:not(.is-disabled):not(.is-selected):hover {
      background-color: mix(@validation-error, @codebox-color, 10%);
    }
  }

  // Correct
  // -----------------------------------------
  &__item-label.is-correct {

    // Turn green when correctly selected.
    &.is-selected {
      background-color: mix(@validation-success, @codebox-color, 7.5%);
      border-top: mix(@validation-success, @codebox-border-color, 15%) @border-width solid;
      border-bottom: mix(@validation-success, @codebox-border-color, 15%) @border-width solid;
    }
  }

  .mcqcode__codebox-default {
    position: relative;
  }

  .mcqcode__codebox-default span {
    .transition(color @duration*2 ease);
  }

  .mcqcode__codebox-default::after {
    content: "";
    width: 0;
    .transition(width @duration*2 ease);
  }

  // Strike through the incorrect value.
  .mcqcode__codebox-incorrect span {
    color: mix(@codebox-color-inverted, @codebox-color, 50%);
  }
  .mcqcode__codebox-incorrect::after {
    content: "";
    position: absolute;
    height: 1px;
    width: calc(100% ~"-" 1.75em);
    left: 1.25em;
    top: 13px; // FIXME: Hardcoded for our font size.

    background-color: fade(mix(@codebox-color-inverted, @codebox-color, 75%), 75%);
  }

  .mcqcode__codebox-incorrect .codebox__inner {
    padding-right: 0;
  }

  // Console box
	// ------------------------------------
  &__console {
    margin-top: @figure-padding-ver/2;
  }

  // Debug only
	// ------------------------------------
	.mode-debug & {

		counter-reset: mcqCodeLineNum;

		.mcqcode__item::before {
			
			position: absolute;
			right: 0.25rem;
			top: 0;

      font-size: 0.75rem;

			color: rgba(129, 12, 129, 0.35);
			counter-increment: mcqCodeLineNum;
			content: counter(mcqCodeLineNum);

      z-index: 2;
		}
	}
}
