// HorgraphicPlusPopup
// -------------------
// The graphic column uses position: sticky to stay in place if the content column needs to scroll.
// TODO: Add support for non-images being used for the graphics when needed.

@hotgraphicplus-popup-transition: @duration * 2;

.notify__popup.hotgraphicplus {
  background-color: transparent;
  opacity: 1 !important;

  // Allow clicking through the transparent parts of notify.
  //pointer-events: none;
}

// Notify container
// ----------------------------------------------------
.notify__popup.hotgraphicplus {

  .notify__popup-inner {
    padding: 0;
  }
}

// Main
// -------------------------------------------------
.hotgraphicplus-popup {
  
  position: relative;
  // Don't show the popup until its inner image has loaded (if there is one).
  &:not(.is-ready) {
    visibility: hidden;
  }

  &__item {
    padding-left: @notify-padding-hoz;
    padding-right: @notify-padding-hoz;
  }
  /*&__item-content-inner,
  &__item-graphic-inner {
    pointer-events: all; // Re-enable pointer events.
  }*/

  // Content column
  // --------------------------------------------------
  &__item-content {

    display: flex;
    align-items: center;
    flex: 1 1 auto;
    
    // Add vertical padding (the same as for normal block content).
    padding-top: @notify-padding-ver;
    padding-bottom: @notify-padding-ver;

    z-index: 1; // Appear above the moving image.
  }
  
  &__item-title {
    margin-bottom: @item-title-margin;
    .item-title();
  }


  // Graphic column (animated; large view only)
  // -------------------------------------------------
  &__item-graphic {

    position: sticky;
    top: 0;
    
    height: 100vh;

    display: flex;
    align-items: center;

    // Add vertical padding (the same as for normal block content).
    padding-top: @notify-padding-ver;
    padding-bottom: @notify-padding-ver;

    // Hide the animating image on phones.
    @media (max-width: @device-width-medium) {
      display: none;
    }
  }

  &__item-graphic-inner {
    width: 100%;
    max-height: 100%;
    
    /*display: flex;
    align-items: center;
    justify-content: center;*/
  }

  // Graphic column image animation
  // --------------------------------------------------
  &__item-image-transition {

    // Transition
    transform-origin: 0 0;

    // Remove flicker caused by transition.
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;

    // FIXME: Chrom flicker caused by transitioning the drop-shadow. To fix, it needs to fade in.

    filter: drop-shadow(2px 4px 6px transparent);

    // This partially fixes the flicker.
    /*
    .hotgraphicplus-popup__item-image {
      padding: 50px;
      margin: -50px;
      box-sizing: content-box;
    }
    */
    
    &.is-active {
      transform: translateZ(0) !important; // Override inline styles.
      filter: drop-shadow(2px 4px 6px rgba(0,0,0,0.75)); // Add a shadow effect (temp?)
    }

    &.transition-enabled {
      .transition(transform @hotgraphicplus-popup-transition ease, filter @hotgraphicplus-popup-transition ease;);

      // Custom transitions added here.
      .hotgraphicplus-popup__item-image-transition-inner {
        .transition(all @hotgraphicplus-popup-transition ease);
      }
    }

    // Filters
    // ----------------------------------------------
    &.has-filter .transition-filter {
      .transition(filter @hotgraphicplus-popup-transition ease);
    }
    // When not active, disable inline filter styles.
    &.has-filter:not(.is-active) .transition-filter {
      filter: none !important;
    }
  }

  &__item-image {
    //min-width: auto;
    object-fit: contain;

    // Prevent the image being too tall.
    max-height: calc(100vh ~"-" (@notify-padding-ver*2));
  }

  // Alt(ernate) graphics
  // -----------------------------------------

  // Have the height of the item be the maximum height between the two alt images.
  &__item-alt-container {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
  }

  // Have the two alt images both be relative inside of their container (this will make both of them affect the height).
  &__item-image.alt-from,
  &__item-image.alt-to {
    .transition(opacity @hotgraphicplus-popup-transition ease);
    width: 100%;
  }
  // Position the .alt-to image over the .alt-from one using negative margins.
  &__item-image.alt-to {
    opacity: 0;
    margin-left: -100%;
  }

  &__item-image-transition.is-active &__item-image.alt-from {opacity: 0;}
  &__item-image-transition.is-active &__item-image.alt-to {opacity: 1;}
  

  // Small device graphic
  // -----------------------------------------
  .hotgraphicplus-popup__item-graphic-small {

    margin: @component-body-margin auto;
    &:first-child {margin-top: 0;}
    &:last-child {margin-bottom: 0;}

    @media (min-width: @device-width-medium) {
      display: none;
    }
  }

  .js-hotgraphicplus-popup-close {
    // Clicking the image will close the notification.
    cursor: pointer;
  }


  // Containers
  // --------------------------------------------------
  &__item-content-inner,
  &__item-image-transition {
    pointer-events: all;
  }

  &__item-content-inner {
    margin: auto;
  }

  // Have the left + right containers take on the same sizing and spacing as the page .components.
  @media (min-width: @device-width-medium) {
    
    &__item {
      display: flex;
      //align-items: center;
    }
    
    &__item-content {

      padding-right: @component-padding-hoz;
      width: 50%;

      .dir-rtl & {
        padding-right: inherit;
        padding-left: @component-padding-hoz;
      }
    }
    
    &__item-graphic {
      
      padding-left: @component-padding-hoz;
      width: 50%;

      .dir-rtl & {
        margin-left: inherit;
        margin-right: auto;
        padding-left: inherit;
        padding-right: @component-padding-hoz;
      }
    }
  }

  // Hotgraphic pop up icons
  // --------------------------------------------------
  &__close .icon {
    .icon-cross;
  }
  
  // Small device mode
  // --------------------------------------------------
  @media (max-width: @device-width-medium) {
    &__item-image {
      min-width: auto;
      margin: auto;
    }
    &__item-content-inner {
      width: 100%;
    }
  }
  
  // Single column mode
  // --------------------------------------------------
  &.is-single &__item-image {
    min-width: auto;
    margin: auto;
  }
  &.is-single &__item-content-inner {
    width: 100%;
  }
  &.is-single &__item-graphic {
    display: none;
  }
  &.is-single &__item-content {
    padding-right: 0;
  }
  .is-single &__item-graphic-small {
    display: flex;
    justify-content: center;
  }
}
