// Scrollbar styles
// ---------------------------------------------
// Stylize the scrollbar, and have scrollbars be visible on touch devices.

// Disable pull-to-refresh.
body {
  overscroll-behavior: contain;
}

// Prevent the main page's scrollbar from being stylized (keep as default).
#app ::-webkit-scrollbar,
.nav ::-webkit-scrollbar,
.sidemenu ::-webkit-scrollbar{
  -webkit-appearance: none;
}

::-webkit-scrollbar:vertical {
  width: @scrollbar-size-small;
}

::-webkit-scrollbar:horizontal {
  height: @scrollbar-size-small;
}

::-webkit-scrollbar-thumb {
  background-color: @scrollbar-color;
  border-radius: @scrollbar-size-small;
  border: @scrollbar-margin solid transparent;
  background-clip: padding-box;
}

// No-touch
// -------------------------------------------------------
// Slightly increase scrollbar size on non-touch devices.
.no-touch ::-webkit-scrollbar:vertical {
  width: @scrollbar-size;
}

.no-touch ::-webkit-scrollbar:horizontal {
  height: @scrollbar-size;
}

// Square scrollbars on .no-touch.
.no-touch ::-webkit-scrollbar-thumb {
  border-radius: 0;
}



