// outputField
// ------------------------------------------

@outputfield-padding: @item-padding/4;

.outputfield {

    position: relative;
    display: inline-flex;

    // Content
    // -------------------------------
    &__content {
        width: 3em; // FIXME: Hardcoded to limit the full length of the content.
        overflow: hidden;
        text-overflow: ellipsis;

        .transition(color @duration ease);
    }

    // Validation error.
    &.has-error &__content {
        font-style: italic;
        color: @placeholder;
    }
    // Hide the prefix & suffix.
    &.has-error &__content-prefix,
    &.has-error &__content-suffix {
        display: none;
    }

    // Incorrect.
    &.is-incorrect &__content {
        color: @validation-error;
    }

    // Correct.
    &.is-correct &__content {
        color: darken(@validation-success, 10%);
    }

    // FIXME: Hardcoded.
    // Do not change colours when inside of .textinputplus component (so as to not give away if the student has the correct answer).
    .textinputplus &.is-incorrect &__content,
    .textinputplus &.is-correct &__content {
        color: inherit;
    }

    // Temp?
    // Give the output a dotted border.
    &__result {
        position: relative;
        padding: 0 @outputfield-padding;
        border: @border-width dotted desaturate(@btn-color, 100%);
    }
}