// StickyBlock
// -----------------------------------------------

@stickyblock-maxwidth: @device-width-large * 1.5;
@stickyblock-timeline-color: @blueDark;

.block.is-stickyblock {
  background-color: @stickyblock-color;

  // Allow stickyblocks to have slightly more width.
  .block__inner {
    max-width: @stickyblock-maxwidth;
  }

  // Sidenav compatibility.
  // --------------------------------------
  // Add an additional margin to the main content to avoid the navbar on the left.
  // FIXME:(?) max-width is a bit hardcoded.
  @media (max-width: (@stickyblock-maxwidth + @sidenav-width*3)) and (min-width: @device-width-medium) {

    .component__container {
        margin-left: @sidenav-width;
        .transition(margin-left @sidemenu-duration ease);
    }
  }

  // If the user opens the sidemenu while looking at the sticky, reduce this margin again.
  @media (min-width: (@device-width-large + @drawer-width)) {
    #wrapper.is-sidemenu-active & .component__container {
      margin-left: 0;
    }
  }
}

.stickyblock {

  &__item:not(:first-child) {
    .component__title {
      margin-bottom: @component-sub-title-margin;
      .component-sub-title;
    }
  }

  // Set all components that have titles (besides the first one) to use the sub-title style instead.
  /*&-has-title:not(:first-of-type) .component__title {
    margin-bottom: @component-sub-title-margin;
    .component-sub-title;
  }*/

  // Timeline
  // ----------------------------------
  &__timeline {
    color: @stickyblock-timeline-color;
    .u-no-select;
  }

  // Position the timeline title just above the static element's title.
  &__timeline-line-top {
    height: calc(@stickyblock-item-margin ~"-" @component-title-size);
  }

  &__timeline-line {
    background-color: currentColor;
  }

  // Fade in/out the timeline at the edges.
  &__item:first-child &__timeline-line-top {
    background-color: transparent;
    background-image: linear-gradient(180deg, transparent, currentColor 10rem);
  }
  &__item:last-child &__timeline-line-bottom {
    background-color: transparent;
    background-image: linear-gradient(180deg, currentColor calc(100% ~"-" 10rem), transparent);
  }

  // Debug
  // ------------------------------------------
  .mode-debug & .component {
    &::after {
      content: attr(data-stickyblock-index);
      background: purple;
      color: white;
      opacity: 0.8;
      font-family: monospace;
      padding: 0 0.5em;
      position: absolute;
      top: 0;
      right: 0;

      font-size: 12px;

      transform: translate(100%, -100%);
      pointer-events: none;
    }
  }

  // Fade in/out sticky items as they become visible.
  // ------------------------------------------------
  &__item.is-sticky {

    // Add a background colour (added to make the crossfading effect look better).
    background: @stickyblock-color;

    // Transition visibility to properly hide it from being clicked on.
    .transition(opacity @duration ease, visibility @duration linear;);

    .component {
      visibility: inherit !important;
    }

    &.is-hidden {
      opacity: 0;
      transition-delay: @duration / 2;
      visibility: hidden;
      
      //.transition(opacity @duration ease, visibility @duration linear @duration;);


      //visibility: visible !important;
    }
    
    &.is-visible {
      opacity: 1;
    }
  }
}

// Componentrow timeline dividers
// --------------------------------------------
.componentrow__divider.is-stickyblock-timeline-divider {
  color: @stickyblock-timeline-color;
  opacity: 1;

  // Align the title to the right.
  .componentrow__divider-line:last-child {
    display: none;
  }
}
  
