// Paper
// -----------------------
// Decorates the element to look like a peice of paper with the top-right corner folded.

@paper-padding: @item-padding * 1.5;
@paper-corner-size: @paper-padding;

@paper-logo-size: 7rem;
@paper-logo-margin: @component-body-margin;

.paper() {
  
  position: relative;
  background-color: @white;
  color: @black;
  .drop-shadow-half();

  // To make the drop shadow accurately go around the corner, the right hand side of the page won't have padding.
  // Instead, a pseudo element + margin will be used to create it.
  // This way, the filter won't create a transparent corner above the page corner with its shadow.
  padding: @paper-padding;
  padding-right: 0;
  border-right: @paper-padding solid transparent;
  background-clip: padding-box;
  //margin-right: @paper-padding;

  &::after,
  &::before {
    content: "";
    position: absolute;
    border-color: @white;
  }

  // Right (fake) padding
  &::before {
    top: @paper-padding;
    bottom: 0;
    right: -@paper-padding;
    width: calc(@paper-padding ~"+" 1px);
    background-color: inherit;
    z-index: -1;
  }

  // Corner
  // ---------------------
  &::after {
    top: -1px;
    right: calc(-1px ~"-" @paper-padding);
    margin-right: 1px; // Fixes a bug where the page triggers horizontal scrollbars to appear.

    border-width: 0 calc(@paper-corner-size ~"+" 2px) calc(@paper-corner-size ~"+" 2px) 0;
    border-style: solid;
    border-color: darken(@white, 5%) transparent;

    -webkit-box-shadow: -0.5px 1px 1px rgba(0,0,0,0.15), -1px 1px 1px rgba(0,0,0,0.2);
    -moz-box-shadow: -0.5px 1px 1px rgba(0,0,0,0.15), -1px 1px 1px rgba(0,0,0,0.2);
    box-shadow: -0.5px 1px 1px rgba(0,0,0,0.15), -1px 1px 1px rgba(0,0,0,0.2);
  }

  // Other
  // --------------------
  // FIXME: May be removed in future.
  p:first-of-type {
    margin-top: 0;
  }
  p:last-of-type {
    margin-bottom: 0;
  }

  // Styles
  // ---------------------

  // Default
  // -------
  background-color: @item-color;
  &::after {border-color: darken(@item-color, 5%) transparent;}

  // When used inside of certain contexts, turns white.
  .tabs__content &,
  .narrativeplus__item-content &,
  .hotgraphicplus-popup__item-content &,
  .accordion__item-content &,
  .stickyblock & {
    background-color: @white;
    &::after {border-color: darken(@white, 5%) transparent;}
  }

  //&.style-steps {
  //}
  
  // Logo
  // ---------------------
  &__logo {
    float: right;
    margin-left: @paper-logo-margin;
    margin-bottom: @paper-logo-margin/2;
    width: @paper-logo-size;
    min-width: 0;
  }
}

.paper {
  .paper();
}