// Iframe support
// --------------
// If the course is embedded in an iframe, it needs to have a background colour to fallback on
// (otherwise the background page will be visible).
body {
  background: @white;
}

.page {
  max-width: inherit;
  .l-container-padding(@page-padding-ver, @page-padding-hoz);

  &.has-bg-image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
  }

  // FIXME: Temporary generated overview block.
  &__overview {
    background-color: desaturate(@question-color, 100%);
  }

  // Page header
  // -------------------------------------
  &__header {
    display: flex;
    align-items: center;

    &.has-bg-image {
      
      // Fixed position used to allow it to remain fixed on mobile devices (which struggle to render background-attachment).
      .page__header-bg {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        min-height: inherit;

        background: @white;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center top;
        //background-attachment: fixed;
        //background-size: 100% auto;

        z-index: @blockbg-z-index; // Ensure it's behind EVERYTHING else.

        // Move the image down slightly if the navigation bar is present.
        @media (max-width: @device-width-medium) {
          top: @sidenav-width-small;
        }
      }
      
      // Sizing.
      // Fixme: values hardcoded to decent sizing.
      min-height: @page-header-height-large;
      
      @media (max-width: @device-width-medium) {
        min-height: 400px;
        /*.page__header-bg {
          background-size: auto 450px;
        }*/
      }
      @media (max-width: @device-width-small) {
        min-height: 300px;
        /*.page__header-bg {
          background-size: auto 300px;
        }*/
      }
    }
  }

  &__header-inner {
    .l-container-padding(@page-header-padding-ver, @page-header-padding-hoz);
    width: 100%;
    max-width: @device-width-large;

    // Fix adjustment for SideNav.
    @media (max-width: @sidenav-media-width) and (min-width: @device-width-medium) {

      & > * { // FIXME: Bad selector used.
          margin-left: @sidenav-width;
      }
    }
  }

  &__title {
    margin-bottom: @page-title-margin;
    .page-title;
    color: @heading-color-inverted;

    @a: 1px;

    filter: drop-shadow(-@a -@a 0 @heading-color)
            drop-shadow(@a -@a 0 @heading-color)
            drop-shadow(-@a @a 0 @heading-color)
            drop-shadow(@a @a 0 @heading-color);

    // Outline the text.
    /*text-shadow: -@a -@a 0 @heading-color,
                 @a -@a 0 @heading-color,
                 -@a @a 0 @heading-color,
                 @a @a 0 @heading-color;*/
  }

  &__subtitle {
    margin-bottom: @page-subtitle-margin;
    .page-subtitle;
  }

  &__body {
    margin-bottom: @page-body-margin;
    .page-body;
  }

  &__instruction {
    margin-bottom: @page-instruction-margin;
    .page-instruction;
  }

  &__body a,
  &__instruction a {
    .link-text;
  }
}
