.component {
  &__title {
    //&:not(:last-child){
      margin-bottom: @component-title-margin;
    //}
    .component-title;
    color: @heading-color;
  }

  &__body:not(:last-child) {
    margin-bottom: @body-text-margin; //@component-body-margin;
  }

  &__instruction {
    margin-top: @component-instruction-margin;
    &:not(:last-child){
      margin-bottom: @component-instruction-margin;
    }
    .instruction-text;
  }

  &__header:not(:last-child) {
    margin-bottom: @component-body-margin;
  }

  // Footer
  // --------------------------------------------------------
  &__footer:not(:first-child) {
    margin-top: @component-body-margin;
  }

  // Don't give the footer a margin-top if it's right after a clearfix (_componentLogo generates clearfix elements).
  .u-clearfix + &__footer {
    margin-top: 0;
  }

  &__body a,
  &__footer-body a, // New
  &__instruction a {
    .link-text;
  }
}
