// ddImgPopup
// ------------------------------

// Modify the notify elements to give as much room for the lightbox img as possible.
.notify__popup.ddimg-lightbox {

    // Allow scrolling in both directions.
    overflow-x: auto;
    overflow-y: auto;
    width: 100vw;
    height: 100vh;

    .notify__popup-inner {
        max-width: none;
        padding-left: 0;
        padding-right: 0;
    }
    // Allow the user to click through everything except the <img>.
    .notify__content {pointer-events: none;}
    
    .notify__content-inner {
        text-align: center;
    }
}

.ddimg-popup {

    pointer-events: all;
    //overflow: auto;

    display: inline-block; // Ensure that the view is only as wide as the image.
    max-width: 100%; // Have it scroll if too wide.

    // Add some margins to the image (stylistic choice; done to allow for slight overflow of absolutely positioned <mark> elements over it.
    .ddimg {
        display: inline-block;
        padding-left: @notify-padding-hoz;
        padding-right: @notify-padding-hoz;

        &.has-content {
            padding-top: @item-margin;
            padding-bottom: @item-margin;
            overflow: hidden;
        }
    }

    // Have the image be as large as it needs to be.
    .ddimg__img {
        max-width: none;
    }
    .ddimg__inner {
        display: inline-block;
        overflow: visible; // Allow any overlaying marks to extend past the boundaries a bit.
    }

    // FIXME: Temp? Hide any registerhelperel__placeholder elements that may be inside the image.
    .registerhelperel__placeholder {
        display: none;
    }

    // Allow for custom lightbox image widths (for SVGs).
    // --------------------------------------------------
    .img-loop(@i) when (@i <= 24) {

        .ddimg-popup__size-@{i} > .ddimg__inner {
            width: (@i/12*@device-width-medium);
        }
        .img-loop(@i + 1);
    }
    .img-loop(1);

    // Optional background behind image (useful for some darker images)
    // ----------------------------------------------------------------
    &__img.has-bg {
        position: relative;

        &::before {
            content: "";
            position: absolute;

            top: -@notify-padding-hoz;
            bottom: -@notify-padding-hoz;
            
            left: 0;
            right: 0;
            background: @white;
        }
    }
}