// ExtraLearning
// ------------------------------

.extralearning-btn {
    
    // Hover state.
    &__title {
        .transition(color @duration ease);
        .u-no-select();
    }
    .extralearning__icon {
        .transition(background-color @duration ease);
    }

    // React to hover on the &_inner element (it has display: inline-block to make it only as wide as the button + text).
    .no-touch &__inner:hover &__title {
        color: @extralearning-btn-color-hover;
    }
    .no-touch &__inner:hover .extralearning__icon {
        background-color: @extralearning-btn-color-hover;
    }
}