// MarkChain
// -------------------------------------------------

// FIXME: A lot of the styles defined here should be defined in the theme, not in the core extension.

@markchain-arrow-size: @mark-border-width;

.markchain {

    position: relative;
    display: inline-block;
    z-index: 0;

    // Allow the background to exceed the boundaries of the main element (so as to not affect the size of it).
    &__background {
        content: "";
        position: absolute;

        top: -@mark-padding;
        left: -@mark-padding*2;
        right: -@mark-padding*2;
        bottom: -@mark-padding;

        background-color: @annotation-mark-color;
        border: @mark-border-width @annotation-mark-border-color solid;

        z-index: -1;
    }

    // Arrow
    // -------------------------
    &__arrow {
        position: absolute;
        
        height: 0;
        width: 0; // Overridden by JS.

        transform-origin: 0% 50%;

        // IMPORTANT! Have the origin of the arrow be centered behind its node (JS calculations require this).
        top: 50%;
        left: 50%;

        z-index: -2;
    }

    // The parent (&__arrow) is used for accurate positioning. The actual arrow is drawn here.
    &__arrow-draw {
        position: absolute;
        
        top: -@markchain-arrow-size;
        bottom: -@markchain-arrow-size;
        left: 0;
        right: @markchain-arrow-size*10; // Allow room for the arrow head.

        background-color: @annotation-mark-border-color;

        text-align: center; // Centers the &__label.

        &::after {
            content: "";
            position: absolute;
            right: -@markchain-arrow-size*10;
            top: -@markchain-arrow-size*8 + @markchain-arrow-size;
            width: 0;
            height: 0;

            border: @markchain-arrow-size*8 solid transparent;
            border-left-color: @annotation-mark-border-color;
            border-right-width: 0;

            transform-origin: right;
            transform: scale(1.5, 0.75);
        }
    }

    // Optional label that sits above the arrow.
    &__label {
        display: inline-block;
        text-align: center;
        white-space: nowrap;
        //width: 100%;

        font-size: @body-size;

        margin-top: @markchain-arrow-size;

        pointer-events: none; // Allow clicking through.

        // Increase the font-size slightly if this is a small label.
        // FIXME: Hardcoded; should be in the theme.
        &.is-small {
            font-size: @body-size * 1.5;
            font-weight: 300;
        }
    }

    &__label-inner {
        background-color: @font-color-inverted;
        padding: @mark-padding @mark-padding*2;
        border: @mark-border-width @annotation-mark-border-color solid;
        border-radius: @border-radius;

        pointer-events: all;

        text-rendering: optimizeLegibility;
    }


    // THEMES
    // ------------------------------------------

    // Ghost (all but the label is invisible).
    &.theme-ghost &__background {
        background: transparent;
        border-color: transparent;
    }
    &.theme-ghost &__arrow-draw {
        background: transparent;
        right: 0;
        &::after {
            content: none;
        }
    }
    /*&.theme-ghost &__label.is-small {
        font-size: inherit;
        font-weight: inherit;
    }*/
    &.theme-ghost &__label-inner {
        //color: @annotation-mark-border-color;
        //color: @item-color;
        background: transparent;
        border-color: transparent;
        //font-weight: bold;
    }
}