.notify {

  // Always go to max width (even if the content inside isn't large enough to warrant it).
  &__content {
    width: 100%;
  }

  // DD has a different approach to displaying notify popups.
  // We want only the content (text) to have the background, and the graphic can just sit infront of the shadow.
  // NOTE: This needs to be done on a per-component basis to ensure the right pointer colours + pointer events are kept.
  &__popup {
    background-color: @notify;
    //background-color: transparent;
    color: @notify-inverted;

    overflow-x: hidden; // Prevents transform animations from triggering horizontal scrolling for a split second.

    transform: translate3d(0,0,0); // iPhone Safari bugfix(?): Fixes nested images from disappearing.

    /*pointer-events: none;
    & > * {
      pointer-events: all;
    }*/
  }

  &__popup-inner {
    position: relative;
    .l-container-padding(@notify-padding-ver, @notify-padding-hoz);
  }

  &__icon {
    display: none;
  }

  &__title {
    margin-bottom: @notify-title-margin;
    .notify-title;

    /*@media (max-width: @device-width-medium) {
      padding-right: unit(@icon-size + @btn-margin + @btn-padding, rem);

      .dir-rtl & {
        padding-right: inherit;
        padding-left: unit(@icon-size + @btn-margin + @btn-padding, rem);
      }
    }*/
  }

  &__body {
    //margin-bottom: @notify-body-margin;
  }

  &__instruction {
    margin-bottom: @notify-instruction-margin;
    .instruction-text;
  }

  &__body a,
  &__instruction a {
    .link-text;
    color: @notify-link;

    .no-touch &:hover {
      color: @notify-link-hover;
      .transition(color @duration ease-in);
    }
  }

  &__btn-container {
    margin-top: @btn-margin;
  }

  // Keep the style of the buttons in notify the same as everywhere else.
  /*
  &__btn {
    background-color: @notify-btn;
    color: @notify-btn-inverted;

    .no-touch &:not(.is-disabled):hover {
      background-color: @notify-btn-hover;
      color: @notify-btn-inverted-hover;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }
  }
  */

  &__btn-icon {
    margin: @btn-margin / 2;
    padding: @btn-padding / 2;
    background-color: @notify-icon;
    color: @notify-icon-inverted;
    border-radius: 50%;

    .no-touch &:hover {
      background-color: @notify-icon-hover;
      color: @notify-icon-inverted-hover;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }
  }

  // Close button modified
  // ----------------------------------------------------------------------------
  // Now always sits in top-right corner, and is positioned with sticky.
  // (position: fixed can't be used as it causes the scrollbar to overlap).
  &__close-btn-container {
    position: sticky;
    top: @btn-margin;
    left: 0;
    width: auto;
    margin: 0;
    margin-right: @btn-margin;
    
    // Don't affect content placed after the button.
    height: 0;

    z-index: 2;

    // Epub (Desktop Bookshelf)
    // --------------------------------------
    // Move the close icon slightly (the scrollbar is too close to it when in Desktop Bookshelf specifically).
    .desktop-bookshelf & {
      margin-right: @btn-margin*2;
    }
  }
  
  &__close-btn {

    position: absolute;
    top: 0;
    right: 0;
    margin: 0;

    .drop-shadow-half();

    //position: sticky;
    //top: @btn-margin;

    /*left: 100%;
    margin: 0;
    margin-right: @btn-margin;*/

    // Don't affect content placed after the button.
    //margin-bottom: calc(-1em ~"-" @btn-padding);

    .dir-rtl & {
      right: inherit;
      left: 0;
    }
  }

  // DD modifications
  // --------------------------------------------------
  &__popup {
    background: transparent;
  }
  &__popup-inner {
    min-height: 100vh;
    display: flex;
    align-items: center;
  }
  
  // Question feedback.
  &__popup.is-component {

    @bgOpacity: 90%;

    .notify__content {
      background-color: fade(@notify, @bgOpacity);
      //padding: @notify-padding-ver @notify-padding-hoz;
      padding: @notify-padding-ver @notify-content-padding;
      width: 100%;

      text-align: center;

      .drop-shadow();

      .notify__title {
        font-size: @notify-title-size + 0.1rem; // + 0.5rem; // FIXME: Temp.
      }

      .notify__body-content,
      .notify__body-instruction {
        font-size: @body-size + 0.1rem; // FIXME: Temp.
      }
    }

    &.is-correct .notify__content,
    &.is-incorrect .notify__content {
      .notify__body-instruction {
        color: @instruction-color-inverted;
      }
    }

    &.is-correct .notify__content {
      background: fade(@validation-success, @bgOpacity);
      color: @validation-success-inverted;
    }

    &.is-incorrect .notify__content {
      background: fade(desaturate(@validation-error, 5%), @bgOpacity); // Desaturate the red slightly (too intense).
      color: @validation-error-inverted;
    }
  }

  // --------------------------------------------------
  // Notify prompt type icon
  // --------------------------------------------------
  // &__icon {
  //   position: absolute;
  //   top: 0;
  //   right: 0;

  //   .dir-rtl & {
  //     right: inherit;
  //     left: 0;
  //   }
  // }
  // --------------------------------------------------


  // Prompt
  // --------------------------------------------------
  // "Would you like to resume the course from the location you were at last time?"
  &__popup-type-prompt &__content {
    width: 100%;
    .drop-shadow();
  }
  
  &__popup-type-prompt &__content-inner {
    background-color: @notify;
    padding: @notify-content-padding;
  }

  // FIXME: Temp consistant width style applied to the buttons.
  &__btn-prompt {
    width: 5rem;
  }
}
