// Paper
// -----------------------

.paper {

  // Margins
  // ---------------------
  &:not(:first-child) {
    margin-top: @figure-margin;
  }
  &:not(:last-child) {
    margin-bottom: @figure-margin;
  }

  // Custom colors (legacy)
  // ----------------------
  // (Had to add !important for SET01).
  &--green,
  &.--green {
    background-color: @greenPale !important;
    &::after {border-color: darken(@greenPale, 5%) transparent !important;}
  }

  &--yellow,
  &.--yellow {
    background-color: @yellowPale !important;
    &::after {border-color: darken(@yellowPale, 5%) transparent !important;}
  }

  &--red,
  &.--red {
    background-color: @redPale !important;
    &::after {border-color: darken(@redPale, 5%) transparent !important;}
  }

  &--white,
  &.--white {
    background-color: @white !important;
    &::after {border-color: darken(@white, 5%) transparent !important;}
  }

  &--blue,
  &.--blue {
    background-color: @blue !important;
    &::after {border-color: darken(@blue, 5%) transparent !important;}
  }

  &--cyan,
  &.--cyan {
    background-color: @cyanPale !important;
    &::after {border-color: darken(@cyanPale, 5%) transparent !important;}
  }

  // FIXME:
  // Need to explicitly define padding for components (some other component styles may overwrite this padding otherwise).
  &.component {
    padding: @paper-padding !important;
  }

  // Styles
  // ---------------------
  
  // Steps. Use double spacing for the list items.
  &.style-steps ol > li {

    &:not(:first-child){margin-top: 1.2rem;}
    &:not(:last-child) {margin-bottom: 1.2rem;}

    // Don't affect nested lists.
    li {
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
    }
  }

  // StickyBlock context
  // ---------------------------
  // If the paper is used as the main element of a .stickyblock__item.is-sticky, limit its height and give it overflow.
  .stickyblock__item.is-sticky & {
    .paper__inner {
      max-height: calc(100vh ~"-" (@block-padding-ver*2));
      overflow-y: auto;
      overflow-x: hidden;
    }
  }
}