.narrativeplus {

  @narrative-duration: @duration*2;
  @narrative-transition: ease-in-out;

  &__slide-container {
    .drop-shadow-item();

    border-radius: @item-border-radius;
  }

  // Controls
  // ------------------------------
  &__controls {
    background-color: @item-btn-color;
    color: @item-btn-color-inverted;

    .no-touch &:hover {
      background-color: @item-color-hover;
      color: @item-color-inverted-hover;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }
  }

  // Progress
  // ------------------------------
  &__progress {
    background-color: @item-btn-color;
    border-radius: 50%;

    .no-touch &:not(.is-selected):hover {
      background-color: @item-color-hover;
      .transition(background-color @duration ease-in);
    }

    &.is-visited {
      background-color: @visited;
    }
  
    &.is-selected {
      background-color: @item-color-selected;
    }
  }

  // Content
  // ---------------------------------
  &__slider-item {
    align-items: stretch;
  }

  &__item-title {
    margin-bottom: @item-title-margin;
    .item-title();
  }

  // Center text and graphic vertically on large devices.
  // TODO: May be removed.
  &__item.has-graphic &__item-inner {
    height: 100%;
    @media (max-width: @device-width-medium) {
      height: auto;
    }
  }

  // Transitions
  html:not(.disable-animation) & {

    // StickyElement compatibility
    // -----------------------------------------------
    .stickyelement {
      will-change: transform;
      .transition(transform @narrative-duration @narrative-transition);
    }

    &__slider {
      will-change: transform;
      backface-visibility: hidden;
      .transition(transform @narrative-duration @narrative-transition);
    }
  }
}
