@pbcontrols-track-size-ms:  1rem; // Size used in old MS browsers (specific to EPUB Bookshelf).

.playbackcontrols {

  // Change colour of the controls to a more subdued one using the item-btn-color instead
  // ------------------------------------------------------------------------------------
  &__range {
    background-color: @item-btn-color;
    border-radius: @border-radius;
  }

  &__range::-webkit-slider-thumb {
    background-color: @item-btn-color-selected;
    border-radius: @border-radius;
  }

  &__button {
    background-color: @item-btn-color;
    color: @item-btn-color-inverted;
    border-radius: @btn-border-radius;
  }

  // Desktop Bookshelf (EPUB) compatibility
  // --------------------------------------
  // Need to style the range slider for old MSEdge (v18, which is what Desktop Bookshelf is using as of 21/09/2021).
  .mode-epub.desktop-bookshelf &__range {
    
    height: @pbcontrols-track-size-ms;
    border-radius: @border-radius;
    cursor: pointer;

    &::-ms-track {
      background: transparent;
      color: transparent;
  
      // Increase the height of the track.
      height: @pbcontrols-track-size-ms;
      border-radius: @border-radius;
    }
    &::-ms-thumb {
        -webkit-appearance: none;
        appearance: none;
    
        width: @pbcontrols-track-size-ms;
        height: @pbcontrols-track-size-ms;
        background: @item-btn-color-selected;
        cursor: pointer;
        border-radius: @border-radius;
    }
  }
}