// NarrativePlus
// ------------------------------

.narrativeplus {

  //max-width: 100%;
  
  // Slider
  // --------------------------------------------------
  &__slide-container {
    position: relative;
    width: 100%;
    overflow-x: hidden;
  }

  // Ensure all items have the same height (makes for a smoother transition between items).
  &__slider {
    display: flex;
    align-items: stretch;
  }

  &__slider-item {
    display: flex;
    align-items: flex-start;        // Align at the top.
    background-color: @item-color;

    @media (max-width: @device-width-medium) {
      align-items: flex-start;
    }
  }

  // Items
  // --------------------------------------------------
  &__item {
    padding: @item-padding;
    width: 100%;
  }
  // Align item text + graphic horizontally
  &__item-inner {
    position: relative;
    display: flex;
    align-items: center;

    // The graphic is always added first (in case it needs to be floated).
    flex-direction: row-reverse;
  }
  &__item-content {
    width: 100%;
  }
  &__item-image {
    width: 100%;
  }
  // Title to display on small devices.
  // FIXME: Weird name for it.
  &__item-small-title {
    display: none;
  }

  // Graphic
  // ----------------------------
  // Circle style
  &__item-image-container.style-circle &__item-image {
    border-radius: 50%;
  }

  // In non-large views, align the text + graphic vertically.
  @media (max-width: @device-width-medium) {
    // Show the small title
    &__item-content-title {
      display: none;
    }
    &__item-small-title {
      display: block;
    }
  }

  // Button controls
  // --------------------------------------------------
  &__controls-container {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: @item-padding;
  }

  &__controls {
    padding: @icon-padding / 2;
    border-radius: 50%;
  }

  /*&__controls {
    position: absolute;
    top: 50%;
    z-index: 10;
    .transform(translateY(-50%));
  }*/

  &__controls-left {
    margin-right: 0.25rem;

    .icon {
      .icon-controls-left;
    }
  }

  &__controls-right {
    margin-left: 0.25rem;

    .icon {
      .icon-controls-right;
    }
  }

  // Progress indicators
  // --------------------------------------------------
  &__indicators {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    //width: 100%;

    /*@media (min-width: @device-width-medium) {
      width: 60%;
    }*/
  }

  &__progress {
    height: @icon-size / 2;
    width: @icon-size / 2;
    margin: 0.25rem;
    cursor: pointer;
    background-color: @background;

    border-radius: 50%;
  }

  &__progress.is-selected {
    background-color: lighten(@background, 10%);
  }
  // --------------------------------------------------

  // --------------------------------------------------
  // When the disable animation class is not present add a transition effect
  // to the slider
  // --------------------------------------------------
  html:not(.disable-animation) & {
    &__slider {
      backface-visibility: hidden;
      .transition(transform 400ms ease-in-out);
    }
  }

  // adapt-sizing compatibility
  // -----------------------------------------------------------------
  // If the graphic has width: auto, give its width priority in the flexbox.
  &__item-image-container.col-auto {
    flex-shrink: 0;
    margin: auto;
  }

  // Adjust margins (give additional space between the title and the graphic).
  @media (max-width: @device-width-medium) {
    &__item.has-graphic &__item-title {
      margin-bottom: @component-body-margin;
    }
  }
}