// Burger button which transforms into an X
// ========================================

@sidemenu-drawer-btn-bar-size: 0.25rem;
@sidemenu-drawer-btn-width: @sidenav-width - (@sidenav-padding * 2);
@sidemenu-drawer-btn-height: @sidemenu-drawer-btn-width * 0.8;

.sidemenu {

    &__btn-container {
        padding: @sidemenu-padding @sidenav-padding;

        // When the sideMenu is inactive, have this button sit outside of it, over the sideNav.
        .transition(transform @sidemenu-duration*0.65 ease);
        //transition-delay: @sidemenu-duration*0.25;
        transform: translateX(100%);
    }

    &__drawer-btn {

        //background-color: pink;
        position: relative;
        padding: 0;
        cursor: pointer;

        width: @sidemenu-drawer-btn-width;
        height: @sidemenu-drawer-btn-height;

        &-bar {
            position: absolute;
            display: block;

            width: 100%;
            height: @sidemenu-drawer-btn-bar-size;

            background-color: @drawer;

            margin: 2px 0px;

            animation-duration: @sidemenu-duration;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
        }

        .bar-0 {
            top: 0;
        }
        .bar-1 {
            top: 50%;
            margin-top: -@sidemenu-drawer-btn-bar-size/2;
        }
        .bar-2 {
            bottom: 0;
        }

        @t: (@sidemenu-drawer-btn-height - @sidemenu-drawer-btn-bar-size*2) / 2;
        @s: 0.5;
        @a: 45deg; //38deg;

        @keyframes k_to-cross-0 {
            0% {transform: translateY(0);}
            45% {transform: translateY(@t);}
            55% {transform: translateY(@t) scaleY(@s);}
            100% {transform: translateY(@t) rotate(@a) scaleY(@s);}
        }

        @keyframes k_to-cross-1 {
            0% {opacity: 1;}
            45% {opacity: 1;}
            55% {opacity: 0;}
            100% {opacity: 0;}
        }

        @keyframes k_to-cross-2 {
            0% {transform: translateY(0);}
            45% {transform: translateY(-@t);}
            55% {transform: translateY(-@t) scaleY(@s);}
            100% {transform: translateY(-@t) rotate(-@a) scaleY(@s);}
        }

        @keyframes k_to-burger-0 {
            0% {transform: translateY(@t) rotate(@a) scaleY(@s);}
            45% {transform: translateY(@t) scaleY(@s);}
            55% {transform: translateY(@t);}
            100% {transform: translateY(0);}
        }

        @keyframes k_to-burger-1 {
            0% {opacity: 0;}
            45% {opacity: 0;}
            55% {opacity: 1;}
            100% {opacity: 1;}
        }

        @keyframes k_to-burger-2 {
            0% {transform: translateY(-@t) rotate(-@a) scaleY(@s);}
            45% {transform: translateY(-@t) scaleY(@s);}
            55% {transform: translateY(-@t);}
            100% {transform: translateY(0);}
        }

        &.is-cross {
            .bar-0 {animation-name: k_to-cross-0;}
            .bar-1 {animation-name: k_to-cross-1;}
            .bar-2 {animation-name: k_to-cross-2;}
        }

        &.is-burger {
            .bar-0 {animation-name: k_to-burger-0;}
            .bar-1 {animation-name: k_to-burger-1;}
            .bar-2 {animation-name: k_to-burger-2;}
        }
    }

    // Active state.
    &.is-active &__btn-container {
        transform: translateX(0);
        transition-delay: 0s;
    }
}