// FlipCard
// ------------------------------

.flipcard {

    &__card {
        position: relative;
        perspective: @perspective;
    }

    &__card-inner {
        display: flex;
        transform-style: preserve-3d;
        .transition(transform @duration*2 ease-in-out);
    }

    &__card.is-active &__card-inner {
        transform: rotateY(180deg);
    }
    
    // Content
    // ------------------------------------
    &__content {

        display: block;
        position: relative;

        width: 100%;
        backface-visibility: hidden;

        padding: @item-padding;
        border-radius: @item-border-radius;
        background-color: @item-color;
    }

    &__content-front {
        transform: rotateY(0);
    }

    // Overlap the content (but don't make it absolute).
    &__content-back {
        margin-left: -100%;
        transform: rotateY(-180deg);
    }

    // Title
    &__item-title {
        .item-title();
        margin-bottom: @item-title-margin;
    }

    // Button
    // -------------------------------------
    &__btn-container {
        margin-bottom: @component-body-margin;
    }
}
