// Marks (both static and annotation)
// ---------------------------------------------

@absolute-mark-shadow-size: 2px;
@absolute-mark-border-width: @mark-border-width*3;

// TODO: Rename mark-annotation color variables to "mark-inverted*".
// Reasoning: the normal mark colours we use are very pale, while the annotation ones stand out clearer. Both have a time and place, but the latter has been gaining a lot of use lately.

// Mixin for marking only the background-color and border-color styles (doesn't affect layout).
.mark-colors() {
  background-color: @mark-color;
  border-color: @mark-border-color;
  
  // Swaps the mark color to the annotation color.
  &.theme-alt,
  &[data-theme="alt"] {
    background-color: @annotation-mark-color;
    border-color: @annotation-mark-border-color;
  }

  // Unique colours (only use these for customs).

  &.theme-0,
  &[data-theme="0"] {
    background-color: @mark-color;
    border-color: @mark-border-color;
  }

  &.theme-1,
  &[data-theme="1"] {
    background-color: @mark-color-1;
    border-color: @mark-border-color-1;
  }

  &.theme-2,
  &[data-theme="2"] {
    background-color: @mark-color-2;
    border-color: @mark-border-color-2;
  }

  &.theme-3,
  &[data-theme="3"] {
    background-color: @mark-color-3;
    border-color: @mark-border-color-3;
  }
  
  // Table interactions
  // --------------------------------------------------------------------
  // If used on a table or table row, have the cells within inherit the colours. 
  tr, th, td {border-color: inherit; border-style: inherit;}
  // Doing this inside of a table will make the border colours out-prioritise their neighbours.
  border-style: double;
}

// TODO: Implement this in the .mark() mixin below too.
.mark-colors-inverted() {
  background-color: @annotation-mark-color;
  border-color: @annotation-mark-border-color;

  // Unique colours (only use these for customs).
  &.theme-1,
  &[data-theme="1"] {
    background-color: @mark-color-1;
    border-color: @mark-border-color-1;
  }

  &.theme-2,
  &[data-theme="2"] {
    background-color: @mark-color-2;
    border-color: @mark-border-color-2;
  }

  &.theme-3,
  &[data-theme="3"] {
    background-color: @mark-color-3;
    border-color: @mark-border-color-3;
  }

  // Table interactions
  // --------------------------------------------------------------------
  // If used on a table or table row, have the cells within inherit the colours. 
  tr, th, td {border-color: inherit; border-style: inherit;}
  // Doing this inside of a table will make the border colours out-prioritise their neighbours.
  border-style: double;
}

.mark() {

  .mark-colors();
  border-style: solid;
  border-width: @mark-border-width;
  
  margin-top: calc((-@mark-padding/2) ~"-" @mark-border-width);
  margin-bottom: calc((-@mark-padding/2) ~"-" @mark-border-width);
  padding: @mark-padding/2 @mark-padding;

  box-sizing: border-box;
  //-webkit-box-decoration-break: clone; // Keep the border across breaks.

  color: inherit;

  &.is-annotation-target {
    border-color: @annotation-mark-border-color;
    background-color: @annotation-mark-color;
  }

  // Inside of console boxes.
  .codebox.is-console &:not(.is-annotation-target),
  .codebox.is-console &.is-annotation-target.is-active {
    color: @font-color;
  }

  // Table interactions
  // --------------------------------------------------------------------

  // Doing this inside of a table will make the border colours out-prioritise their neighbours.
  // FIXME: Couldn't find a way to apply it only while in tables (that didn't break the mixin). Uses double globally.
  // (Shouldn't be an issue however, as at border-width: 1px, double looks identical to solid).
  border-style: double;

  // Absolute positioning
  // --------------------------------------------------------------------
  // Used to create outlines over {{#img}} elements.

  // Fades out everything else except for the mark.
  // Note: Only works when there's only one mark active.
  &.is-absolute.has-shadow::before {
    content: "";
    position: absolute;
    top: calc(-@absolute-mark-border-width ~"-" @absolute-mark-shadow-size);
    left: calc(-@absolute-mark-border-width ~"-" @absolute-mark-shadow-size);
    width: calc(100% ~"+" (@absolute-mark-border-width*2) ~"+" (@absolute-mark-shadow-size*2));
    height: calc(100% ~"+" (@absolute-mark-border-width*2) ~"+" (@absolute-mark-shadow-size*2));

    margin-left: -100vw;
    margin-top: -100vw;

    border: 100vw solid fade(@shadow, 50%);
    pointer-events: none;
    box-sizing: content-box;

    border-radius: calc(100vw ~"+" @border-radius);
  }
  // Disable shadow when in lightbox.
  .ddimg-lightbox &.is-absolute.has-shadow::before {
    content: none;
  }
  
  &.is-absolute.is-empty {
    
    border-radius: @border-radius;

    background: transparent;
    border-width: @mark-border-width*3; // Increase border width for visibility.
    /*box-shadow: 
      inset @mark-color @absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
      inset @mark-color -@absolute-mark-shadow-size -@absolute-mark-shadow-size 0px,
      @mark-color @absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
      @mark-color -@absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
      @mark-color @absolute-mark-shadow-size -@absolute-mark-shadow-size 0px,
      @mark-color -@absolute-mark-shadow-size -@absolute-mark-shadow-size 0px;*/

    min-height: 1rem; // Prevent it becoming too small.
    min-width: 1rem;

    // Have the border be on the outside.
    box-sizing: content-box;
    margin-left: -@mark-border-width*3 - @absolute-mark-shadow-size;
    margin-top: -@mark-border-width*3 - @absolute-mark-shadow-size;

    border-color: @annotation-mark-border-color;
    box-shadow: 
      inset @annotation-mark-color @absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
      inset @annotation-mark-color -@absolute-mark-shadow-size -@absolute-mark-shadow-size 0px,
      @annotation-mark-color @absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
      @annotation-mark-color -@absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
      @annotation-mark-color @absolute-mark-shadow-size -@absolute-mark-shadow-size 0px,
      @annotation-mark-color -@absolute-mark-shadow-size -@absolute-mark-shadow-size 0px;

    &.is-annotation-target {

      .transition(transform @duration/2 ease, border @duration/2 ease, box-shadow @duration/2 ease;);
      border-width: 0;
      transform: scale(1.2);
      box-shadow: 
        inset @annotation-mark-color @absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
        inset @annotation-mark-color -@absolute-mark-shadow-size -@absolute-mark-shadow-size 0px,
        @annotation-mark-color @absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
        @annotation-mark-color -@absolute-mark-shadow-size @absolute-mark-shadow-size 0px,
        @annotation-mark-color @absolute-mark-shadow-size -@absolute-mark-shadow-size 0px,
        @annotation-mark-color -@absolute-mark-shadow-size -@absolute-mark-shadow-size 0px;

      &:not(.is-active) {
        box-shadow: 
          inset @annotation-mark-color 0 0 0px,
          inset @annotation-mark-color 0 0 0px,
          @annotation-mark-color 0 0 0px,
          @annotation-mark-color 0 0 0px,
          @annotation-mark-color 0 0 0px,
          @annotation-mark-color 0 0 0px;
      }

      &.has-shadow::before {
        .transition(opacity @duration/2 ease);
        opacity: 0;
      }

      &.is-active {
        border-width: @mark-border-width * 3;
        transform: scale(1);

        &.has-shadow::before {
          opacity: 1;
        }
      }
    }
  }

  &.is-absolute.has-content {
    
    padding: @item-padding;
    transform: scale(1.2);
    .drop-shadow-half();
  
    &.is-annotation-target {
      .transition(transform @duration/2 ease, border @duration/2 ease, color @duration/2 ease;);
    }

    &:not(.is-active) {
      color: transparent;
    }

    &.is-active {
      transform: scale(1);
    }
  }


  // Unique color in blockquotes and .paper elements (may not be a permanent thing?).
  blockquote &,
  .paper & {
    background: @blockquote-mark-color;
		border-color: @blockquote-mark-border-color;
    
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;

    margin-left: -@mark-padding;
    margin-right: -@mark-padding;

    &.is-correct {
      .transition(background-color @duration ease, border-color @duration ease;);

      background: hsl(hue(@validation-success), saturation(@blockquote-mark-color), lightness(@blockquote-mark-color));
      border-color: hsl(hue(@validation-success), saturation(@blockquote-mark-border-color), lightness(@blockquote-mark-border-color));
    }
  }
}

mark,
.mark {
  .mark();
}

// Opposite of mark. Fades the element (makes it black and white and semi-transparent).
.is-faded,
.is-fade {
  filter: grayscale(1) contrast(0.7);
  opacity: 0.7;
}

// SVG Mark
// ---------------------------------------------------
// Custom mark shapes that resemble (and behave) the same way as normal marks.
.svgmark {

  &.is-absolute {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    filter: 
      drop-shadow(@absolute-mark-shadow-size 0 0 @annotation-mark-color) 
      drop-shadow(-@absolute-mark-shadow-size 0 0 @annotation-mark-color) 
      drop-shadow(0 @absolute-mark-shadow-size 0 @annotation-mark-color) 
      drop-shadow(0 -@absolute-mark-shadow-size 0 @annotation-mark-color);

    &.is-annotation-target {

      .transition(transform @duration/2 ease, opacity @duration/2 ease;);
      opacity: 0;
      transform: scale(1.2);

      &:not(.is-active) {
        opacity: 0;
      }

      &.is-active {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
}