// HotgraphicFlip
// ------------------------------------------

//@hotgraphicflip-card-color: mix(@btn-color, @font-color-inverted, 25%);

.hotgraphicmini {
  
  // Items container
  // -------------------------------------
  &__item-container {
    //display: grid;
    //flex-wrap: wrap;
    //justify-content: space-around;
    //align-items: center;
    //margin: 0px -@item-margin/2;

    //justify-items: center;
    //align-items: center;
  }

  // Item
  // -------------------------------------
  &__item {
    display: block;
    position: relative;
    width: 100%;
    //flex: 1 1 auto;
    //width: 33.333%;
    //padding: @item-margin/2;

    padding: @item-padding;
    
    cursor: pointer;
    //padding: @item-margin; // Spacing.
    
    //max-width: 100% !important;
  }

  &__item-inner {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
  }

  // Content ("tooltip")
  // -------------------------------------

  // Start off hidden.
  &__item-content {
    opacity: 0;
    pointer-events: none;
    cursor: auto;

    .transition(margin-bottom @duration*2 ease, opacity @duration*2 ease;);
  }

  // Once the item is active, slide the content element up.
  &__item.is-active &__item-content {
    margin-bottom: 0 !important; // Override the JS style.
    opacity: 1;

    pointer-events: all;
  }
  &__item-content-inner {
    background-color: @item-color;
    padding: @item-padding;
    margin-bottom: @item-padding;
    margin-top: @item-margin; // FIXME: TEMP?
  }

  &__item-title {
    .item-title();
    &:not(:last-child) {
      margin-bottom: @item-title-margin;
    }
  }

  // Button + graphic
  // -------------------------------------
  &__item-btn-container {
    //height: 100%;

    display: flex;
    align-items: center;
    flex-grow: 0;
    flex-shrink: 0;
  }

  &__item-btn {
    width: 100%;
  }


  // Active state change
  // -------------------------------------
  // FIXME: quick implementation.
  &__item.has-active-state &__item-content {
    transition-delay: @duration*4; // Show the state change before the animation.
  }
  &__item.has-active-state &__image,
  &__item.has-active-state &__image-active {
    .transition(opacity ease @duration);
  }

  &__image-active {
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item.is-active.has-active-state &__item-btn,
  &__item:not(.is-active).is-visited.has-active-state &__item-btn {
    .effect-bounceOnce();
    animation-duration: @duration*2;
  }
  &__item.is-active.has-active-state &__image {
    opacity: 0;
    pointer-events: none;
  }
  &__item.is-active.has-active-state &__image-active {
    opacity: 1;
    pointer-events: all;
  }
}