// Narrative--pdf
//--------------------
// Stack the reveals one above the other.

.mode-pdf .narrativeplus {
  
  // Items
  // ------------------------------------------
  &__item {
    //.page-break-avoid;
  }

  &__item-content {
    display: block; // Show the content without needing to be clicked.
    page-break-before: avoid; // Avoid splitting up the item button and content between pagebreaks.
  }

  // Hide the "+" icon.
  &__item-icon {
    display: none;
  }

  // Change button colours.
  &__item-btn {
    background-color: @item-color-selected;
    color: @item-color-inverted-selected;
  }
}